import { MobileStepper } from '@mui/material';
import { Box, useTheme } from '@mui/system';
import GradientButton from 'components/Buttons/GradientButton';
import ScrimItem from 'components/Lists/ScrimItem';
import ScrollView from 'components/ScrollView';
import { Stack } from 'components/styled/layout.styled';
import { NormalText } from 'components/styled/text.styled';
import { IconSource } from 'modules/assetpath';
import { dateFormat } from 'modules/helpers';
import history from 'modules/history';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import { useAppDispatch, useAppSelector } from 'redux/hooks';
import { resetScrimDraftAction } from 'redux/scrim/ScrimDraftReducer';
import { fetchScrimList } from 'redux/scrim/ScrimService';
import { useAppThunkDispatch } from 'redux/store';
import { getMyCurrentTeamFromSession } from 'redux/team/TeamService';

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);
const images = [
  {
    label: 'MABAR Scrim Program',
    imgPath: `${process.env.PUBLIC_URL}/media/images/banner/Banner MABAR SCRIM-02.jpg`,
    link: `${process.env.PUBLIC_URL}/media/images/banner/MABAR SCRIM Banner_1 (1)_compressed.pdf`,
  },
  {
    label: 'MABAR Scrim Guide',
    imgPath: `${process.env.PUBLIC_URL}/media/images/banner/Banner MABAR Scrim Guide-14.jpg`,
    link: `${process.env.PUBLIC_URL}/media/images/banner/MABAR-Scrim-guide.pdf`,
  },
];

export default function ScrimListPage() {
  const { t } = useTranslation();
  const dispatch = useAppDispatch();
  const thunkDispatch = useAppThunkDispatch();

  const myCurrentTeam = getMyCurrentTeamFromSession();
  const { scrimList, isScrimListLoading, gameSelected } = useAppSelector(
    ({ scrimList, scrimDraft }) => ({
      scrimList: scrimList.data,
      isScrimListLoading: scrimList.loading,
      gameSelected: scrimDraft.gameSelected,
    }),
  );

  const isHaveTeam = myCurrentTeam && myCurrentTeam.length >= 1;
  const isCaptain =
    isHaveTeam && myCurrentTeam.find(v => v.gameId === gameSelected.id)?.level.code == 1;

  const handleFetchScrimList = () => {
    thunkDispatch(fetchScrimList({ gameSlug: gameSelected.urlSlug, pageIndex: 1, pageSize: 20 }));
  };

  useEffect(() => {
    handleFetchScrimList();
  }, []);

  const goCreateScrim = () => {
    dispatch(resetScrimDraftAction());
    history.push('/create-scrim');
  };

  const theme = useTheme();
  const [activeStep, setActiveStep] = useState(0);
  const maxSteps = images.length;

  const handleStepChange = (step: React.SetStateAction<number>) => {
    setActiveStep(step);
  };

  return (
    <>
      <Box sx={{ pl: '16px', pr: '16px', pt: '16px', pb: '14', borderRadius: '8px' }}>
        <AutoPlaySwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={activeStep}
          onChangeIndex={handleStepChange}
          enableMouseEvents
        >
          {images.map((step, index) => (
            <>
              {Math.abs(activeStep - index) <= 2 ? (
                <a href={step.link} target="blank">
                  <Box
                    key={step.label}
                    component="img"
                    sx={{
                      //  height: '152px',
                      display: 'block',
                      // maxWidth: 400,
                      overflow: 'hidden',
                      width: '100%',
                      borderRadius: '8px',
                    }}
                    src={step.imgPath}
                    alt={step.label}
                  />
                </a>
              ) : null}
            </>
          ))}
        </AutoPlaySwipeableViews>
        <MobileStepper
          steps={maxSteps}
          position="static"
          activeStep={activeStep}
          sx={{
            '.MuiMobileStepper-dots': {
              '& .MuiMobileStepper-dot': {
                bgcolor: '#C4C4C4',
                width: '4px',
                height: '4px',
                transform: 'scale(1)',
                boxShadow: '0px 4px 4px rgba(0, 0, 0, 0.16)',
              },
              '& .MuiMobileStepper-dotActive': {
                bgcolor: '#FFFFFF',
                transform: 'scale(1.3)',
                boxShadow: '0px 4px 4px rgba(0, 0, 0, 0.16)',
              },
            },

            position: 'relative',
            background: 'linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%)',
            height: '30px',
            // backgroundColor: 'transparent',
            justifyContent: 'center',
            width: '100%',
            marginTop: '-30px',
            padding: 0,
          }}
          nextButton={null}
          backButton={null}
          variant="dots"
        />
      </Box>
      {scrimList.count >= 1 ? (
        <>
          <ScrollView id="ScrimList" margin="0px 16px 80px 16px" direction="vertical">
            {scrimList.items &&
              scrimList.items.map(item => {
                return (
                  <ScrimItem
                    key={item.id.replace('#', '')}
                    id={item.id}
                    data={item}
                    startAt={dateFormat(item.startAt, 'Asia/Jakarta', 'id').format(
                      'dddd, DD MMM YYYY - HH:mm z',
                    )}
                    teamChallengerLogo={item.avatar}
                    teamChallengerName={item.firstTeam}
                    schemaMatch={
                      item.type.value.replace('BO3', 'Best of 3').replace('BO5', 'Best of 5') ??
                      'Unknown'
                    }
                    onScrimClick={d => {
                      history.push(`/scrim/${gameSelected.urlSlug}/${d.id}`);
                    }}
                  />
                );
              })}
          </ScrollView>
          {isCaptain && (
            <div
              style={{
                position: 'fixed',
                zIndex: 199,
                flexDirection: 'row',
                alignSelf: 'center',
                justifyContent: 'center',
                marginBottom: '75px',
                bottom: 0,
                left: '50%',
                transform: 'translateX(-50%)',
                maxWidth: '500px',
                width: 'max-content',
              }}
            >
              <GradientButton
                variant="primary"
                onClick={goCreateScrim}
                padding="12px 20px 12px 20px"
                styled={{
                  backgroundSize: '100%',
                }}
              >
                {t('player_explore_scrim_create_scrim')}
              </GradientButton>
            </div>
          )}
        </>
      ) : (
        !isScrimListLoading && (
          <Stack
            direction="column"
            align="center"
            margin="130px 0px 0px 0px"
            disableSelectText
            disableDrag
          >
            <img alt="" src={IconSource('mabar-typeface-fullwhite')} />
            <NormalText
              fontSize="14px"
              fontWeight="400"
              color="#969393"
              lineHeight="20.44px"
              textAlign="center"
              margin="24px 0px 30px 0px"
            >
              {isHaveTeam
                ? t('player_explore_scrim_empty_state')
                : t('player_explore_scrim_empty_state_team')}
              <br />
            </NormalText>
            {isCaptain && (
              <GradientButton
                variant="primary"
                onClick={goCreateScrim}
                padding="12px 20px 12px 20px"
                styled={{
                  backgroundSize: '100%',
                }}
              >
                {t('player_explore_scrim_create_scrim')}
              </GradientButton>
            )}
          </Stack>
        )
      )}
    </>
  );
}
