import React from 'react'
import { NormalText } from "components/styled/text.styled"
import ConfirmModal from "components/UniPopup/Modals/ConfirmModal"
import InputField from 'components/Inputs/Input'
import { Control, Controller, FieldValues } from 'react-hook-form'
import { useTranslation } from 'react-i18next'

const InputEmailModal = (p: {
    onConfirmClick: () => void, onCancelClick: () => void, onChangeEmail?: React.ChangeEventHandler<HTMLInputElement>,
    defaultEmail: string, formControl: Control<FieldValues, object>, isLoading?: boolean, isError?: boolean, errorMessage?: string
}) => {
    const {t} = useTranslation();
    return (
        <Controller
            name='newEmail'
            control={p.formControl}
            defaultValue={p.defaultEmail}
            render={({ field: { onChange, ref }, fieldState: { invalid, error } }) => (
                <ConfirmModal
                    title={t('player_account_verification_flow_email_title')}
                    btnConfirmText={
                        <NormalText
                            color="white"
                            fontWeight="700"
                            fontSize="14px"
                            textAlign="center"
                            padding="0px 12px 0px 12px"
                        >
                            {t('player_account_verification_flow_get_code')}
                        </NormalText>
                    }
                    onConfirmClick={invalid ? ()=> null : p.isError ? p.isLoading ? ()=> null : ()=> null : p.onConfirmClick}
                    btnCancelText={t('player_account_verification_flow_cancel')}
                    onCancelClick={p.onCancelClick}
                    btnConfirm={{
                        type: invalid ? 'reset' : p.isError ? p.isLoading ? 'button' : 'reset' : 'submit',
                        variant: invalid ? 'disabled' : p.isError ? p.isLoading ? 'primary' : 'disabled' : 'primary'
                    }}>
                    <InputField
                        ref={ref}     
                        isRequired
                        type='email'
                        label={t('player_account_verification_flow_email')}
                        inputMode="email"
                        placeholder="hi@mabar.com"
                        defaultValue={p.defaultEmail}
                        validationType={invalid ? 'ERROR' : p.isError ? 'ERROR' : undefined}
                        validationText={error?.message ? error?.message : p.errorMessage ?? undefined}
                        onChange={(e) => {
                            onChange(e.target.value)
                            if (p.onChangeEmail) {
                                p.onChangeEmail(e)
                            }
                        }} 
                    />
                </ConfirmModal>
            )}
        />
    )
}

export default InputEmailModal;