import { CardThumbnailWrapper, ImgThumbnail, CardThumbnailRedOverlay } from './styles';
import { Box, Stack, CardHeader, Avatar } from '@mui/material';
import { PrimaryButton } from 'design/Buttons/PrimaryButton';
import { Text } from 'design/Text';
import { IconSource } from 'modules/assetpath';
import React from 'react';
import { useTranslation } from 'react-i18next';
import SVG from 'react-inlinesvg';

export interface GameCardProps {
  gameLogo?: string;
  gameBg: string;
  gameName: string;
  teamLogo?: string;
  teamName?: string;
  isWaiting?: boolean;
  onClick?: (data?: any) => void;
}

export const GameCard = ({
  gameLogo,
  gameBg,
  gameName,
  teamLogo,
  teamName,
  isWaiting = false,
  onClick,
}: GameCardProps) => {
  const { t } = useTranslation();
  return (
    <Box
      sx={theme => ({
        width: '100%',
        height: '166px',
        borderRadius: theme.borderRadius.md,
        border: '1px solid #2E3033',
        mb: '16px',
        position: 'relative',
      })}
      onClick={onClick}
    >
      {isWaiting && (
        <Stack
          direction="row"
          alignItems="center"
          sx={{
            height: '16px',
            position: 'absolute',
            left: '12px',
            top: '12px',
          }}
        >
          <SVG src={IconSource('ic-ribbon-timer')} width="10.5px" height="10.5px" />
          <Text variant="button" sx={{ color: 'primary500', lineHeight: '16px', ml: '4.5px' }}>
            {t('tim_saya_waiting_confirmation')}
          </Text>
        </Stack>
      )}
      <CardThumbnailWrapper>
        <ImgThumbnail src={gameBg} bw={teamLogo || teamName ? false : true} />
        {(teamLogo || teamName) && <CardThumbnailRedOverlay />}
      </CardThumbnailWrapper>
      <CardHeader
        avatar={
          <Avatar
            variant="rounded"
            sx={theme => ({
              width: '86px',
              height: '86px',
              border: '2px solid #F1EBEB',
              borderRadius: '8px',
              background: theme.palette.neutral700,
              img: {
                objectFit: 'contain',
              },
            })}
            aria-label="logo-team"
            src={teamLogo ? teamLogo : gameLogo}
          />
        }
        title={
          teamName ? (
            <>
              <Text variant="label" sx={{ color: 'neutral300' }}>
                {gameName}
              </Text>
              <Text variant="h3" sx={{ color: 'neutral200', mt: '4px' }}>
                {teamName}
              </Text>
            </>
          ) : (
            <>
              <Stack direction="row" justifyContent="flex-end" alignItems="center" spacing={2}>
                <Text variant="label" sx={{ color: 'neutral300', flex: 1 }}>
                  {gameName}
                </Text>
                <PrimaryButton variant="small">{t('tim_saya_cari_team')}</PrimaryButton>
              </Stack>
            </>
          )
        }
        sx={{
          padding: '64px 12px 12px 12px',
        }}
      />
    </Box>
  );
};
