import { Box, useTheme } from '@mui/material';
import history from 'modules/history';
import React, { useState } from 'react';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import styled from 'styled-components';
import { Image, Button } from 'styled-minimal';
import { TextTitle } from 'styles/text.style';

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);
const images = [
  {
    label: 'MABAR HIGH SCHOOL TOURNAMENT - SEASON 1 2022',
    imgPath:
    `${process.env.PUBLIC_URL}/media/images/banner/mabar-dbl-tournament-season-1.jpg`,
    link: '/event/mabar-beta',
  },
  {
    label: 'MABAR SCRIM',
    imgPath:
    `${process.env.PUBLIC_URL}/media/images/banner/Banner-Home-MABAR-SCRIM.jpg`,
      link: '/explore/scrim/mobile-legend',
  },
];

export const FrameBgCss = styled.div`
  position: absolute;
  height: 84px;
  bottom: 0px;
  width: 100%;
  z-index: 3;
  background: linear-gradient(
    0deg,
    #1d1d20 7.56%,
    rgba(29, 29, 32, 0.941293) 20%,
    rgba(29, 29, 32, 0.888237) 35%,
    rgba(29, 29, 32, 0.71522) 50%,
    rgba(29, 29, 32, 0.6) 65%,
    rgba(29, 29, 32, 0.5) 85%
  );
`;

export const Column = styled.div`
  float: left;
  width: 50%;
`;

export const Row = styled.div`
  &::after {
    content: '';
    display: table;
    clear: both;
  }
`;

export const Banner = styled.div`
  position: relative;
  z-index: 1;
`;

export const BannerImage = styled.img`
  width: 100%;
  z-index: 2;
`;

function SwipeableTextMobileStepper() {
  const theme = useTheme();
  const [activeStep, setActiveStep] = useState(0);
  const maxSteps = images.length;

  const handleNext = () => {
    setActiveStep(prevActiveStep => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  };

  const handleStepChange = (step: React.SetStateAction<number>) => {
    setActiveStep(step);
  };

  return (
    <Banner>
      <Box>
        <AutoPlaySwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={activeStep}
          onChangeIndex={handleStepChange}
          enableMouseEvents
        >
          {images.map((step, index) => (
            <>
              {Math.abs(activeStep - index) <= 2 ? (
                <Box
                  key={step.label}
                  component="img"
                  sx={{
                    display: 'block',
                    overflow: 'hidden',
                    width: '100%',
                  }}
                  src={step.imgPath}
                  alt={step.label}
                  onClick={() => history.push(step.link)}
                />
              ) : null}
            </>
          ))}
        </AutoPlaySwipeableViews>
        <FrameBgCss>
          <Row>
            <Column style={{ width: '85%' }}>
              <TextTitle
                size="16px"
                style={{
                  color: 'white',
                  fontWeight: 'bold',
                  paddingLeft: '10px',
                  marginTop: '25px',
                  marginBottom: '0px',
                }}
              >
                {images[activeStep].label}
              </TextTitle>
            </Column>
            <Column style={{ width: '15%' }}>
              <Button
                sizes="small"
                onClick={handleBack}
                disabled={activeStep === 0}
                size="md"
                style={{ background: '#39383b', border: '1px solid #4e4e4e' }}
              >
                <Image
                  alt="prev"
                  src={`${process.env.PUBLIC_URL}/media/icons/ic-caret-backward.svg`}
                />
              </Button>
              <Button
                sizes="small"
                onClick={handleNext}
                disabled={activeStep === maxSteps - 1}
                size="md"
                style={{ background: '#39383b', border: '1px solid #4e4e4e' }}
              >
                <Image
                  alt="next"
                  src={`${process.env.PUBLIC_URL}/media/icons/ic-caret-forward.svg`}
                />
              </Button>
            </Column>
          </Row>
        </FrameBgCss>
      </Box>
    </Banner>
  );
}

export default SwipeableTextMobileStepper;
