import { Box, Stack } from '@mui/material';
import { IconButton } from 'design/Buttons/IconButton';
import { PrimaryButton } from 'design/Buttons/PrimaryButton';
import { SecondaryButton } from 'design/Buttons/SecondaryButton';
import { CardPlayerDetail } from 'design/Cards/CardPlayerDetail';
import { InfoModal } from 'design/Cards/InfoModal';
import { Player as PlayerCard, PlayerProps } from 'design/Cards/Player';
import { DrawerPrimary } from 'design/Drawer/Primary';
import { TextInput } from 'design/Forms/TextInput';
import { TextInputSearch } from 'design/Forms/TextInputSearch';
import { Modal } from 'design/Modal';
import { Text } from 'design/Text';
import he from 'he';
import { ImageSource, IconSource } from 'modules/assetpath';
import React, { useState, useEffect, ReactNode } from 'react';
import { useTranslation } from 'react-i18next';
import SVG from 'react-inlinesvg';
import { fetchPlayerProfile } from 'redux/account/AccountServices';
import { fetchGameDetail } from 'redux/game/GameReducer';
import { useAppSelector } from 'redux/hooks';
import { useAppThunkDispatch } from 'redux/store';
import { approveJoin, rejectJoin, kickMember } from 'redux/team/TeamApproval';
import { Player } from 'redux/team/TeamMemberReducer';
import { editDelegateCaptain } from 'redux/team/TeamMemberReducer';
import { fetchMyTeamList, fetchPlayerSchool } from 'redux/team/TeamService';
import { fetchTeamMember, inviteMember, fetchTeamDetail } from 'redux/teamProfile/slice';
import AuthService from 'services/AuthService';
import { PHONE_NUMBER_PATTERN } from 'validation/account/EditProfileValidation';

interface MemberPageProps {
  gameSlug: string;
  teamSlug: string;
  setPageTitle?: any;
  setOpenToast: (value: boolean) => void;
  setToastMessages: (value: string) => void;
  setOpenErrorToast: (value: boolean) => void;
  setToastErrorMessages: (value: string) => void;
}

interface PlayerData {
  playerName?: string;
  playerPhoto?: string;
  gameId?: string;
  nickname?: string;
  urlSlug?: string;
  status?: string;
  type?: string;
  teamId?: string;
  playerId?: string;
  level?: string;
}

const BoxWrapper = ({ title, children }: { title?: string; children?: ReactNode }) => (
  <Box
    sx={theme => ({
      background: theme.palette.neutral700,
      border: '1px solid #242628',
      borderRadius: '8px',
    })}
  >
    <Text variant="h3" sx={{ p: '12px' }}>
      {title}
    </Text>
    {children}
  </Box>
);

const MemberPage = ({
  gameSlug,
  teamSlug,
  setPageTitle,
  setOpenToast,
  setToastMessages,
  setOpenErrorToast,
  setToastErrorMessages,
}: MemberPageProps) => {
  const dispatch = useAppThunkDispatch();
  const { t } = useTranslation();
  const auth = new AuthService();
  const isAuthenticated = auth.isAuthenticated();

  const [isOpenPlayerDetail, setIsOpenPlayerDetail] = useState(false);
  const [isOpenAddMember, setIsOpenAddMember] = useState(false);
  const [playerData, setPlayerData] = useState<PlayerData>();

  const {
    accountInfo,
    teamMember,
    teamMemberStatus,
    gameDetail,
    playerList,
    playerListStatus,
    teamDetail,
  } = useAppSelector(({ account, teamProfile, game, teamMember }) => ({
    accountInfo: account?.data,
    teamMember: teamProfile?.teamMember,
    teamMemberStatus: teamProfile?.status?.teamMember,
    gameDetail: game?.game,
    playerList: teamMember?.playerSchool,
    playerListStatus: teamMember?.status?.playerSchool,
    teamDetail: teamProfile?.teamDetail,
  }));

  const [joinedPlayer, setJoinedPlayer] = useState<PlayerProps[]>([]);
  const [selectedPlayers, setSelectedPlayers] = useState<Player[]>([]);
  const [refetchTeamProfile, setRefetchTeamProfile] = useState(false);
  const [openLoader, setOpenLoader] = useState(false);
  const [openModalConfirmation, setOpenModalConfirmation] = useState(false);
  const [getModalAction, setModalAction] = useState<
    | 'confirm-action'
    | 'reject-request-join'
    | 'kick-member'
    | 'failed-kick-player'
    | 'failed-delegate-captain'
    | 'delegate-captain'
  >('confirm-action');
  const [activeButton, setActiveButton] = useState(false);
  const [resetInputSearch, setResetInputSearch] = useState(false);
  const [inputTeamName, setInputTeamName] = useState('');
  const [inputTeamNameError, setInputTeamNameError] = useState('');

  const myRole = isAuthenticated
    ? accountInfo?.games?.find(game => game?.team?.urlSlug === teamSlug)
    : undefined;
  const isCaptain = myRole?.team?.level?.value === 'Captain';

  const linkWA = teamDetail?.whatsAppGroupUrl;
  const linkDiscord = teamDetail?.discordUrl;

  const handleFetchTeamDetail = (teamSlug: string) => {
    dispatch(fetchTeamDetail({ teamSlug: teamSlug, version: '' }));
  };

  const handleFetchTeamMember = (teamSlug: string) => {
    dispatch(
      fetchTeamMember({
        approved: false,
        teamSlug: teamSlug,
        pageIndex: 1,
        pageSize: 20,
        version: '',
      }),
    );
  };

  const handleFetchPlayerList = (
    schoolId: string,
    gameId: string,
    pI: number,
    pS: number,
    search?: string,
  ) => {
    dispatch(
      fetchPlayerSchool({
        schoolId: schoolId,
        gameId: gameId,
        pageIndex: pI,
        pageSize: pS,
        search: search,
      }),
    );
  };

  const handleFetchGameDetail = (gameSlug: string) => {
    dispatch(fetchGameDetail({ gameSlug: gameSlug }));
  };

  const handleFetchPlayerProfile = (username: string) => {
    dispatch(
      fetchPlayerProfile({
        username: username,
      }),
    );
  };

  const handleFetchMyTeamList = (username: string) => {
    dispatch(
      fetchMyTeamList({
        playerSlug: username,
        pageIndex: 1,
        pageSize: 10,
      }),
    );
  };

  const acceptMember = (props: { playerId: string; teamId: string }) => {
    setOpenLoader(true);
    const thunk = approveJoin(props);
    dispatch(thunk)
      .unwrap()
      .then(res => {
        setOpenLoader(false);
        setIsOpenPlayerDetail(false);
        if (res && res?.isAxiosError && res?.data?.status != 204) {
          setToastErrorMessages(res?.data?.detail);
          setOpenErrorToast(true);
        } else {
          setToastMessages('Player bergabung dengan tim');
          setTimeout(() => {
            setOpenLoader(false);
            setOpenToast(true);
          }, 4000);
        }
        setRefetchTeamProfile(true);
      });
  };

  const rejectMember = (props: { playerId: string; teamId: string }) => {
    setOpenLoader(true);
    const thunk = rejectJoin(props);
    if (accountInfo && myRole) {
      dispatch(thunk)
        .unwrap()
        .then(res => {
          setOpenModalConfirmation(false);
          if (res && res?.isAxiosError && res?.data?.status != 204) {
            setOpenLoader(false);
            setToastErrorMessages(res?.data?.detail);
            setOpenErrorToast(true);
          }
          setRefetchTeamProfile(true);
          handleFetchPlayerList(accountInfo?.school?.id!, myRole.game.id, 1, 200);
          setTimeout(() => {
            setOpenLoader(false);
          }, 4000);
        });
    }
  };

  const removeMember = (props: { playerId: string; teamId: string }) => {
    setOpenLoader(true);
    setOpenModalConfirmation(false);
    const thunk = kickMember(props);
    dispatch(thunk)
      .unwrap()
      .then(res => {
        setIsOpenPlayerDetail(false);
        if (res && res?.isAxiosError && res?.data?.status != 204) {
          setOpenLoader(false);
          if (res?.data?.detail != undefined) {
            if (res?.data?.status == 1300) {
              setModalAction('failed-kick-player');
              setOpenModalConfirmation(true);
            }
          }
        } else {
          if (playerData?.status === 'Waiting' && playerData?.type == 'Invite') {
            setToastMessages('Undangan dibatalkan');
          } else {
            setToastMessages('Anggota telah dikeluarkan');
          }
          setTimeout(() => {
            handleFetchTeamDetail(teamSlug);
            setOpenLoader(false);
            setOpenToast(true);
          }, 4000);
        }
        setRefetchTeamProfile(true);
      })
      .catch(err => {
        if (process.env.NODE_ENV !== 'production') console.error(err);
      });
  };

  const handleClickSave = () => {
    if (myRole) {
      setOpenLoader(true);
      dispatch(inviteMember({ teamId: myRole.team.id, members: selectedPlayers.map(m => m.id) }))
        .unwrap()
        .then(res => {
          setOpenLoader(false);
          setIsOpenAddMember(false);
          if (res && res?.isAxiosError && res?.data?.errors) {
            setToastErrorMessages(msgLoopError(res?.data?.errors));
            setOpenErrorToast(true);
          } else {
            setToastMessages('Undangan bergabung terkirim');
            setOpenToast(true);
            if (myRole.game)
              handleFetchPlayerList(accountInfo?.school?.id!, myRole.game.id, 1, 200);

            // clear checkbox and inputbox search
            setSelectedPlayers([]);
            setResetInputSearch(true);
          }
          setRefetchTeamProfile(true);
        });
    }
  };

  const handleEditDelegateCaptain = () => {
    setInputTeamNameError('');

    if (inputTeamName.toLowerCase() == teamDetail?.name.toLowerCase()) {
      const thunk = editDelegateCaptain({
        teamId: teamDetail?.id,
        playerId: playerData?.playerId,
      });
      setOpenLoader(true);

      dispatch(thunk)
        .unwrap()
        .then(res => {
          setOpenLoader(false);
          setOpenModalConfirmation(false);

          if (res && res?.isAxiosError && res?.data?.status != 204) {
            if (res?.data?.detail != undefined) {
              if (res?.data?.status == 1300) {
                setModalAction('failed-delegate-captain');
                setOpenModalConfirmation(true);
              } else {
                setToastErrorMessages(res?.data?.errors);
                setOpenErrorToast(true);
              }
            }
          } else {
            setRefetchTeamProfile(true);
            setToastMessages('Kapten baru berhasil terpilih');
            setOpenToast(true);
          }
        });
    } else {
      setInputTeamNameError('Nama tim yang dimasukkan salah');
    }
  };

  const msgCountLengthPlayer = (maxMember: any) => {
    let maxPlayer = maxMember ?? 0;
    return t('create_add_player_error_full_team', { maxPlayer: maxPlayer });
  };

  const msgLoopError = (errors: any) => {
    if (errors) {
      let errorsList: string[] = [];
      Object.values(errors).forEach((prop: any) => {
        if (prop[0].code == 'RX1302') {
          errorsList = errorsList.concat(
            msgCountLengthPlayer(prop[0].formatedValue['memberCount']),
          );
        }
        // else {
        //   errorsList = errorsList.concat(_.map(prop, _.property('message')));
        // }
      });
      return errorsList.join('<br>');
    }
    return '';
  };

  const maxChoosePlayer = gameDetail?.maxTeamMember ?? 0;

  const handleAddPlayerClick = (player: Player) => {
    const isChecked = !selectedPlayers.find(e => {
      return e.id === player.id;
    });
    let newData: Player[] = [];
    if (selectedPlayers?.length < maxChoosePlayer) {
      if (isChecked) {
        newData = [...selectedPlayers];
        if (
          !newData.find(e => {
            return e.id === player.id;
          })
        ) {
          newData.push(player);
        } else {
          newData = selectedPlayers.filter(el => el.id !== player.id);
        }
      } else {
        newData = selectedPlayers.filter(el => el.id !== player.id);
      }
      setSelectedPlayers(newData);
    } else {
      if (!isChecked) {
        newData = [...selectedPlayers];
        if (
          newData.find(e => {
            return e.id === player.id;
          })
        ) {
          newData = selectedPlayers.filter(el => el.id !== player.id);
          setSelectedPlayers(newData);
        }
      }
    }
  };

  const totalCheckPlayer = () => {
    let totalPlayerSelected = selectedPlayers?.length ?? 0;
    let totalPlayerMember = teamMember?.totalCount ?? 0;
    let totalPlayerCheck = totalPlayerMember + totalPlayerSelected;
    return totalPlayerCheck ?? 0;
  };

  const checkPlayerState = (playerSelected: any) => {
    if (totalCheckPlayer() >= maxChoosePlayer) {
      return selectedPlayers?.find(e => {
        return e.id === playerSelected.id;
      })
        ? false
        : true;
    } else {
      return false;
    }
  };

  const handleOpenAddMember = () => {
    setOpenLoader(true);
    setIsOpenAddMember(true);
    if (accountInfo && myRole) {
      handleFetchPlayerList(accountInfo.school.id, myRole.game.id, 1, 200);
    }
  };

  useEffect(() => {
    handleFetchTeamMember(teamSlug);
  }, []);

  useEffect(() => {
    if (playerListStatus === 'succeeded') {
      setOpenLoader(false);
    }
  }, [playerListStatus]);

  useEffect(() => {
    if (refetchTeamProfile && accountInfo) {
      setRefetchTeamProfile(false);
      handleFetchPlayerProfile(accountInfo.userName);
      handleFetchTeamMember(teamSlug);
      handleFetchTeamDetail(teamSlug);
      handleFetchMyTeamList(accountInfo.userName);
    }
  }, [refetchTeamProfile]);

  // joined member
  useEffect(() => {
    if (teamMember && accountInfo) {
      const dataPlayer = (teamMember.items ?? [])
        .slice()
        .sort((a, b) => (a.joinAt > b.joinAt ? 1 : -1))
        .sort((a, _) => (a.level.value === 'Captain' ? -1 : 1))
        .filter(member => member.status.value !== 'Waiting')
        .map((member, index) => {
          return {
            key: index,
            playerId: member?.player.id,
            playerName: member.player.name,
            playerInfo: `${member.player.nickName} | ${member.player.gameProviderId}`,
            playerPhoto: member.player.picture !== null ? member.player.picture : undefined,
            isCaptain: member.level.value === 'Captain',
            isYou: member?.player.id === accountInfo.id,
            sx: { m: '0' },
            cta: <SVG src={IconSource('ic-chevron-right')} />,
            onClick: () => {
              setPlayerData({
                playerName: member?.player.name,
                playerPhoto: member?.player.picture ?? undefined,
                gameId: member?.player.gameProviderId,
                nickname: member?.player.nickName,
                urlSlug: member?.player.urlSlug,
                status: member?.status.value,
                type: member?.type.value,
                teamId: member?.teamId,
                playerId: member?.player.id,
                level: member?.level.value,
              });
              setIsOpenPlayerDetail(true);
            },
          };
        });

      return setJoinedPlayer(dataPlayer);
    }
  }, [teamMember]);

  useEffect(() => {
    if (!gameDetail) {
      handleFetchGameDetail(gameSlug);
    }
  }, [gameDetail]);

  useEffect(() => {
    if (selectedPlayers?.length >= 1) {
      setActiveButton(true);
    } else {
      setActiveButton(false);
    }
  }, [selectedPlayers]);

  useEffect(() => {
    setPageTitle(
      t('mabar_page_title', {
        title: 'My Team > Member',
      }),
    );
  }, [setPageTitle]);

  return (
    <>
      <Stack direction="column" spacing={2}>
        {teamMemberStatus === 'succeeded' && teamMember?.items && (
          <Text variant="h3" sx={{ pt: '10px' }}>
            {t('my_team')}
          </Text>
        )}
        {teamMemberStatus === 'succeeded' && (linkDiscord || linkWA) && (
          <BoxWrapper title="Hubungi Kapten">
            <Stack spacing={2} direction="row" sx={{ p: ' 0 12px 12px' }}>
              <SecondaryButton
                variant="small"
                icon={linkDiscord ? IconSource('discord_purple') : IconSource('discord_gray')}
                sx={theme => ({
                  svg: { width: '18px' },
                  'svg .arrow': { fill: theme.palette.neutral600 },
                })}
                wide
                onClick={() => {
                  linkDiscord && window.open(linkDiscord, '_blank');
                }}
                disabled={!linkDiscord}
              >
                Discord
              </SecondaryButton>
              <SecondaryButton
                variant="small"
                icon={linkWA ? IconSource('wa_active') : IconSource('wa_inactive')}
                sx={theme => ({
                  svg: { width: '18px' },
                  'svg .arrow': { fill: theme.palette.neutral600 },
                })}
                wide
                onClick={() => {
                  linkWA &&
                    window.open(
                      linkWA.match(PHONE_NUMBER_PATTERN)
                        ? `https://wa.me/${linkWA?.split('+')[1] ? linkWA?.split('+')[1] : linkWA}`
                        : linkWA,
                      '_blank',
                    );
                }}
                disabled={!linkWA}
              >
                Whatsapp
              </SecondaryButton>
            </Stack>
          </BoxWrapper>
        )}
        {teamMemberStatus === 'succeeded' &&
          teamMember?.items &&
          teamMember?.items?.filter(member => member.status.value === 'Waiting').length > 0 && (
            <BoxWrapper title="Aktivitas">
              <Stack direction="column">
                {teamMember?.items
                  .slice()
                  .sort((a, b) => (a.joinAt > b.joinAt ? 1 : -1))
                  .filter(member => member.status.value === 'Waiting')
                  .map((player, index) => {
                    return (
                      <PlayerCard
                        key={index}
                        playerId={player?.player.id}
                        playerName={player.player.name}
                        playerInfo={`${player.player.nickName} | ${player.player.gameProviderId}`}
                        playerPhoto={
                          player.player.picture !== null ? player.player.picture : undefined
                        }
                        isCaptain={player.level.value === 'Captain'}
                        icon={
                          isCaptain &&
                          player.status.value === 'Waiting' &&
                          player.type.value == 'Request'
                            ? undefined
                            : IconSource('ic-waiting')
                        }
                        cta={
                          isCaptain &&
                          player.status.value === 'Waiting' &&
                          player.type.value === 'Request' ? (
                            <Stack direction="row" alignItems="center" spacing={1}>
                              <PrimaryButton
                                wide
                                variant="small"
                                sx={{ p: '4px 8px' }}
                                onClick={() => {
                                  acceptMember({
                                    playerId: player?.player.id ?? '',
                                    teamId: player?.teamId ?? '',
                                  });
                                }}
                              >
                                Terima
                              </PrimaryButton>
                              <SecondaryButton
                                wide
                                variant="small"
                                sx={{ p: '4px 8px' }}
                                onClick={() => {
                                  setPlayerData({
                                    playerName: player?.player.name,
                                    playerPhoto: player?.player.picture ?? undefined,
                                    gameId: player?.player.gameProviderId,
                                    nickname: player?.player.nickName,
                                    urlSlug: player?.player.urlSlug,
                                    status: player?.status.value,
                                    type: player?.type.value,
                                    teamId: player?.teamId,
                                    playerId: player?.player.id,
                                    level: player?.level.value,
                                  });

                                  setModalAction('reject-request-join');
                                  setOpenModalConfirmation(true);
                                }}
                              >
                                Tolak
                              </SecondaryButton>
                            </Stack>
                          ) : (
                            <SVG src={IconSource('ic-chevron-right')} />
                          )
                        }
                        sx={theme => ({ borderTop: `1px solid ${theme.palette.neutral530}`, m: 0 })}
                        {...(isCaptain &&
                        player.status.value === 'Waiting' &&
                        player.type.value === 'Request'
                          ? {
                              onClickContent: () => {
                                setPlayerData({
                                  playerName: player?.player.name,
                                  playerPhoto: player?.player.picture ?? undefined,
                                  gameId: player?.player.gameProviderId,
                                  nickname: player?.player.nickName,
                                  urlSlug: player?.player.urlSlug,
                                  status: player?.status.value,
                                  type: player?.type.value,
                                  teamId: player?.teamId,
                                  playerId: player?.player.id,
                                  level: player?.level.value,
                                });

                                setIsOpenPlayerDetail(true);
                              },
                            }
                          : {
                              onClick: () => {
                                setPlayerData({
                                  playerName: player?.player.name,
                                  playerPhoto: player?.player.picture ?? undefined,
                                  gameId: player?.player.gameProviderId,
                                  nickname: player?.player.nickName,
                                  urlSlug: player?.player.urlSlug,
                                  status: player?.status.value,
                                  type: player?.type.value,
                                  teamId: player?.teamId,
                                  playerId: player?.player.id,
                                  level: player?.level.value,
                                });

                                setIsOpenPlayerDetail(true);
                              },
                            })}
                      />
                    );
                  })}
              </Stack>
            </BoxWrapper>
          )}
        {teamMemberStatus === 'succeeded' && joinedPlayer && joinedPlayer.length > 0 && (
          <BoxWrapper title="Anggota Tim">
            {isCaptain && (
              <Stack
                direction="row"
                alignItems="center"
                spacing={1}
                sx={theme => ({ p: '12px', borderTop: `1px solid ${theme.palette.neutral530}` })}
                onClick={handleOpenAddMember}
              >
                <Stack
                  justifyContent="center"
                  alignItems="center"
                  sx={{
                    width: '42px',
                    height: '42px',
                    bgcolor: 'neutral600',
                    borderRadius: '100%',
                    flexShrink: 0,
                  }}
                >
                  <SVG src={IconSource('ic-add')} />
                </Stack>
                <Text variant="label" sx={{ fontWeight: 400 }}>
                  {t('add_new_member')}
                </Text>
              </Stack>
            )}
            <Stack direction="column">
              {joinedPlayer.map(player => {
                return (
                  <PlayerCard
                    key={player.playerId}
                    {...player}
                    sx={theme => ({ borderTop: `1px solid ${theme.palette.neutral530}`, m: 0 })}
                  />
                );
              })}
            </Stack>
          </BoxWrapper>
        )}
      </Stack>

      <DrawerPrimary
        position="bottom"
        open={isOpenPlayerDetail}
        onClose={() => setIsOpenPlayerDetail(false)}
        paperSx={{ overflow: 'visible' }}
      >
        <CardPlayerDetail
          playerName={playerData?.playerName}
          playerPhoto={playerData?.playerPhoto}
          gameId={playerData?.gameId ?? ''}
          nickname={playerData?.nickname ?? ''}
          url={`/player/${playerData?.urlSlug}`}
          cta={
            isCaptain && playerData?.status === 'Waiting' && playerData?.type === 'Request' ? (
              <Stack
                direction="row"
                spacing={1}
                sx={theme => ({
                  borderTop: `1px solid ${theme.palette.neutral530}`,
                  pt: '16px',
                })}
              >
                <PrimaryButton
                  wide
                  variant="small"
                  onClick={() => {
                    acceptMember({
                      playerId: playerData?.playerId ?? '',
                      teamId: playerData?.teamId ?? '',
                    });
                  }}
                >
                  {t('invitation_accept')}
                </PrimaryButton>
                <SecondaryButton
                  wide
                  variant="small"
                  onClick={() => {
                    setIsOpenPlayerDetail(false);
                    setModalAction('reject-request-join');
                    setOpenModalConfirmation(true);
                  }}
                >
                  {t('invitation_reject')}
                </SecondaryButton>
              </Stack>
            ) : isCaptain && playerData?.status === 'Waiting' && playerData?.type == 'Invite' ? (
              <Stack
                direction="row"
                alignItems="center"
                sx={theme => ({
                  borderTop: `1px solid ${theme.palette.neutral530}`,
                  cursor: 'pointer',
                  p: '16px 0 6px',
                })}
                onClick={() =>
                  removeMember({
                    playerId: playerData?.playerId ?? '',
                    teamId: playerData?.teamId ?? '',
                  })
                }
              >
                <SVG src={IconSource('ic-circle-close')} />
                <Text variant="button" sx={{ ml: '8px' }}>
                  Batalkan Undangan Bergabung
                </Text>
              </Stack>
            ) : isCaptain && playerData?.level !== 'Captain' ? (
              <Stack
                direction="column"
                sx={theme => ({ borderTop: `1px solid ${theme.palette.neutral530}` })}
              >
                <Stack
                  direction="row"
                  alignItems="center"
                  sx={{ cursor: 'pointer', p: '16px 0' }}
                  onClick={() => {
                    setIsOpenPlayerDetail(false);
                    setModalAction('delegate-captain');
                    setOpenModalConfirmation(true);
                  }}
                >
                  <SVG src={IconSource('ic-switch-captain')} />
                  <Text variant="button" sx={{ ml: '8px' }}>
                    Pilih Jadi Kapten Baru
                  </Text>
                </Stack>
                <Stack
                  direction="row"
                  alignItems="center"
                  sx={theme => ({
                    cursor: 'pointer',
                    p: '16px 0 6px',
                    borderTop: `1px solid ${theme.palette.neutral530}`,
                  })}
                >
                  <SVG src={IconSource('ic-trash')} />
                  <Text
                    variant="button"
                    sx={{ ml: '8px' }}
                    onClick={() => {
                      setIsOpenPlayerDetail(false);
                      setModalAction('kick-member');
                      setOpenModalConfirmation(true);
                    }}
                  >
                    Keluarkan Anggota
                  </Text>
                </Stack>
              </Stack>
            ) : undefined
          }
        />
      </DrawerPrimary>

      <DrawerPrimary
        position="bottom"
        open={isOpenAddMember}
        onClose={() => {
          setIsOpenAddMember(false);
          setSelectedPlayers([]);
          setResetInputSearch(true);
        }}
        paperSx={{ overflow: 'visible', p: '12px 0', height: '100%', top: 0 }}
        title="Tambah Anggota"
        titleSx={{ p: '0 16px' }}
      >
        <Box
          sx={theme => ({
            p: '0 16px 16px',
            borderBottom: `1px solid ${theme.palette.neutral530}`,
          })}
        >
          <Text sx={{ mb: '16px' }}>{t('select_and_send_invitation')}</Text>
          <TextInputSearch
            bgcolor="neutral600"
            placeholder="Cari player"
            resetValue={resetInputSearch}
            onChange={(value: any) => {
              if (accountInfo && myRole) {
                handleFetchPlayerList(
                  accountInfo.school.id,
                  myRole.game.id,
                  1,
                  200,
                  value ?? undefined,
                );
                setResetInputSearch(false);
              }
            }}
          />
        </Box>
        {playerListStatus === 'succeeded' && playerList && (
          <Stack direction="column">
            {playerList?.items?.map(data => {
              return (
                <PlayerCard
                  playerName={data.name}
                  playerInfo={`${data.nickName} | ${data.gameProviderId}`}
                  playerPhoto={data.picture}
                  isCaptain={false}
                  sx={theme => ({
                    mb: '0px',
                    padding: '12px',
                    borderBottom: `1px solid ${theme.palette.neutral530}`,
                    opacity: checkPlayerState(data) ? '0.5' : '1',
                    cursor: checkPlayerState(data) ? 'default' : 'pointer',
                    pointerEvents: checkPlayerState(data) ? 'none' : 'auto',
                  })}
                  cta={
                    <IconButton
                      sx={{
                        p: '0px',
                        border: 'none',
                      }}
                      onClick={() => {
                        handleAddPlayerClick(data);
                      }}
                      icon={
                        selectedPlayers?.find(e => {
                          return e.id === data.id;
                        }) ?? false
                          ? IconSource('checklist.orange.active')
                          : IconSource('checkbox-empty')
                      }
                    />
                  }
                />
              );
            })}
          </Stack>
        )}
        {playerListStatus === 'succeeded' &&
          playerList &&
          ((playerList.items && playerList.items.length == 0) || !playerList.items) && (
            <Stack direction="row" justifyContent="center" alignItems="center">
              <Text sx={{ mt: '40px', color: '#8D8784' }}>{t('search_not_found')}</Text>
            </Stack>
          )}
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
          sx={theme => ({
            position: 'absolute',
            bottom: '-54px',
            left: 0,
            width: '100%',
            minHeight: '60px',
            bgcolor: 'neutral900',
            borderTop: `1px solid ${theme.palette.neutral530}`,
          })}
        >
          <Box sx={{ pl: '16px' }}>
            <Text>Player terpilih</Text>
            <Text>{`${totalCheckPlayer()}/${gameDetail?.maxTeamMember ?? 0}`}</Text>
          </Box>
          <Box sx={{ pr: '16px' }}>
            <PrimaryButton
              variant="small"
              disabled={!activeButton}
              onClick={() => handleClickSave()}
            >
              Kirim Undangan
            </PrimaryButton>
          </Box>
        </Stack>
      </DrawerPrimary>

      <Modal show={openModalConfirmation}>
        {getModalAction === 'reject-request-join' ? (
          <InfoModal
            cta={
              <Stack direction="row" justifyContent="right" spacing={1} sx={{ width: '100%' }}>
                <SecondaryButton onClick={() => setOpenModalConfirmation(false)}>
                  {t('home_player_invite_player_email_cancel')}
                </SecondaryButton>
                <PrimaryButton
                  sx={theme => ({ background: theme.palette.danger600 })}
                  onClick={() => {
                    rejectMember({
                      playerId: playerData?.playerId ?? '',
                      teamId: playerData?.teamId ?? '',
                    });
                  }}
                >
                  {t('invitation_reject')}
                </PrimaryButton>
              </Stack>
            }
          >
            <Box sx={{ width: '100%', textAlign: 'left' }}>
              <Text variant="h2" sx={{ mb: '8px' }}>
                {t('reject_request')}
              </Text>
              <Text
                sx={theme => ({
                  color: theme.palette.neutral400,
                  b: { color: theme.palette.neutral300 },
                })}
                dangerouslySetInnerHTML={{
                  __html: he.decode(
                    t('reject_request_message', { playerName: playerData?.playerName }),
                  ),
                }}
              ></Text>
            </Box>
          </InfoModal>
        ) : getModalAction === 'failed-kick-player' ? (
          <InfoModal
            icon={ImageSource('Warning', 'png')}
            cta={
              <Stack direction="row" justifyContent="flex-end" sx={{ width: '100%' }}>
                <SecondaryButton onClick={() => setOpenModalConfirmation(false)} wide>
                  {t('confirmation_ok')}
                </SecondaryButton>
              </Stack>
            }
          >
            <Box sx={{ width: '100%' }}>
              <Text variant="h2" mb="8px" textAlign="center">
                {t('request_failed')}
              </Text>
              <Text textAlign="center">{t('kick_member_message_failed')}</Text>
            </Box>
          </InfoModal>
        ) : getModalAction === 'failed-delegate-captain' ? (
          <InfoModal
            cta={
              <Stack direction="row" justifyContent="flex-end" sx={{ width: '100%' }}>
                <PrimaryButton onClick={() => setOpenModalConfirmation(false)}>
                  {t('home_player_invite_success_close')}
                </PrimaryButton>
              </Stack>
            }
          >
            <Box sx={{ width: '100%' }}>
              <Text variant="h2" mb="8px">
                {t('request_failed')}
              </Text>
              <Text>{t('delegate_captain_confirm_message_failed')}</Text>
            </Box>
          </InfoModal>
        ) : getModalAction === 'delegate-captain' ? (
          <InfoModal
            cta={
              <Stack direction="row" justifyContent="flex-end" sx={{ width: '100%' }}>
                <SecondaryButton
                  sx={{ mr: '16px' }}
                  onClick={() => {
                    setInputTeamName('');
                    setInputTeamNameError('');
                    setOpenModalConfirmation(false);
                  }}
                >
                  Batal
                </SecondaryButton>
                <PrimaryButton onClick={() => handleEditDelegateCaptain()}>
                  Konfirmasi
                </PrimaryButton>
              </Stack>
            }
          >
            <Box sx={{ width: '100%', textAlign: 'left' }}>
              <Text variant="h2" sx={{ mb: '8px' }}>
                {t('delegate_captain_confirm')}
              </Text>
              <Text
                sx={theme => ({ color: theme.palette.neutral400 })}
                dangerouslySetInnerHTML={{
                  __html: he.decode(
                    t('delegate_captain_confirm_message', { playerName: playerData?.playerName }),
                  ),
                }}
              ></Text>
              <Stack direction="column" spacing={2} sx={{ mt: '16px' }}>
                <Box>
                  <TextInput
                    id="inputtimname"
                    label="Nama Tim"
                    onChange={e => {
                      setInputTeamName(e.target.value);
                    }}
                  />
                  {inputTeamNameError && (
                    <Text variant="subtitle" sx={{ color: 'danger600', mt: '3px' }}>
                      {inputTeamNameError}
                    </Text>
                  )}
                </Box>
              </Stack>
            </Box>
          </InfoModal>
        ) : (
          getModalAction === 'kick-member' && (
            <InfoModal
              cta={
                <Stack direction="row" justifyContent="right" spacing={1} sx={{ width: '100%' }}>
                  <SecondaryButton onClick={() => setOpenModalConfirmation(false)}>
                    {t('home_player_invite_player_email_cancel')}
                  </SecondaryButton>
                  <PrimaryButton
                    sx={theme => ({ background: theme.palette.danger600 })}
                    onClick={() => {
                      removeMember({
                        playerId: playerData?.playerId ?? '',
                        teamId: playerData?.teamId ?? '',
                      });
                    }}
                  >
                    {t('kick_confirmation')}
                  </PrimaryButton>
                </Stack>
              }
            >
              <Box sx={{ width: '100%', textAlign: 'left' }}>
                <Text variant="h2" sx={{ mb: '8px' }}>
                  {t('kick_member')}
                </Text>
                <Text
                  sx={theme => ({
                    color: theme.palette.neutral400,
                    b: { color: theme.palette.neutral300 },
                  })}
                  dangerouslySetInnerHTML={{
                    __html: he.decode(
                      t('kick_member_message', { playerName: playerData?.playerName }),
                    ),
                  }}
                ></Text>
              </Box>
            </InfoModal>
          )
        )}
      </Modal>

      <Modal
        show={Boolean(teamMemberStatus !== 'succeeded' || openLoader)}
        contentSx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          background: 'transparent',
        }}
      >
        <img src={IconSource('ic-loading')} style={{ position: 'relative', outline: 0 }} />
      </Modal>
    </>
  );
};

export default MemberPage;
