import styled from 'styled-components';

interface Props {
  size?: string;
  padding?: string;
  margin?: string;
}
export const TextTitle = styled.p<Props>`
  position: relative;
  font-size: ${p => p.size ?? '0px'};
  margin: ${p => p.margin ?? '0px'};
  padding: ${p => p.padding ?? '0px'};
`;

export const TextTitleTurnamen = styled.p`
  position: static;
  width: 103px;
  left: calc(50% - 103px / 2);
  top: 16%;
  bottom: 16%;

  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 140%;
  /* identical to box height, or 17px */

  /* White */

  color: #ffffff;

  /* Inside Auto Layout */

  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0px 10px;
`;

