import ShareProfile from './modal/ShareProfileModal';
import { unwrapResult } from '@reduxjs/toolkit';
import GradientButton from 'components/Buttons/GradientButton';
import { CardInfoGame } from 'components/CardInfoGame/index';
import MatchItem from 'components/Lists/MatchItem';
import MatchResultItem from 'components/Lists/MatchResultItem';
import MatchLayout from 'components/MatchLayout';
import HeaderAccount from 'components/cards/Account/HeaderAccount';
import AppLayout from 'components/layouts/AppLayout';
import Modal from 'components/layouts/Modal';
import { Avatar } from 'components/styled/avatar.styled';
import { Box, Stack } from 'components/styled/layout.styled';
import { NormalText, SpanText } from 'components/styled/text.styled';
import { ContainerToast } from 'components/styled/toast.styled';
import copy from 'copy-to-clipboard';
import { IconSource, ImageSource } from 'modules/assetpath';
import { dateFormat } from 'modules/helpers';
import history from 'modules/history';
import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router';
import { toast } from 'react-toastify';
import { useAppSelector } from 'redux/hooks';
import { fetchPublicProfile, fetchProfileHistory } from 'redux/publicProfile/slice';
import { useAppThunkDispatch } from 'redux/store';
import { getMyCurrentTeamFromSession } from 'redux/team/TeamService';
import { MatchHistory } from 'redux/teamProfile/slice';

const PublicProfile = (p: { gameSlug: string; playerSlug: string }) => {
  //@ts-ignore
  let { urlSlug } = useParams();
  const { data } = useAppSelector(state => state.publicProfile);
  const dispatch = useAppThunkDispatch();
  const [isLoading, setLoading] = useState(false);
  const [dataScrimChallenge, setDataScrimChallenge] = useState<MatchHistory[]>([]);
  const { gameSlug } = p;
  const [showErrorModal, setShowErrorModal] = useState(false);
  //NEED FIX: need fix witing for api for additional team info
  const currentTeam = getMyCurrentTeamFromSession();
  const teamId = currentTeam[0]?.id;
  //NEED FIX
  const states = useAppSelector(state => state.teamProfile);
  // const params = useParams() as { version: string, urlSlug : string };
  const [showModal, setShowModal] = useState(false);
  const { t } = useTranslation();
  const CopyText = (playerId: string, titleToast: string) => {
    if (copy(playerId))
      toast.info(titleToast, {
        position: 'top-center',
        autoClose: 1000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: false,
        progress: undefined,
      });
  };

  useEffect(() => {
    dispatch(fetchPublicProfile({ urlSlug: urlSlug }));
    fetchData();
  }, [dispatch]);
  const fetchData = () => {
    setLoading(true);
    const thunk = fetchProfileHistory({
      playerSlug: urlSlug,
      pageIndex: 1,
      pageSize: 100,
      version: '',
    });
    dispatch(thunk)
      .then(unwrapResult)
      .then(data => {
        if (data.error && data.status.teamHistory === 'error') {
          setShowErrorModal(true);
        } else {
          const items = data.items;
          items?.sort((a: MatchHistory, b: MatchHistory) =>
            (a as MatchHistory).startAt > (b as MatchHistory).startAt ? -1 : 1,
          );
          setDataScrimChallenge(items ?? []);
          setLoading(false);
        }
      })
      .catch(() => {
        setLoading(false);
      });
  };

  const modalError = () => {
    return (
      <Box padding="16px" width="100%">
        <Stack direction="column" margin="16px">
          <NormalText
            textAlign="center"
            color="#FFFFFF"
            fontSize="16px"
            fontWeight="bold"
            margin="8px 0px"
          >
            Terjadi Kesalahan!
          </NormalText>
          <NormalText
            textAlign="center"
            color="red"
            fontSize="14px"
            fontWeight="normal"
            margin="8px 0px"
          >
            {states.error}
          </NormalText>
          <br />
          <Box padding="8px" width="100%">
            <Stack direction="column" align="center">
              <GradientButton
                onClick={() => setShowErrorModal(false)}
                styled={{
                  backgroundSize: '100%',
                  width: '180px',
                }}
                variant="primary"
              >
                {t('player_scrim_match_schedule_not_available_ok')}
              </GradientButton>
            </Stack>
          </Box>
        </Stack>
      </Box>
    );
  };

  const MatchEmptyPage = () => {
    return (
      <Stack
        align="center"
        position="relative"
        direction="column"
        margin="68px 0px 0px"
        disableDrag
        disableSelectText
      >
        <img
          style={{ backgroundRepeat: 'no-repeat' }}
          src={IconSource('mabar-typeface-fullwhite')}
          alt="mabar-typeface-fullwhite"
        />
        <NormalText
          color="#969393"
          textAlign="center"
          fontStyle="normal"
          fontWeight="400"
          fontSize="14px"
          margin="24px 0px"
        >
          {t('player_player_profile_match_history_empty')}
        </NormalText>
      </Stack>
    );
  };

  const ClickBackHandle = () => {
    if (teamId) {
      history.goBack();
    } else {
      history.push('/');
    }
  };

  return (
    <AppLayout
      noMargin={true}
      opacityBg="0.04"
      appBar={{
        bg: {},
        navleft: {
          bg: 'ic-arrow-back-light',
          width: '24px',
          height: '24px',
          onClick: () => ClickBackHandle(),
        },
        navright: {
          bg: 'white',
          variant: 'share-social',
          onClick: () => setShowModal(true),
        },
        isDisable: false,
        title: t('player_player_profile_header_title'),
      }}
      backgroundImage={ImageSource('app.background')}
      backgroundMask="linear-gradient(359.1deg, #131318 0.29%, rgba(19, 19, 24, 0.97299) 32.28%, rgba(19, 19, 24, 0.934402) 43.9%, rgba(19, 19, 24, 0.89284) 56.38%, rgba(19, 19, 24, 0.02) 80.4%)"
      showModal={showModal}
      modalContent={<ShareProfile setShowModal={setShowModal} playerSlug={urlSlug} />}
    >
      <HeaderAccount
        avatarUrl={data?.picture ?? undefined}
        cityName={data?.school?.city?.name ?? ''}
        generation={data?.generation?.toString() ?? ''}
        // onClick={() => {}}
        personName={data?.firstName?.concat(` ${data.lastName ?? ''}`) ?? ''}
        schoolName={data?.school?.name ?? ''}
      />
      <NormalText
        margin="8px 21px"
        fontSize="14px"
        fontWeight="bold"
        lineHeight="140%"
        color="white"
      >
        {t('player_account_game_info_title')}
      </NormalText>
      {data?.games ? (
        <>
          <ContainerToast
            theme="dark"
            autoClose={3000}
            hideProgressBar
            newestOnTop
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable={false}
            pauseOnHover
          />
          {data?.games?.map(it => {
            return (
              <CardInfoGame
                key={it.gameProviderId}
                {...{
                  src: IconSource('ml-logo'),
                  gameId: t('player_team_profile_member_game_id', { gameId: it.gameProviderId }),
                  label: it.nickName,
                  copyText: t('player_player_profile_information_game_copy'),
                  onClick: () => {
                    CopyText(it.gameProviderId ?? '', t('general_success_game_id_copy'));
                  },
                }}
              />
            );
          })}{' '}
        </>
      ) : (
        <div
          style={{
            padding: '10px',
            height: '80px',
            top: '50%',
            background: 'rgba(55, 57, 66, 0.6)',
            borderRadius: '16px',
            textAlign: 'center',
            margin: '5px 16px',
          }}
        >
          <NormalText
            margin="6px 8px 16px"
            fontStyle="normal"
            fontSize="14px"
            fontWeight="700px"
            color="#969393"
            textAlign="center"
          >
            {t('player_player_profile_information_game_empty')}
          </NormalText>
        </div>
      )}
      <NormalText
        margin="30px 21px"
        fontSize="14px"
        fontWeight="bold"
        lineHeight="140%"
        color="white"
      >
        {t('player_player_profile_match_history')}
      </NormalText>
      <Stack direction="column" align="center">
        <MatchLayout style={{ width: '90%' }} showModal={showModal} modalContent={modalError()}>
          <Modal show={showErrorModal}>{modalError()}</Modal>
          {dataScrimChallenge && dataScrimChallenge.length >= 1 ? (
            dataScrimChallenge.map((data: MatchHistory) => {
              if (data.status.value === 'Dispute') {
                return (
                  <MatchResultItem
                    onClick={() => {}}
                    key={data.id}
                    variant="COMPETE"
                    headerTitle={<b>Menunggu verifikasi pemenang</b>}
                    data={{
                      firstTeam: data.firstTeam,
                      firstTeamAvatar: data.firstTeamAvatar,
                      firstTeamScore: data.firstTeamScore,
                      matchDate: data.startAt,
                      secondTeam: data.secondTeam,
                      secondTeamAvatar: data.secondTeamAvatar,
                      secondTeamScore: data.secondTeamScore,
                      matchType: ` - ${data.type.value
                        .replace('BO3', t('player_explore_scrim_match_mode'))
                        .replace('BO5', t('player_explore_scrim_match_mode_bo5'))}`,
                    }}
                  />
                );
              }
              if (data.status.value === 'ResultUpload') {
                return (
                  <MatchResultItem
                    onClick={() => {}}
                    key={data.id}
                    variant="COMPETE"
                    headerTitle={
                      <Stack direction="row" align="center">
                        <b>{t('player_scrim_status_message_result_upload')}</b>
                      </Stack>
                    }
                    data={{
                      firstTeam: data.firstTeam,
                      firstTeamAvatar: data.firstTeamAvatar,
                      matchDate: data.startAt,
                      secondTeam: data.secondTeam,
                      secondTeamAvatar: data.secondTeamAvatar,
                    }}
                  />
                );
              }
              if (
                data.status.value === 'DataCompleted' ||
                data.status.value === 'ResultCalculated'
              ) {
                const titleWin = (
                  <Stack align="center" direction="row">
                    &nbsp;
                    <b>{t('player_scrim_status_message_result_lose', { scrimMatchNo: data.id })}</b>
                    &nbsp;🏆
                  </Stack>
                );
                const titleLose = (
                  <b>{t('player_scrim_status_message_result_win', { scrimMatchNo: data.id })}</b>
                );
                const isWalkOver =
                  data.resultType &&
                  (data.resultType.key === '2' || data.resultType.value === 'Walkover');
                const firstTeamWinOrLose = data.winnerTeamId === teamId;
                const secondTeamWinOrLose = data.winnerTeamId !== teamId;
                const matchTitleResult = firstTeamWinOrLose ? titleWin : titleLose;
                const matchVariantResult = firstTeamWinOrLose ? 'WIN' : 'LOSE';
                return (
                  <MatchResultItem
                    onClick={() => {}}
                    key={data.id}
                    variant={matchVariantResult}
                    headerTitle={matchTitleResult}
                    data={{
                      firstTeam: data.firstTeam,
                      firstTeamAvatar: data.firstTeamAvatar,
                      firstTeamScore:
                        !(data.winnerTeamId === data.firstTeamId) && isWalkOver
                          ? 'WO'
                          : data.firstTeamScore,
                      firstTeamWin: data.winnerTeamId === data.firstTeamId,
                      matchDate: data.startAt,
                      secondTeam: data.secondTeam,
                      secondTeamAvatar: data.secondTeamAvatar,
                      secondTeamScore:
                        !secondTeamWinOrLose && isWalkOver ? 'WO' : data.secondTeamScore,
                      secondTeamWin: secondTeamWinOrLose,
                      matchType: data.type.value
                        ? ` - ${data.type.value
                            .replace('BO3', 'Best of 3')
                            .replace('BO5', 'Best of 5')}`
                        : undefined,
                    }}
                  />
                );
              }
              if (data.status.value === 'Reject') {
                return (
                  <MatchItem
                    key={data.id}
                    matchId={t('player_team_profile_scrim_no', { scrimId: data.id })}
                    matchDate={dateFormat(data.startAt, 'Asia/Jakarta', 'id').format(
                      'DD MMM yyyy, HH:mm z',
                    )}
                    data={{ ...data, secondTeam: undefined }}
                    noEnemyText={
                      <>
                        <b>{data.secondTeam}</b>&nbsp;
                        {t('player_scrim_detail_public_rejected_message')}
                      </>
                    }
                    onClick={(_e, m) => {
                      history.push(`/scrims/${gameSlug}/${m?.id ?? data.id}`);
                    }}
                    boxRight={
                      <Stack direction="column" align="center">
                        <SpanText
                          color="#969393"
                          fontSize="16px"
                          fontWeight="900"
                          textAlign="center"
                          lineHeight="22.4px"
                        >
                          Ditolak
                        </SpanText>
                      </Stack>
                    }
                  />
                );
              }
              if (data.status.value === 'Expired') {
                return (
                  <MatchItem
                    key={data.id}
                    matchId={t('player_team_profile_scrim_no', { scrimId: data.id })}
                    data={data}
                    matchDate={dateFormat(data.startAt, 'Asia/Jakarta', 'id').format(
                      'DD MMM yyyy, HH:mm z',
                    )}
                    noEnemyText={t('player_scrim_status_message_expired_opponent')}
                    boxRight={
                      <Stack direction="column" align="center">
                        <SpanText
                          color="#969393"
                          fontSize="16px"
                          fontWeight="900"
                          textAlign="center"
                          lineHeight="22.4px"
                        >
                          {t('player_scrim_status_message_expired')}
                        </SpanText>
                      </Stack>
                    }
                  />
                );
              }
              if (data.status.value === 'NotValid') {
                return (
                  <MatchResultItem
                    onClick={() => {}}
                    key={data.id}
                    variant="NotValid"
                    headerTitle={<b>{t('player_scrim_match_dispute_message')}</b>}
                    data={{
                      firstTeam: data.firstTeam,
                      firstTeamAvatar: data.firstTeamAvatar,
                      matchDate: data.startAt,
                      secondTeam: data.secondTeam,
                      secondTeamAvatar: data.secondTeamAvatar,
                    }}
                  />
                );
              }
              return <></>;
            })
          ) : isLoading ? (
            <Stack direction="column" align="center">
              <Avatar
                alt=""
                src={IconSource('ic-loader')}
                style={{ position: 'fixed', zIndex: 199, bottom: '8%', backgroundSize: '100%' }}
              />
            </Stack>
          ) : (
            !isLoading && dataScrimChallenge.length <= 0 && <MatchEmptyPage />
          )}
        </MatchLayout>
      </Stack>
    </AppLayout>
  );
};

export default PublicProfile;
