import InvitationEmptyPage from './InvitationEmpty';
import { Stack } from '@mui/material';
import { Avatar } from 'components/styled/avatar.styled';
import { Invitation } from 'design/Cards/Invitation';
import { Ribbon } from 'design/Ribbon';
import { GameOnToastStyle } from 'design/Ribbon/styles';
import { Text } from 'design/Text';
import { Toast } from 'design/Toast';
import _ from 'lodash';
import { IconSource } from 'modules/assetpath';
import React, { useState } from 'react';
import { use100vh } from 'react-div-100vh';
import { useTranslation } from 'react-i18next';
import { useTitle } from 'react-use';
import { fetchPlayerProfile, getPlayerProfileFromSession } from 'redux/account/AccountServices';
import { useAppDispatch, useAppSelector } from 'redux/hooks';
import { useAppThunkDispatch } from 'redux/store';
import { approveInvite, rejectInvite } from 'redux/team/TeamApproval';
import { fetchTeamDetail } from 'redux/team/TeamDetailReducer';
import {
  getTeamInvitationList,
  setTeamInvitationSelectedAction,
  TeamInvitation,
} from 'redux/team/TeamInvitationReducer';

const InvitationList = () => {
  useTitle(`Team > Invitation | Mabar`);
  const { t } = useTranslation();
  const height = use100vh();
  const playerInfo = getPlayerProfileFromSession();
  const dispatch = useAppDispatch();
  const thunkDispatch = useAppThunkDispatch();
  const { data: data, loading: isLoading, errors } = useAppSelector(state => state.teamInvitation);
  const { data: profile } = useAppSelector(state => state.account);
  const [getTeamInvitations, setTeamInvitations] = useState(data.items);
  const [getErrorList, setErrorList] = useState(errors);
  const [getPlayerInfo, setPlayerInfo] = useState(profile);

  const [openMsgSuccess, setMsgSuccessOpen] = useState(false);
  const [openMsgFail, setMsgFailOpen] = useState(false);
  const [openMsgCancel, setMsgCancelOpen] = useState(false);

  const playerProfileThunk = fetchPlayerProfile({
    username: getPlayerInfo?.userName ?? playerInfo.userName,
  });
  const teamInvititationListThunk = getTeamInvitationList({
    playerSlug: getPlayerInfo?.userName ?? playerInfo.userName,
    pageIndex: 1,
    pageSize: 50,
  });

  const fetchAccountProfile = () => {
    thunkDispatch(playerProfileThunk)
      .unwrap()
      .then(res => {
        res && res.id && setPlayerInfo(res);
      });
  };

  const fetchInvititation = React.useCallback(async () => {
    await thunkDispatch(teamInvititationListThunk)
      .unwrap()
      .then(res => {
        if (res && res?.isAxiosError) {
          setErrorList(res ?? {});
        } else {
          setTeamInvitations(res.items);
        }
      })
      .catch(err => {
        if (process.env.NODE_ENV === 'development') console.error(err);
      });
  }, [thunkDispatch, getTeamInvitations]);

  React.useEffect(() => {
    fetchAccountProfile();
    fetchInvititation();
  }, [thunkDispatch, getPlayerInfo?.userName]);

  const setTeamSelected = React.useCallback(
    (team: TeamInvitation) =>
      dispatch(setTeamInvitationSelectedAction({ teamInviteSelected: team })),
    [dispatch],
  );

  const approveInvitation = async (teamId: any) => {
    await thunkDispatch(
      approveInvite({
        playerId: getPlayerInfo?.id ?? playerInfo?.id,
        teamId: teamId,
      }),
    )
      .unwrap()
      .then(async res => {
        if (res && res?.isAxiosError) {
          setErrorList(res);
          setMsgFailOpen(true);
          await fetchInvititation();
        } else {
          await fetchInvititation();
          setMsgSuccessOpen(true);
        }
      })
      .catch(err => {
        if (process.env.NODE_ENV === 'development') {
          console.error(err);
        }
        fetchInvititation();
      });
  };

  const rejectInvitation = async (teamId: any) => {
    await thunkDispatch(
      rejectInvite({
        playerId: getPlayerInfo?.id ?? playerInfo?.id,
        teamId: teamId,
      }),
    )
      .unwrap()
      .then(async res => {
        if (res && res?.isAxiosError) {
          setErrorList(res);
          await fetchInvititation();
          setMsgFailOpen(true);
        } else {
          await fetchInvititation();
          setMsgCancelOpen(true);
        }
      })
      .catch(err => {
        if (process.env.NODE_ENV === 'development') {
          console.error(err);
        }
        fetchInvititation();
      });
  };
  const parseError = (errors: any) => {
    if (errors) {
      let errorsList: string[] = [];
      Object.values(errors).forEach((prop: any) => {
        errorsList = errorsList.concat(_.map(prop, _.property('message')));
      });
      return errorsList.join('\r\n');
    }
    return null;
  };

  return (
    <>
      <Toast show={openMsgSuccess} onClose={() => setMsgSuccessOpen(false)}>
        <Ribbon
          icon={IconSource('check-gameon')}
          label={
            <Text variant="body" sx={{ color: 'success900' }}>
              {t('invitation_msg_success')}
            </Text>
          }
          position="relative"
          sx={GameOnToastStyle}
        />
      </Toast>

      <Toast show={openMsgFail} onClose={() => setMsgFailOpen(false)}>
        <Ribbon
          label={
            <Text variant="body" sx={{ color: 'success900' }}>
              {parseError(getErrorList.errors) ??
                getErrorList?.detail ??
                'Ada kesalahan, Silahkan kamu coba lagi!'}
            </Text>
          }
          position="relative"
          sx={{ bgcolor: 'primary200', borderRadius: '8px', padding: '10px 12px' }}
        />
      </Toast>

      <Toast show={openMsgCancel} onClose={() => setMsgCancelOpen(false)}>
        <Ribbon
          label={
            <Text variant="body" sx={{ color: 'success900' }}>
              {t('invitation_msg_cancel')}
            </Text>
          }
          position="relative"
          sx={{ bgcolor: 'primary200', borderRadius: '8px', padding: '10px 12px' }}
        />
      </Toast>

      {isLoading && data.count <= 0 ? (
        <Stack
          alignItems="center"
          justifyContent="center"
          sx={{
            minHeight: height! - (56 + 43 + 55 + 48),
            height: !height ? 'calc(100vh - (56px + 43px + 55px + 48px))' : 'auto',
            width: '100%',
          }}
        >
          <Avatar
            alt=""
            src={IconSource('ic-loading')}
            style={{ zIndex: 199, backgroundSize: '100%' }}
          />
        </Stack>
      ) : data.count > 0 ? (
        <Stack direction="column">
          {getTeamInvitations !== undefined &&
            getTeamInvitations.map(data => {
              return (
                <>
                  <Invitation
                    id={data.id}
                    date={new Date(data.joinAt)}
                    title={data?.name}
                    subTitle={data?.game}
                    image={data?.avatarUrl}
                    isRead={false}
                    onAcceptInvitation={async () => {
                      await thunkDispatch(fetchTeamDetail({ teamSlug: data.urlSlug }))
                        .unwrap()
                        .then(res => {
                          const { isAxiosError, errors } = res as {
                            isAxiosError: boolean;
                            errors: any;
                          };
                          if (isAxiosError && errors) {
                            setErrorList(errors);
                            setMsgFailOpen(true);
                          } else {
                            setTeamSelected(data);
                            approveInvitation(data.id);
                            fetchInvititation();
                          }
                        })
                        .catch(err => {
                          if (process.env.NODE_ENV === 'development') console.error(err);
                        });
                    }}
                    onCancelInvitation={async () => {
                      await thunkDispatch(fetchTeamDetail({ teamSlug: data.urlSlug }))
                        .unwrap()
                        .then(res => {
                          const { isAxiosError, errors } = res as {
                            isAxiosError: boolean;
                            errors: any;
                          };
                          if (isAxiosError && errors) {
                            setErrorList(errors);
                            setMsgFailOpen(true);
                          } else {
                            setTeamSelected(data);
                            rejectInvitation(data.id);
                          }
                        })
                        .catch(err => {
                          if (process.env.NODE_ENV === 'development') console.error(err);
                        });
                    }}
                  />
                </>
              );
            })}
        </Stack>
      ) : (
        <InvitationEmptyPage />
      )}
    </>
  );
};

export default InvitationList;
