// import GradientButton from 'components/Buttons/GradientButton';
import InputEmailModalContent from './modal/InputEmailModalContent';
import InputPhoneModalContent from './modal/InputPhoneModalContent';
import AddImage from 'components/Forms/AddImage';
import { InputField } from 'components/Inputs/Input';
import SelectOption, { IOptions } from 'components/Inputs/SelectOption';
import MessageModal from 'components/UniPopup/Modals/MessageModal';
import AppLayout from 'components/layouts/AppLayout';
import { Avatar } from 'components/styled/avatar.styled';
import { Box, Stack } from 'components/styled/layout.styled';
import { NormalText } from 'components/styled/text.styled';
import { IconSource, ImageSource } from 'modules/assetpath';
import history from 'modules/history';
import React, { useEffect, useState } from 'react';
import { Controller, useForm, useWatch } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { EditAccountProfileProps } from 'redux/account/AccountProps';
import { AccountValidationProblemDetails } from 'redux/account/AccountResponse';
import {
  editAccountProfile,
  fetchPlayerProfile,
  getPlayerProfileFromSession,
} from 'redux/account/AccountServices';
import { useAppSelector } from 'redux/hooks';
import { useAppThunkDispatch } from 'redux/store';

//import InputButton from 'components/Inputs/InputButton';

const handleNavBack = () => {
  history.push('/account');
};

const EditProfile = () => {
  const { register, handleSubmit, control } = useForm();
  const { t } = useTranslation();
  const watchFields = useWatch({ control: control });
  const submitRef = React.createRef<HTMLInputElement>();
  let [showModal, setShowModal] = useState(false);
  let [modalType, setModalType] = useState<'email' | 'phone' | 'error' | 'loading'>('loading');

  const isFormComplete =
    watchFields['Generation'] && watchFields['StudentIdNumber'] && watchFields['StudentCardFile'];
  const dispatch = useAppThunkDispatch();
  const { profile, errors } = useAppSelector(({ account }) => ({
    profile: account.data,
    errors: account.errors,
  }));
  const [saveDisabled, setSaveDisabled] = useState(false);
  const [getPlayerProfile, setPlayerProfile] = useState(profile);
  const [getValidationErrors, setValidationErrors] = useState<AccountValidationProblemDetails>(
    errors as AccountValidationProblemDetails,
  );
  let isError = getValidationErrors && getValidationErrors?.errors;
  const playerProfileThunk = fetchPlayerProfile({
    username: getPlayerProfileFromSession().userName,
  });
  const fetchAccountProfile = () => {
    dispatch(playerProfileThunk)
      .unwrap()
      .then(res => {
        res && res.id && setPlayerProfile(res);
      });
  };

  const getGenerationYearList = (): IOptions[] => {
    const minYear = 2018;
    const maxYear = new Date();
    const temp: IOptions[] = [];
    for (let year = minYear; year <= maxYear.getFullYear(); year++) {
      temp.push({ label: year.toString(), value: year });
    }
    return temp;
  };

  useEffect(() => {
    fetchAccountProfile();
  }, [getPlayerProfile?.userName]);

  const saveAccountProfile = (data: any) => {
    let formData = data as EditAccountProfileProps;
    if (getPlayerProfile) {
      formData.Id = getPlayerProfile.id;
      formData.Email = data.Email ?? getPlayerProfile.email;
      formData.PhoneNumber = data.PhoneNumber ?? getPlayerProfile.phoneNumber;
      if (formData.Picture?.size == 0) {
        formData.Picture = undefined;
      }
      if (formData.StudentCardFile?.size == 0) {
        formData.StudentCardFile = undefined;
      }
      setModalType('loading');
      setShowModal(true);
      const editAccountProfileThunk = editAccountProfile(formData);
      dispatch(editAccountProfileThunk)
        .unwrap()
        .then(res => {
          if (res && res.isAxiosError && res.data?.status >= 400) {
            if (modalType === 'loading') {
              setShowModal(false);
            }
            setValidationErrors(res.data);
            setModalType('error');
            setShowModal(true);
          } else {
            setTimeout(() => {
              setShowModal(false);
              history.push('/account');
            }, 3000);
          }
        })
        .catch(() => {
          setModalType('error');
          setShowModal(true);
        });
    }
  };
  return (
    <AppLayout
      appBar={{
        bg: {},
        navleft: {
          bg: 'ic-arrow-back-light',
          width: '24px',
          height: '24px',
          onClick: handleNavBack,
        },
        navright: {
          bg: isFormComplete
            ? 'linear-gradient(131.42deg, #d51f40 46.48%, #e24134 62.51%, #e8542e 74.36%, #f37024 83.78%)'
            : '#969393',
          children: t('player_account_edit_profile_save'),
          variant: 'button',
          onClick: () => {
            submitRef.current?.click();
          },
        },
        isDisable: saveDisabled,
        title: t('player_account_edit_profile_header_title'),
      }}
      backgroundImage={ImageSource('app.background')}
      backgroundMask="linear-gradient(359.1deg, #131318 0.29%, rgba(19, 19, 24, 0.97299) 32.28%, rgba(19, 19, 24, 0.934402) 43.9%, rgba(19, 19, 24, 0.89284) 56.38%, rgba(19, 19, 24, 0.02) 80.4%)"
      showModal={showModal}
      modalStyled={{ background: modalType === 'loading' ? 'transparent' : '#373942' }}
      modalContent={
        modalType === 'email' ? (
          <InputEmailModalContent
            getShowModal={v => {
              setShowModal(v);
              if (v === false) {
                fetchAccountProfile();
              }
            }}
            modalType={{
              step: getPlayerProfile?.email
                ? getPlayerProfile?.emailConfirmed === true
                  ? 'input-email'
                  : 'verif-email'
                : 'input-email',
            }}
            onChangeEmail={v => v}
            defaultEmail={getPlayerProfile?.email ?? ''}
          />
        ) : modalType === 'phone' ? (
          <InputPhoneModalContent
            getShowModal={v => {
              setShowModal(v);
              if (v === false) {
                fetchAccountProfile();
              }
            }}
            modalType={{
              step: getPlayerProfile?.phoneNumber
                ? getPlayerProfile?.phoneNumberConfirmed === true
                  ? 'input-phone'
                  : 'verif-phone'
                : 'input-phone',
            }}
            onChangePhoneNumber={v => v}
            currentPhoneNumber={getPlayerProfile?.phoneNumber ?? ''}
          />
        ) : (
          (modalType === 'error' && (
            <MessageModal
              btnConfirmText={
                <NormalText
                  color="white"
                  fontWeight="700"
                  fontSize="14px"
                  textAlign="center"
                  padding="0px 80px 0px 80px"
                >
                  OK
                </NormalText>
              }
              onConfirmClick={() => setShowModal(false)}
              title={t('errors:HTTP400')}
            >
              <NormalText
                textAlign="center"
                color="#FF4D6D"
                fontSize="14px"
                fontWeight="normal"
                margin="8px 0px"
              >
                {getValidationErrors?.detail?.replace(
                  `'Content' must not be empty`,
                  'Data tidak boleh sama',
                )}
              </NormalText>
            </MessageModal>
          )) ||
          (modalType === 'loading' && (
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <Avatar src={IconSource('ic-loader')} />
            </div>
          ))
        )
      }
    >
      <form
        onSubmit={handleSubmit(saveAccountProfile)}
        noValidate
        autoComplete="off"
        autoCapitalize="off"
      >
        <Box padding="8px" width="100%" margin="50px 0px 40px">
          <Stack direction="column" align="center">
            <Controller
              control={control}
              name="Picture"
              defaultValue={getPlayerProfile?.picture ?? undefined}
              render={({ field: { onChange, ref } }) => (
                <AddImage
                  defaultImage={getPlayerProfile?.picture ?? undefined}
                  height={160}
                  isEffect
                  variant="Circle"
                  width={160}
                  descriptionText={t('player_account_edit_profile_upload_image_no_limit')}
                  getImageBlob={val => onChange(val)}
                  inputRef={ref}
                  placeholderStyle={{
                    color: isError && getValidationErrors.errors?.picture ? '#FF4D6D' : 'white',
                  }}
                  placeholder={
                    isError && getValidationErrors.errors?.picture
                      ? getValidationErrors.errors?.picture[0].message
                      : 'Unggah'
                  }
                  setSaveDisabled={setSaveDisabled}
                />
              )}
            />
          </Stack>
        </Box>

        <InputField
          label={t('player_account_edit_profile_email')}
          margin="24px 0px 8px"
          isRequired
          disabled
          type="email"
          inputMode="email"
          defaultValue={getPlayerProfile?.email ?? undefined}
          registerForm={register('Email')}
          validationType={isError && getValidationErrors.errors?.email ? 'ERROR' : undefined}
          validationText={
            isError && getValidationErrors.errors?.email
              ? getValidationErrors.errors?.email[0].message
              : undefined
          }
        />
        {/* <InputButton
          label={t('player_account_edit_profile_email')}
          buttonText={t('player_account_edit_profile_email_add')}
          defaultValue={getPlayerProfile?.email ?? undefined}
          disabled={getPlayerProfile?.emailConfirmed}
          btnRightText={getPlayerProfile?.email !== undefined && (getPlayerProfile?.emailConfirmed ? undefined:'Verifikasi')}
          onButtonRightClick={()=> {
            setModalType('email')
            setShowModal(true)
          }}
          onButtonClick={() => {
            setModalType('email')
            setShowModal(true)
          }}
          iconRight={getPlayerProfile?.emailConfirmed ? IconSource('check-account') : undefined}
          isError={isError && getValidationErrors.errors?.email ? true : false}
          errorMessage={isError && getValidationErrors.errors?.email ? getValidationErrors.errors?.email[0].message: undefined}
        />
        <InputButton
          label={t('player_account_edit_profile_phone_number')}
          buttonText={t('player_account_edit_profile_phone_number_add')}
          defaultValue={getPlayerProfile?.phoneNumber ?? undefined}
          disabled={getPlayerProfile?.phoneNumberConfirmed}
          btnRightText={getPlayerProfile?.phoneNumber !== undefined && (getPlayerProfile?.phoneNumberConfirmed ? undefined : 'Verifikasi')}
          onButtonRightClick={()=> {
            setModalType('phone')
            setShowModal(true)
          }}
          onButtonClick={() => {
            setModalType('phone')
            setShowModal(true)
          }}
          iconRight={getPlayerProfile?.phoneNumberConfirmed ? IconSource('check-account') : undefined}
          margin='24px 0px'
          isError={isError && getValidationErrors.errors?.phoneNumber ? true : false}
          errorMessage={isError && getValidationErrors.errors?.phoneNumber ? getValidationErrors.errors?.phoneNumber[0].message : undefined}
        /> */}
        <InputField
          label={t('player_account_edit_profile_phone_number')}
          margin="8px 0px"
          isRequired
          type="tel"
          inputMode="tel"
          defaultValue={getPlayerProfile?.phoneNumber ?? undefined}
          registerForm={register('PhoneNumber')}
          disabled
          validationType={isError && getValidationErrors.errors?.phoneNumber ? 'ERROR' : undefined}
          validationText={
            isError && getValidationErrors.errors?.phoneNumber
              ? getValidationErrors.errors?.phoneNumber[0].message
              : undefined
          }
        />

        <Controller
          control={control}
          name="Generation"
          defaultValue={getPlayerProfile?.generation ?? undefined}
          render={({ field: { onChange, ref } }) => (
            <SelectOption
              inputRef={ref}
              margin="24px 0px 0px"
              noOptionsMsg={t('player_account_edit_profile_select_message_join_year')}
              option={getGenerationYearList()}
              defaultValue={
                getPlayerProfile?.generation
                  ? { label: getPlayerProfile?.generation, value: getPlayerProfile?.generation }
                  : undefined
              }
              label={t('player_account_edit_profile_select_join_year')}
              placeholder={t('player_account_edit_profile_join_year')}
              isRequired
              isError={isError && getValidationErrors.errors?.generation ? true : false}
              errorMessage={
                isError && getValidationErrors.errors?.generation
                  ? getValidationErrors.errors?.generation[0].message
                  : undefined
              }
              onItemChange={(e: any) => {
                onChange(e.value);
              }}
            />
          )}
        />
        <InputField
          label={t('player_account_edit_profile_student_number')}
          placeholder="0001827111"
          margin="24px 0px"
          isRequired
          inputMode="numeric"
          type="number"
          defaultValue={getPlayerProfile?.studentIdNumber ?? undefined}
          registerForm={register('StudentIdNumber')}
          validationType={
            isError && getValidationErrors.errors?.studentIdNumber ? 'ERROR' : undefined
          }
          validationText={
            isError && getValidationErrors.errors?.studentIdNumber
              ? getValidationErrors.errors?.studentIdNumber[0].message
              : undefined
          }
        />
        <Controller
          control={control}
          name="StudentCardFile"
          defaultValue={getPlayerProfile?.studentCardFile ?? undefined}
          render={({ field: { onChange, ref } }) => (
            <AddImage
              inputRef={ref}
              getImageBlob={v => onChange(v)}
              defaultImage={getPlayerProfile?.studentCardFile ?? undefined}
              height={214}
              iconOptions={{
                backgroundHeight: '100%',
                backgroundRadius: '16px',
                backgroundWidth: '100%',
                iconHeight: '18px',
                iconMargin: '0px',
                iconPosition: 'center',
                iconSrc: 'add-photo',
                iconWidth: '22.5px',
              }}
              bgColor="transparent"
              widthMatchParent
              onImageLoad={function noRefCheck() {}}
              placeholder={
                isError && getValidationErrors.errors?.studentCardFile
                  ? getValidationErrors.errors?.studentCardFile[0].message
                  : t('player_account_edit_profile_upload_image_message')
              }
              placeholderStyle={{
                color: isError && getValidationErrors.errors?.picture ? '#FF4D6D' : '#969393',
                fontWeight: '700',
                fontSize: '12px',
                lineHeight: '17.52px',
              }}
              variant="Box"
              setSaveDisabled={setSaveDisabled}
            />
          )}
        />
        <input ref={submitRef} hidden type="submit" />
      </form>
    </AppLayout>
  );
};

export default EditProfile;
