import styled from 'styled-components';

export const TextIdScrim = styled.p`
  position: static;
  left: 6.9%;
  right: 6.9%;
  top: 16%;
  bottom: 16%;

  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 140%;
  /* identical to box height, or 17px */

  /* Gray 01 */

  color: #ddd8d8;

  /* Inside Auto Layout */

  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0px 0px;
`;

