export const TextJamPertandingan = styled.span`
  position: static;
  width: 75px;
  height: 22px;
  left: calc(50% - 75px / 2 + 2.5px);
  top: calc(50% - 22px / 2 - 8.5px);

  font-family: Roboto;
  font-style: normal;
  font-weight: 900;
  font-size: 16px;
  line-height: 140%;
  /* or 22px */

  text-align: right;

  /* White */

  color: #ffffff;

  /* Inside Auto Layout */
  word-wrap: break-word;

  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0px 0px;
`;

