import { Box, Stack } from '@mui/material';
import { Avatar } from 'components/styled/avatar.styled';
import { GameCard } from 'design/Cards/GameCard';
import { Text } from 'design/Text';
import { IconSource } from 'modules/assetpath';
import history from 'modules/history';
import React, { useEffect } from 'react';
import { use100vh } from 'react-div-100vh';
import { useTitle } from 'react-use';
import { fetchPlayerProfile } from 'redux/account/AccountServices';
import { fetchGameOptions, GameOptions } from 'redux/game/GameReducer';
import { useAppSelector } from 'redux/hooks';
import { useAppThunkDispatch } from 'redux/store';
import { getMyTeamAction } from 'redux/team/MyTeamReducer';
import { setGameSelectedAction } from 'redux/team/TeamDraftReducer';

const ListOfMyTeam = () => {
  useTitle(`Team > My Team | MABAR`);
  const height = use100vh();
  const dispatch = useAppThunkDispatch();
  const {
    playerInfo,
    myTeamData,
    myTeamGameId,
    teamWaitingId,
    isMyTeamLoading,
    gameListData,
    isGameListLoading,
  } = useAppSelector(({ myTeam, game, account }) => ({
    playerInfo: account?.data,
    myTeamData: myTeam.data?.items,
    myTeamGameId:
      myTeam.data.count > 0
        ? myTeam.data?.items
            .filter(
              arr =>
                !(arr.invitationType.key == '3' && arr.status?.key === '1') &&
                !(arr.invitationType.key == '2' && arr.status?.key === '1'),
            )
            .map(arr => arr.game.id)
        : [],
    teamWaitingId:
      myTeam.data.count > 0
        ? myTeam.data?.items
            .filter(arr => arr.invitationType.key == '3' && arr.status.key == '1')
            .map(arr => arr.game.id)
        : [],
    isMyTeamLoading: myTeam.loading,
    gameListData: game.gameOption,
    isGameListLoading: game.loading,
  }));

  useEffect(() => {
    if (playerInfo) {
      dispatch(getMyTeamAction({ pageIndex: 1, pageSize: 20, playerSlug: playerInfo.userName }));
      dispatch(
        fetchPlayerProfile({
          username: playerInfo.userName,
        }),
      );
    }
  }, [dispatch]);

  useEffect(() => {
    if (!gameListData) dispatch(fetchGameOptions({ pageIndex: 1, pageSize: 10 }));
  }, []);

  let saveGameSelected = React.useCallback(
    (gameSelected: GameOptions) => dispatch(setGameSelectedAction({ game: gameSelected })),
    [dispatch],
  );

  let handleGameSelected = (e: GameOptions) => {
    saveGameSelected(e);
    // setTimeout(()=> {
    history.push('/create-team/team-list');
    // }, 200)
  };

  return (
    <Box margin="24px 16px">
      {isMyTeamLoading ? (
        <Stack
          alignItems="center"
          justifyContent="center"
          spacing={2}
          sx={{
            minHeight: height! - (56 + 43 + 55 + 48),
            height: !height ? 'calc(100vh - (56px + 43px + 55px + 48px))' : 'auto',
          }}
        >
          <Avatar
            alt=""
            src={IconSource('ic-loading')}
            style={{ zIndex: 199, backgroundSize: '100%' }}
          />
        </Stack>
      ) : myTeamGameId.length >= 1 ? (
        <>
          <Text variant="h3" mb="16px">
            Tim Saya
          </Text>
          <Stack alignItems="center" mb="8px">
            {myTeamData.map(data => {
              if (
                (data?.invitationType?.key === '2' && data?.status?.key === '1') ||
                (data?.invitationType?.key === '3' && data?.status?.key === '1')
              ) {
                return <></>;
              } else {
                return (
                  <>
                    <GameCard
                      gameBg={data?.game?.avatarUrl}
                      gameName={data?.game?.name}
                      teamLogo={data?.avatarUrl}
                      teamName={data?.name ?? ''}
                      onClick={() => {
                        history.push(
                          `/team/${data?.game?.urlSlug}/${playerInfo?.school?.urlSlug}/${data?.urlSlug}`,
                        );
                      }}
                    />
                  </>
                );
              }
            })}
          </Stack>
        </>
      ) : (
        <></>
      )}
      {gameListData && (
        <>
          {isGameListLoading &&
          gameListData?.items.length <= 1 &&
          gameListData?.items[0].id == '' ? (
            <></>
          ) : (
            <>
              {myTeamGameId.length <= 0 && (
                <Text variant="h3" mb="16px">
                  Belum Ada Team
                </Text>
              )}
              <Stack alignItems="center" mb="8px">
                {gameListData !== undefined &&
                  gameListData?.items
                    .filter(v => v.id == '74a35f8f-8a1b-49bd-b8ed-49b206ab375b')
                    .map(data => {
                      if (myTeamGameId.includes(data?.id)) {
                        return <></>;
                      } else if (teamWaitingId.includes(data?.id)) {
                        return (
                          <>
                            <GameCard
                              gameLogo={data?.iconUrl}
                              gameBg={data?.avatarUrl}
                              gameName={data?.name}
                              isWaiting={true}
                              onClick={() => {
                                handleGameSelected(data);
                                // history.push('/create-team/choose-game');
                              }}
                            />
                          </>
                        );
                      } else {
                        return (
                          <>
                            <GameCard
                              gameLogo={data?.iconUrl}
                              gameBg={data?.avatarUrl}
                              gameName={data?.name}
                              onClick={() => {
                                handleGameSelected(data);
                                // history.push('/create-team/choose-game');
                              }}
                            />
                          </>
                        );
                      }
                    })}
              </Stack>
            </>
          )}
        </>
      )}
    </Box>
  );
};

export default ListOfMyTeam;
