import { Stack as StackMui, Link } from '@mui/material';
import Avatar from '@mui/material/Avatar';
import useMediaQuery from '@mui/material/useMediaQuery';
import { Box as BoxMui } from '@mui/system';
import { PrimaryButton } from 'design/Buttons/PrimaryButton';
import { SecondaryButton } from 'design/Buttons/SecondaryButton';
import { Text } from 'design/Text';
import { dateFormat, parseTimeDay, limitedChar } from 'modules/helpers';
import React, { FC } from 'react';
import { useTranslation } from 'react-i18next';

export interface InvitationProp {
  id?: string;
  date?: Date | undefined;
  title?: string;
  subTitle?: string;
  image?: string;
  isRead: boolean | false;
  onAcceptInvitation?: () => void;
  onCancelInvitation?: () => void;
}

export const Invitation: FC<InvitationProp> = ({ date, title, subTitle, image, isRead, onAcceptInvitation, onCancelInvitation }) => {
  const { t } = useTranslation();

  let colomnWidthText: string = '50%';
  let colomnWidthButton: string = '50%';
  const widthMax760 = useMediaQuery('(min-width: 414px)');
  const widthMax480 = useMediaQuery('(min-width: 320px)');

  if (widthMax760) {
    colomnWidthText = '46%';
    colomnWidthButton = '54%';
  } else if (widthMax480) {
    colomnWidthText = '30%';
    colomnWidthButton = '70%';
  } else {
    colomnWidthText = '50%';
    colomnWidthButton = '50%';
  }

  

  const getDayTimeLeft = () => {
    const timeLeft = parseTimeDay({
      dateTime: date,
      locale: 'id',
    });
    return timeLeft;
  };

  return (
    <>
     
      <BoxMui
        sx={{
         // outline: '#242628 solid 1px',
          background: isRead
            ? 'linear-gradient(282.07deg, rgba(234, 136, 45, 0.1) 0%, rgba(224, 21, 21, 0.1) 58.69%), #1A1817'
            : '#1A1817',
          boxShadow: 'inset 0px -1px 4px rgba(46, 48, 51, 0.25)',
          pl: '12px',
          pr: '12px',
        }}
      >
        <BoxMui sx={{ mb: '16px', pt: '12px' }}>
          <Text
            variant="bodySmall"
            sx={{
              lineHeight: '16px',
              fontWeight: '700',
              fontFamily: 'Inter',
              fontStyle: 'Mixed',
              color: '#94989E',
            }}
          >
            {getDayTimeLeft()
              ? t('invitation_date_today') +
                ' • ' +
                dateFormat(date, 'Asia/Jakarta', 'id').format('HH:mm')
              : dateFormat(date, 'Asia/Jakarta', 'id').format('DD MMMM YYYY • HH:mm')}{' '}
          </Text>
        </BoxMui>

        <StackMui direction="row">
          <StackMui sx={{ width: colomnWidthText, mb: '16px' }} direction="row">
            <Link href="#" underline="none">
              <Avatar
                variant="rounded"
                alt="Remy Sharp"
                src={image}
                sx={{ width: '40px', height: '40px', boxShadow: '#F1EBEB 0px 0px 0px 2px', borderRadius: '8px' }}
              />
            </Link>
            <StackMui
              direction="column"
              sx={{
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap',
                overflow: 'hidden',
              }}
            >
              <Text
                variant="labelSmall"
                sx={{
                  fontSize: '12px',
                  lineHeight: '16.8px',
                  fontWeight: '600',
                  letterSpacing: '0px',
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap',
                  overflow: 'hidden',
                  ml: '8px',
                  mb: '2px',
                }}
              >
                {limitedChar(title, 20)}
              </Text>
              <Text
                variant="subtitle"
                sx={{
                  fontSize: '10px',
                  lineHeight: '14px',
                  letterSpacing: '0.16px',
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap',
                  overflow: 'hidden',
                  ml: '8px',
                  color: '#94989E',
                }}
              >
                {limitedChar(subTitle, 20)}
              </Text>
            </StackMui>
          </StackMui>

          <StackMui
            direction="row"
            justifyContent="flex-end"
            alignItems="flex-end"
            sx={{
              width: colomnWidthButton,
              mb: '18px',
              textAlign: 'right',
            }}
          >
            <SecondaryButton onClick={onCancelInvitation} variant="small" sx={{ mr: '7px' }}>
              {t('invitation_reject')}
            </SecondaryButton>
            <PrimaryButton onClick={onAcceptInvitation} variant="small">
              {t('invitation_confirmation')}
            </PrimaryButton>
          </StackMui>
        </StackMui>
      </BoxMui>
    </>
  );
};
