import { TeamProfileNav } from './navigation';
import { Box, Stack, Avatar } from '@mui/material';
import { LayoutContext } from 'context/LayoutContext';
import { PrimaryButton } from 'design/Buttons/PrimaryButton';
import { SecondaryButton } from 'design/Buttons/SecondaryButton';
import { InfoModal } from 'design/Cards/InfoModal';
import { TeamProfileTournament } from 'design/Cards/TeamProfileTournament';
import { TextInput } from 'design/Forms/TextInput';
import { Modal } from 'design/Modal';
import { Ribbon } from 'design/Ribbon';
import { GameOnToastStyle, WarningStyle } from 'design/Ribbon/styles';
import { Text } from 'design/Text';
import { Toast } from 'design/Toast';
import he from 'he';
import { IconSource, ImageSource } from 'modules/assetpath';
import { onClickShare } from 'modules/helpers';
import history from 'modules/history';
import React, { useEffect, useState, useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';
import { fetchPlayerProfile } from 'redux/account/AccountServices';
import { useAppSelector } from 'redux/hooks';
import { useAppThunkDispatch } from 'redux/store';
import { resetInfoMessageAction } from 'redux/team/TeamDraftReducer';
import { exitFromTeam } from 'redux/team/TeamMemberReducer';
import {
  fetchTeamDetail,
  deleteMyTeam,
  fetchTeamMember,
  resetTeamProfile,
} from 'redux/teamProfile/slice';
import { fetchTournamentListMyTeam } from 'redux/tournament/TournamentServices';
import AuthService from 'services/AuthService';

const MyTeamProfile = () => {
  const { t } = useTranslation();
  const dispatch = useAppThunkDispatch();
  const auth = new AuthService();
  const isAuthenticated = auth.isAuthenticated();

  const params = useParams() as { gameSlug: string; schoolSlug: string; teamSlug: string };

  const [openModalConfirmation, setOpenModalConfirmation] = useState(false);
  const [getModalAction, setModalAction] = useState<
    | 'share-action'
    | 'confirm-action'
    | 'delete-team-failed-member'
    | 'delete-team-failed-match'
    | 'confirm-delete-team'
    | 'confirm-leave-team'
  >('confirm-action');
  const [showModalLoading, setShowModalLoading] = useState(false);
  const [inputTeamName, setInputTeamName] = useState('');
  const [pageTitle, setPageTitle] = useState('');
  const { setValue } = useContext(LayoutContext);
  const [openToast, setOpenToast] = useState(false);
  const [toastMessages, setToastMessages] = useState<any>('');
  const [openErrorToast, setOpenErrorToast] = useState(false);
  const [toastErrorMessages, setToastErrorMessages] = useState<any>('');
  const [maxMemberCount, setMaxMemberCount] = useState<any>('');

  const { playerInfo, myTeam, teamDetail, infoMessage } = useAppSelector(
    ({ account, myTeam, teamProfile, teamDraft }) => ({
      playerInfo: account?.data,
      myTeam: myTeam?.data?.items,
      teamDetail: teamProfile?.teamDetail,
      infoMessage: teamDraft?.infoMessage,
    }),
  );

  const handleFetchPlayerProfile = (username: string) => {
    dispatch(fetchPlayerProfile({ username: username }));
  };

  const handleFetchTeamDetail = (teamSlug: string) => {
    dispatch(fetchTeamDetail({ teamSlug: teamSlug, version: '' }));
  };

  const handleFetchTeamMember = (teamSlug: string) => {
    dispatch(
      fetchTeamMember({
        approved: false,
        teamSlug: teamSlug,
        pageIndex: 1,
        pageSize: 20,
        version: '',
      }),
    );
  };

  const handleFetchTournamentListMyTeam = (teamId: string) => {
    dispatch(fetchTournamentListMyTeam({ teamId: teamId, pageIndex: 1, pageSize: 100 }));
  };

  const myRole = isAuthenticated
    ? playerInfo?.games?.find(game => game?.team?.urlSlug === params.teamSlug)
    : undefined;
  const isCaptain = myRole?.team?.level?.value === 'Captain';

  useEffect(() => {
    if (isAuthenticated && playerInfo) {
      handleFetchPlayerProfile(playerInfo?.userName);
    }
    Promise.all([dispatch(resetTeamProfile()), handleFetchTeamDetail(params.teamSlug)]);
  }, []);

  useEffect(() => {
    if (teamDetail) {
      handleFetchTournamentListMyTeam(teamDetail.id);
      setMaxMemberCount(teamDetail?.memberCount ?? 0);
    }
  }, [teamDetail]);

  useEffect(() => {
    if (infoMessage && infoMessage.show) {
      setToastMessages(infoMessage.message);
      setOpenToast(infoMessage.show);
    } else {
      setToastMessages('');
      setOpenToast(false);
    }
  }, [infoMessage]);

  useEffect(() => {
    const splitPathname = history.location.pathname.split('/');
    const lastPathname = splitPathname[splitPathname.length - 1];
    if (!myTeam || lastPathname !== myTeam[0]?.urlSlug) {
      return history.push('/my-team');
    }
  }, []);

  const resetTostrMessage = () => {
    dispatch(resetInfoMessageAction());
  };

  const handleDeleteTeam = () => {
    if (isCaptain)
      if (maxMemberCount > 1) {
        setModalAction('delete-team-failed-member');
        setOpenModalConfirmation(true);
      } else {
        setModalAction('confirm-delete-team');
        setOpenModalConfirmation(true);
      }
  };

  const handleLeaveTeam = () => {
    setModalAction('confirm-leave-team');
    setOpenModalConfirmation(true);
  };

  const confirmDeleteTeam = () => {
    if (teamDetail && teamDetail?.id) {
      if (inputTeamName === teamDetail?.name) {
        dispatch(deleteMyTeam({ teamId: teamDetail?.id })).then((res: any) => {
          if (res) {
            setOpenModalConfirmation(false);
            handleFetchTeamMember(params.teamSlug);
            history.push('/my-team');
          }
        });
      } else {
        console.log('Nama tim tidak sama');
      }
    }
  };

  const cancelDeleteTeam = () => {
    setOpenModalConfirmation(false);
  };
  const cancelLeaveTeam = () => {
    setOpenModalConfirmation(false);
  };

  const confirmOutTeam = () => {
    const thunk = exitFromTeam({
      teamId: teamDetail?.id,
      playerId: playerInfo?.id,
    });
    dispatch(thunk)
      .unwrap()
      .then(res => {
        setOpenModalConfirmation(false);
        var skipRefresh = false;
        if ((res && res?.isAxiosError) || res?.data || res?.data?.status != 204) {
          if (res?.data?.detail != undefined) {
            if (res?.data?.status == 1300) {
              setModalAction('delete-team-failed-match');
              setOpenModalConfirmation(true);
              skipRefresh = true;
            }
          }
        }

        if (!skipRefresh) {
          return history.push('/my-team');
        }
      });
  };

  const navigationMenu = [
    {
      title: (
        <Stack direction="row" alignItems="center" spacing={1} sx={{ ml: '-8px' }}>
          <Avatar
            alt="exit"
            src={IconSource('ic-exit')}
            sx={{ width: '24px', height: '18px', img: { objectFit: 'initial' } }}
          />
          <Text>{t('header_action_leave_team')}</Text>
        </Stack>
      ),
      onClick: () => handleLeaveTeam(),
    },
    {
      title: (
        <Stack direction="row" alignItems="center" spacing={1} sx={{ ml: '-8px' }}>
          <Avatar
            alt="home"
            src={IconSource('ic-home')}
            sx={{ width: '24px', height: '18px', img: { objectFit: 'initial' } }}
          />
          <Text>{t('not_found_button')}</Text>
        </Stack>
      ),
      dest: `/`,
    },
  ];

  const navigationMenuForCaptain = [
    {
      title: (
        <Stack direction="row" alignItems="center" spacing={1} sx={{ ml: '-8px' }}>
          <Avatar
            alt="edit"
            src={IconSource('ic-edit')}
            sx={{ width: '24px', height: '18px', img: { objectFit: 'initial' } }}
          />
          <Text>{t('edit_team_profile')}</Text>
        </Stack>
      ),
      dest: `/team/${teamDetail?.game?.urlSlug}/${teamDetail?.urlSlug}/edit`,
    },
    {
      title: (
        <Stack direction="row" alignItems="center" spacing={1} sx={{ ml: '-8px' }}>
          <Avatar
            alt="exit"
            src={IconSource('ic-exit')}
            sx={{ width: '24px', height: '18px', img: { objectFit: 'initial' } }}
          />
          <Text>{t('player_team_profile_captain_disband_team')}</Text>
        </Stack>
      ),
      onClick: () => handleDeleteTeam(),
    },
    {
      title: (
        <Stack direction="row" alignItems="center" spacing={1} sx={{ ml: '-8px' }}>
          <Avatar
            alt="home"
            src={IconSource('ic-home')}
            sx={{ width: '24px', height: '18px', img: { objectFit: 'initial' } }}
          />
          <Text>{t('not_found_button')}</Text>
        </Stack>
      ),
      dest: `/`,
    },
  ];

  const handleOpenToast = (openToast: boolean, openErrorToast: boolean) => {
    if (openToast) {
      setOpenErrorToast(false);
      return openToast;
    }
    if (openErrorToast) {
      setOpenErrorToast(false);
      return openErrorToast;
    }
    return false;
  };

  const handleCloseToast = () => {
    setOpenToast(false);
    setOpenErrorToast(false);
    resetTostrMessage();
  };

  useEffect(() => {
    setValue({
      pageTitle: pageTitle,
      background: { color: '#1A1817' },
      appBar: {
        logo: false,
        title: (
          <Stack direction="column" sx={{ textAlign: 'center' }}>
            <Text variant="h3" sx={{ fontWeight: 700, letterSpacing: '0.12px' }}>
              {t('myteam_profile_title')}
            </Text>
            <Text variant="subtitle" sx={{ color: 'neutral400', mt: '2px' }}>
              Mobile Legends: Bang Bang
            </Text>
          </Stack>
        ),
        back: { to: '/my-team' },
        menu: isCaptain ? navigationMenuForCaptain : navigationMenu,
        share: (shareOpen: boolean, handleShare: () => void) => {
          if (shareOpen == true) {
            onClickShare({
              title: 'Match > Tim Profile',
              handleIconShare: () => handleShare(),
            });
          }
        },
      },
      padding: '0',
      bottomBar: {
        show: false,
      },
      additional: (
        <>
          <Toast
            show={handleOpenToast(openToast, openErrorToast)}
            onClose={handleCloseToast}
            duration={3000}
          >
            {openToast && (
              <Ribbon
                icon={IconSource('check-gameon')}
                label={
                  <Text variant="body" sx={{ color: 'success900' }}>
                    {toastMessages}
                  </Text>
                }
                sx={GameOnToastStyle}
              />
            )}
            {openErrorToast && (
              <Ribbon
                label={
                  <Text variant="body" sx={{ color: 'success900' }}>
                    {toastErrorMessages}
                  </Text>
                }
                sx={WarningStyle}
              />
            )}
          </Toast>
        </>
      ),
    });
  }, [isCaptain, pageTitle, teamDetail, openToast, openErrorToast, maxMemberCount]);

  return (
    <>
      <Box
        sx={theme => ({
          p: '16px 16px 0 16px',
          borderTop: `1px solid ${theme.palette.neutral530}`,
          bgcolor: 'neutral700',
        })}
      >
        <TeamProfileTournament
          bgImage={ImageSource('profile.background')}
          teamLogo={teamDetail?.avatarUrl}
          teamName={teamDetail?.name}
          teamInfo={teamDetail?.school?.name}
          margin="0"
          size={{
            width: '90px',
            height: '90px',
          }}
        />
      </Box>

      <TeamProfileNav
        hash={history.location.hash || '#match'}
        gameSlug={params.gameSlug}
        teamSlug={params.teamSlug}
        setShowModalLoading={setShowModalLoading}
        setPageTitle={setPageTitle}
        setOpenToast={setOpenToast}
        setToastMessages={setToastMessages}
        setOpenErrorToast={setOpenErrorToast}
        setToastErrorMessages={setToastErrorMessages}
      />

      <Modal show={openModalConfirmation}>
        {getModalAction === 'confirm-leave-team' ? (
          <InfoModal
            cta={
              <Stack direction="row" justifyContent="flex-end" sx={{ width: '100%' }}>
                <SecondaryButton sx={{ mr: '16px' }} onClick={() => cancelLeaveTeam()}>
                  Batal
                </SecondaryButton>
                <PrimaryButton
                  sx={theme => ({ background: theme.palette.danger600 })}
                  onClick={() => confirmOutTeam()}
                >
                  {t('player_team_profile_member_confirm_resign')}
                </PrimaryButton>
              </Stack>
            }
          >
            <Box sx={{ width: '100%' }}>
              <Text variant="h2" mb="8px">
                {isCaptain
                  ? t('player_team_profile_captain_disband_team')
                  : t('player_team_profile_member_resign_team')}
              </Text>
              <Text
                dangerouslySetInnerHTML={{
                  __html: he.decode(
                    t('player_team_profile_leave_popup_message', { teamName: teamDetail?.name }),
                  ),
                }}
              ></Text>
            </Box>
          </InfoModal>
        ) : getModalAction === 'confirm-delete-team' ? (
          <InfoModal
            cta={
              <Stack direction="row" justifyContent="flex-end" sx={{ width: '100%' }}>
                <SecondaryButton sx={{ mr: '16px' }} onClick={() => cancelDeleteTeam()}>
                  Batal
                </SecondaryButton>
                <PrimaryButton
                  sx={theme => ({ background: theme.palette.danger600 })}
                  onClick={() => confirmDeleteTeam()}
                >
                  {t('player_team_profile_remove_popup_confirmation_button')}
                </PrimaryButton>
              </Stack>
            }
          >
            <Box>
              <Text variant="h2" textAlign="left" mb="8px">
                {t('player_team_profile_captain_disband_team')}
              </Text>
              <>
                <Text>{t('player_team_profile_delete_team')}</Text>
                <TextInput
                  id="inputteamname"
                  label="Nama Tim"
                  onChange={e => {
                    setInputTeamName(e.target.value);
                  }}
                  placeholder="Masukan nama tim kamu"
                  sx={{ mt: '16px' }}
                />
                {inputTeamName !== '' && teamDetail?.name !== inputTeamName && (
                  <Text variant="subtitle" sx={{ color: 'danger600', mt: '3px' }}>
                    Nama tim tidak sesuai
                  </Text>
                )}
              </>
            </Box>
          </InfoModal>
        ) : getModalAction === 'delete-team-failed-member' ? (
          <InfoModal
            cta={
              <Stack direction="row" justifyContent="flex-end" sx={{ width: '100%' }}>
                <PrimaryButton onClick={cancelDeleteTeam}>Tutup</PrimaryButton>
              </Stack>
            }
          >
            <Box sx={{ width: '100%' }}>
              <Text variant="h2" mb="8px">
                Permintaan Gagal
              </Text>
              <Text>Pembubaran team tidak diizinkan apabila masih ada anggota di dalamnya.</Text>
            </Box>
          </InfoModal>
        ) : (
          getModalAction === 'delete-team-failed-match' && (
            <InfoModal
              icon={ImageSource('Warning', 'png')}
              cta={
                <Stack direction="row" justifyContent="flex-end" sx={{ width: '100%' }}>
                  <SecondaryButton onClick={cancelDeleteTeam} wide>
                    Oke
                  </SecondaryButton>
                </Stack>
              }
            >
              <Box sx={{ width: '100%' }}>
                <Text variant="h2" mb="8px" textAlign="center">
                  Permintaan Gagal
                </Text>
                <Text textAlign="center">
                  Pembubaran team dan keluar dari team tidak diizinkan di saat tim memiliki jadwal
                  pertandingan.
                </Text>
              </Box>
            </InfoModal>
          )
        )}
      </Modal>

      <Modal
        show={showModalLoading}
        contentSx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          background: 'transparent',
        }}
      >
        <img src={IconSource('ic-loading')} style={{ position: 'relative', outline: 0 }} />
      </Modal>
    </>
  );
};

export default MyTeamProfile;
