import { NormalText } from 'components/styled/text.styled';
import MessageModal from 'components/UniPopup/Modals/MessageModal';
import { LogDev } from 'modules/helpers';
import React, { useEffect, useState } from 'react'
import { useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { AccountValidationProblemDetails } from 'redux/account/AccountResponse';
import { changePhoneNumber, verifyPhoneNumber } from 'redux/account/AccountServices';
import { useAppSelector } from 'redux/hooks';
import { useAppThunkDispatch } from 'redux/store';
import NotFound from 'routes/NotFound';
import { EditProfileFormValues, inputOTPCodeFormOptions, inputPhoneNumberFormOptions } from 'validation/account/EditProfileValidation';
import InputPhoneModal from './InputPhoneModal';
import VerificationCodeModal from './VerificationCodeModal';
interface ModalStateType {
    step: 'input-phone' | 'verif-phone' | 'success' | 'failed';
}

const InputPhoneModalContent = (props: { getShowModal: (val: boolean) => void, currentPhoneNumber: string, onChangePhoneNumber: (val: string) => void, modalType :ModalStateType }) => {
    const { t } = useTranslation();
    const { isLoading, isReduxError } = useAppSelector(({ account }) => ({
        isLoading: account.status === 'loading' ? true : false,
        isReduxError: account.status === 'error' ? true : false
    }));
    const [getPhoneNumber, setPhoneNumber] = useState(props.currentPhoneNumber);
    let [getVerifyTime, setVerifyTime] = useState(new Date().getTime())
    const [getTokenCode, setTokenCode] = useState<number>(0);
    const [modalState, setModalState] = useState<ModalStateType>(props.modalType ?? { step: 'input-phone' });
    const { control, handleSubmit } = useForm(modalState.step === 'input-phone' ? inputPhoneNumberFormOptions : modalState.step === 'verif-phone' ? inputOTPCodeFormOptions : undefined); 
    let [getValidationForm, setValidationForm] = useState<EditProfileFormValues | AccountValidationProblemDetails>({})
    let formError = getValidationForm ? (getValidationForm as EditProfileFormValues) : {}
    let respError = isReduxError ? (getValidationForm as AccountValidationProblemDetails) : {}
    let isInputPhoneError = formError?.newPhoneNumber ? true : respError?.status ?? 204 >= 400 ? true : false;
    let inputPhoneErrorMsg = formError?.newPhoneNumber?.message ? formError?.newPhoneNumber?.message :
        respError ? respError.errors?.newPhoneNumber ? respError.errors?.newPhoneNumber[0].message :
            respError.detail ? respError.detail : '' : ''
    let isVerifyOTPError = formError?.token ? true : respError?.status ?? 204 >= 400 ? true : false; 
    let inputVerifyOTPErrorMsg = formError?.token?.message ? formError?.token?.message :
        respError ? respError.title ? respError.title :
            respError.detail ? respError.detail : undefined : undefined      
    const dispatch = useAppThunkDispatch();
    const changePhoneNumberThunk = changePhoneNumber({ currentPhoneNumber: props.currentPhoneNumber, newPhoneNumber: getPhoneNumber })
    const verifyPhoneNumberThunk = verifyPhoneNumber({ newPhoneNumber: getPhoneNumber, token: getTokenCode })
    async function confirmChangePhoneNumber() {
        props.onChangePhoneNumber(getPhoneNumber)
        await dispatch(changePhoneNumberThunk).unwrap().then((res) => {
            if (res && res.isAxiosError && res.data?.status >= 400) {
                setValidationForm(res.data as AccountValidationProblemDetails)
                if(res.data.status >= 500)                 
                    setModalState({ step: 'failed' })
            } else {
                setModalState({ step: 'verif-phone' })
            }
        }).catch((err) => {
            if (process.env.NODE_ENV === 'development')
                console.error(err)
        })
    }
    async function resendOTPCode() {
        setVerifyTime(new Date().getTime())
        await dispatch(changePhoneNumberThunk).unwrap().then((res) => {
            if (res && res.isAxiosError && res.data?.status >= 400) {
                setValidationForm(res.data as AccountValidationProblemDetails)
                if(res.data.status >= 500)                 
                    setModalState({ step: 'failed' })
            } else {
                if (process.env.NODE_ENV === 'development')
                    LogDev('INFO',res)
            }
        }).catch((err) => {
            if (process.env.NODE_ENV === 'development')
                console.error(err)
        })
    }
    async function verifyOTPCode() {
        await dispatch(verifyPhoneNumberThunk).unwrap().then((res) => {
            if (res && res.isAxiosError && res.data?.status >= 400) {
                setValidationForm(res.data as AccountValidationProblemDetails)
                if(res.data.status >= 500)
                    setModalState({ step: 'failed' })
            } else {
                setModalState({ step: 'success' })
            }
        }).catch((err) => {
            if (process.env.NODE_ENV === 'development')
                console.error(err)
        })
    }
    function onFormSubmitInvalid(e?: EditProfileFormValues) {
        e && setValidationForm(e)
    }
    useEffect(() => {
        if(props.modalType.step === 'verif-phone'){
            resendOTPCode()
        }
    }, [props.modalType.step])
    return (modalState?.step === 'input-phone' ?
        (
            <form onSubmit={handleSubmit(confirmChangePhoneNumber, onFormSubmitInvalid)} noValidate style={{ width: '100%' }}>
                <InputPhoneModal
                    formControl={control}
                    isLoading={isLoading}
                    isError={isInputPhoneError}
                    errorMessage={inputPhoneErrorMsg}
                    defaultPhoneNumber={getPhoneNumber}
                    onConfirmClick={() => null}
                    onCancelClick={() => props.getShowModal(false)} 
                    onChangePhone={(e) => {
                        setPhoneNumber(e.target.value)
                        setValidationForm({})
                    }}/>
            </form>
        ) : modalState?.step === 'verif-phone' ?
            (
            <form onSubmit={handleSubmit(verifyOTPCode, onFormSubmitInvalid)} noValidate style={{ width: '100%' }}>
                <VerificationCodeModal
                    formControl={control}
                    verifyTime={getVerifyTime}
                    isError={isVerifyOTPError}
                    errorMessage={inputVerifyOTPErrorMsg}                    
                    isLoading={isLoading}
                    phoneNumber={getPhoneNumber}
                    onChangeCode={(v) => {
                        setTokenCode(parseInt(v))
                        setValidationForm({})
                    }}
                    onChangeClick={() => setModalState({ step: 'input-phone' })}
                    onResendClick={async () => await resendOTPCode()}
                    onConfirmClick={async () => null}
                    onCancelClick={() => props.getShowModal(false)} />
            </form>
            ) : modalState?.step === 'success' ? (
                <MessageModal title={t('player_account_verification_flow_phone_success_title')}
                    onConfirmClick={() => {
                        props.getShowModal(false)
                    }}
                    btnConfirmText={
                        <NormalText
                            color="white"
                            fontWeight="700"
                            fontSize="14px"
                            textAlign="center"
                            padding="0px 80px 0px 80px">
                            {t('player_account_verification_flow_phone_success_button')}
                        </NormalText>
                    }>
                    <NormalText
                        textAlign="center"
                        color="#969393"
                        fontSize="14px"
                        fontWeight="normal"
                        margin="8px 0px">
                        {t('player_account_verification_flow_phone_message_otp')}
                    </NormalText>
                </MessageModal>
            ) : modalState?.step === 'failed' ? (                 
                <MessageModal title={t('player_account_verification_flow_phone_failed_title')}
                    onConfirmClick={() => {
                        props.getShowModal(false)
                    }}
                    btnConfirmText={
                        <NormalText
                            color="white"
                            fontWeight="700"
                            fontSize="14px"
                            textAlign="center"
                            padding="0px 80px 0px 80px">
                            {t('player_account_verification_flow_phone_success_button')}
                        </NormalText>
                    }>
                    <NormalText
                        textAlign="center"
                        color="#FF4D6D"
                        fontSize="14px"
                        fontWeight="500"
                        margin="8px 0px">
                        {t('player_account_verification_flow_phone_failed_message')}
                    </NormalText>
                </MessageModal>
            ) : <NotFound/>                
    )
}

export default InputPhoneModalContent;