declare module '@mui/material/styles' {
  //Palette
  interface Palette extends ColorTypes {}
  interface PaletteOptions extends ColorTypes {}

  //Typography
  interface TypographyVariants extends TypographTypes {}
  interface TypographyVariantsOptions extends TypographTypes {}

  //Theme
  interface Theme {
    headerHeight: number;
    bottomBarHeight: number;
    appColor: string;
    easing: string;
    button: ButtonBorderRadiusTypes;
    borderRadius: BorderRadiusTypes;
    fontFamily: FontFamilyTypes;
    lineHeight: LineHeightTypes;
  }
  interface ThemeOptions {
    headerHeight: number;
    bottomBarHeight: number;
    appColor: string;
    easing: string;
    button: ButtonBorderRadiusTypes;
    borderRadius: BorderRadiusTypes;
    fontFamily: FontFamilyTypes;
    lineHeight: LineHeightTypes;
  }
}

// Update the Typography's variant prop options
declare module '@mui/material/Typography' {
  interface TypographyPropsVariantOverrides {
    h1: true;
    h2: true;
    h3: true;
    label: true;
    labelSmall: true;
    body: true;
    bodySmall: true;
    button: true;
    subtitle: true;
    ribbon: true;
    h4: false;
    h5: false;
    h6: false;
    subtitle1: false;
    subtitle2: false;
    body1: false;
    body2: false;
    caption: false;
    overline: false;
  }
}
