import styled from 'styled-components';

export const InputCode = styled.input`
  appearance: none;
  -webkit-appearance: none;
  background: transparent;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
  padding: 10px;
  position: static;
  width: 48px;
  height: 48px;
  border: 1px solid #969393;
  box-sizing: border-box;
  border-radius: 12px;
  margin-left: 8px;
  margin-right: 8px;
  cursor: text;
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 700;
  line-height: 19.6px;
  color: #969393;
  &:focus {
    outline: none !important;
    border-color: orange;
  }
  &::-moz-selection {
    background: transparent;
  }
  &::selection {
    background: transparent;
  }
  &:invalid {
    outline: none !important;
    border-color: #e43c5a;
  }
  &::-webkit-inner-spin-button{
      display: none;
  }
`;