import React, { FC, ReactNode } from 'react';
import AppLayout from 'components/layouts/AppLayout';
import { ImageSource } from 'modules/assetpath';
import { Blockquoute, Box, Stack } from 'components/styled/layout.styled';
import history from 'modules/history';
import { compiler } from 'markdown-to-jsx';
import { GradientText, NormalText, SpanText } from 'components/styled/text.styled';
import BackgroundImage from 'components/layouts/BackgroundImage';
import { getPlayerProfileFromSession } from 'redux/account/AccountServices';

export interface SupportLayoutProps {
  headers: HeaderSupportType;
  content?: string;
  children?: ReactNode;
}

const SupportLayout = ({ headers, content, children }: SupportLayoutProps) => {
  const myprofile = getPlayerProfileFromSession();

  const ClickBackHandle = () => {
    if (myprofile?.id) {
      history.goBack();
    } else {
      history.push('/');
    }
  };

  return (
    <AppLayout
      appBar={{
        bg: {},
        navleft: {
          bg: 'ic-arrow-back-light',
          width: '24px',
          height: '24px',
          onClick: () => ClickBackHandle(),
        },
        navright: {
          bg: '',
          variant: '',
        },
        isDisable: false,
        title: headers.title,
      }}
      noMargin={true}
      opacityBg="0.045"
      backgroundImage={ImageSource('app.background')}
      backgroundMask="linear-gradient(359.1deg, #131318 0.29%, rgba(19, 19, 24, 0.97299) 32.28%, rgba(19, 19, 24, 0.934402) 43.9%, rgba(19, 19, 24, 0.89284) 56.38%, rgba(19, 19, 24, 0.02) 80.4%)"
    >
      <HeaderSupport {...headers}>
        {content ? (
          <Box
            background="rgba(55, 57, 66, 0.6)"
            padding="16px"
            borderRadius="16px"
            margin="10px 10px 80px"
          >
            <Stack direction="row" style={{ alignItems: 'flex-start' }}>
              <Stack direction="column" align="start">
                <SpanText
                  fontSize="12px"
                  color="white"
                  lineHeight="17.52px"
                  fontWeight="400"
                  textAlign="justify"
                  textJustify="inter-word"
                  letterSpacing="0px"
                  onClick={e => e}
                >
                  {compiler(content, {
                    overrides: {
                      strong: {
                        component: SpanText,
                        props: {
                          color: 'white',
                          fontSize: '12px',
                          fontWeight: '700',
                          lineHeight: '19.6px',
                        },
                      },
                      h3: {
                        component: SpanText,
                        props: {
                          color: '#969393',
                          fontSize: '14px',
                          fontWeight: '500',
                          lineHeight: '19.6px',
                        },
                      },
                      a: {
                        component: GradientText,
                        props: {
                          fontSize: '12px',
                          fontWeight: '700',
                          lineHeight: '140%',
                          isPressable: true,
                          disableSelectText: true,
                          onClick: (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => {
                            if (e.currentTarget && e.currentTarget.getAttribute('href'))
                              window.open(e.currentTarget.getAttribute('href') ?? '#', '_blank');
                          },
                        },
                      },
                      blockquote: {
                        component: Blockquoute,
                        props: {
                          display: 'block',
                          background: '#2D2F37',
                          borderRadius: '16px',
                          width: 'auto',
                          padding: '4px 16px',
                        },
                      },
                    },
                  })}
                </SpanText>
              </Stack>
            </Stack>
          </Box>
        ) : (
          children
        )}
      </HeaderSupport>
    </AppLayout>
  );
};

export interface HeaderSupportType {
  headerBackground?: string | undefined;
  title?: string | undefined;
  update?: string | undefined;
  children?: React.ReactNode;
}

export const HeaderSupport: FC<HeaderSupportType> = ({
  headerBackground,
  title,
  update,
  children,
}) => {
  return (
    <Box width="100%" disableDrag>
      <BackgroundImage
        src={headerBackground}
        backgroundColor="#0b0b0ee6"
        minHeight="242px"
        mask="linear-gradient(transparent, #000000a4, #2e29294c, #2e292926, transparent)"
      >
        <Box width="100%">
          <Stack
            direction="column"
            align="start"
            justify-content="end"
            margin="-55px 24px 0px"
            disableSelectText
          >
            <NormalText textAlign="center" color="#ffffff" fontSize="25.2px" fontWeight="700">
              {title}
            </NormalText>
            <NormalText
              textAlign="center"
              color="#969393"
              margin="8px 0px 0px"
              fontSize="12px"
              fontWeight="700"
              lineHeight="17.52px"
            >
              {update}
            </NormalText>
          </Stack>
          <Stack position="absolute" disableDrag display="flex" zIndex={1945} margin="16px 0px 0px">
            {children}
          </Stack>
        </Box>
      </BackgroundImage>
    </Box>
  );
};
export default SupportLayout;
