import { Button } from 'components/styled/button.sytled';
import { GradientText, NormalText } from 'components/styled/text.styled';
import React, { FC } from 'react';

export interface TabButtonType {
  id?: number | undefined;
  title?: string | undefined;
  isSelected?: boolean | undefined;
  onClickButton?: (() => void) | undefined;
}

const TabButton: FC<TabButtonType> = ({ title, isSelected, onClickButton }) => {
  return (
    <Button
      onClick={() => {
        if (onClickButton) {
          onClickButton();
        }
      }}
      background={isSelected ? 'rgba(55, 57, 66, 0.4)' : 'none'}
      borderRadius="8px"
      padding="8px 16px"
    >
      {isSelected ? (
        <GradientText
          fontSize="14px"
          fontWeight="700"
          color="linear-gradient(to bottom right, rgba(231, 85, 112, 1),rgba(237, 103, 106, 1), rgba(241, 114, 103, 1),rgba(253, 150, 91, 1))"
        >
          {title}
        </GradientText>
      ) : (
        <NormalText fontSize="14px" fontWeight="700" color="rgba(106, 99, 99, 1)">
          {title}
        </NormalText>
      )}
    </Button>
  );
};

export default TabButton;
