import styled, { keyframes, Keyframes } from "styled-components";

export interface AnimationProps {
  display?: 'flex' | 'block' | 'inline-block' | 'inline-flex' | undefined;
  position?:
    | 'fixed'
    | 'relative'
    | 'sticky'
    | 'absolute'
    | 'static'
    | '-webkit-sticky'
    | 'inherit'
    | 'initial'
    | '-ms-page'
    | 'unset'
    | undefined;
  background?: string;
  borderRadius?: string;
  border?: string;
  boxShadow?: string;
  color?: string;
  margin?: string;
  padding?: string;
  alignSelf?: 'center' | 'start' | 'end' | undefined;
  width?: string;
  height?: string;
  customAnimation?:{
      keyFrame?: Keyframes;
      duration?:string;
      delay?: string;
      iterationCount?: 'infinite' | string;
      direction?: 'normal' | 'reverse' | 'alternate' | 'alternate-reverse';
      timingFunction?: 'ease' | 'linear' | 'ease-in' | 'ease-out' | 'ease-in-out';
      fillMode?: 'none' | 'forwards' | 'backwards' | 'both';
  }
}

const Blinker = keyframes`
    50% {
        opacity: 0;
    }
`;

export const Rectangle = styled.rect<AnimationProps>`
  display: ${({ display }) => display ?? 'flex'};
  position: ${({ position }) => position};
  background: ${({ background }) => background};  
  border-radius: ${({ borderRadius }) => borderRadius};  
  border: ${({ border }) => border};  
  color: ${({ color }) => color};  
  margin: ${({ margin }) => margin};  
  padding: ${({ padding }) => padding};  
  height: ${({ height }) => height};  
  width: ${({ width }) => width};  
  box-shadow: ${({ boxShadow }) => boxShadow};
  animation-name: ${({ customAnimation }) => customAnimation?.keyFrame ?? Blinker};
  animation-duration:${({ customAnimation }) => customAnimation?.duration ?? '1s'};
  animation-iteration-count:${({ customAnimation }) => customAnimation?.iterationCount ?? 'infinite'};
  animation-delay:${({ customAnimation }) => customAnimation?.delay};
  animation-timing-function:${({ customAnimation }) => customAnimation?.timingFunction ?? 'linear'};
  animation-direction:${({ customAnimation }) => customAnimation?.direction};
  animation-fill-mode:${({ customAnimation }) => customAnimation?.fillMode};
  align-self: ${({ alignSelf }) => {
    switch (alignSelf) {
      case 'start':
        return 'flex-start';
      case 'end':
        return 'flex-end';
      case 'center':
        return 'center';
      default:
        return undefined;
    }
  }};
`;

