import React, { ReactNode } from 'react';
import Stack from '@mui/material/Stack';
import { InformationBox, ImageBox, ImageTop, ImageBottom } from './styles';
import { ImageSource } from 'modules/assetpath';

interface InformationModalProps {
  icon?: string;
  cta?: ReactNode;
  children?: ReactNode;
}

export function InfoModal({ icon, cta, children }: InformationModalProps) {
  return (
      <InformationBox>
        <Stack justifyContent="center" alignItems="center" width="100%">
          {icon && (
            <ImageBox>
              <ImageBottom src={ImageSource('pop-up-blur-bg')} />
              <ImageTop src={icon} />
            </ImageBox>
          )}
          {children}
          <Stack
            justifyContent="center"
            alignItems="center"
            width="100%"
            flexDirection="row"
            mt={icon ? '40px' : '16px'}
          >
            {cta}
          </Stack>
        </Stack>
      </InformationBox>
  );
}
