import { Stack } from 'components/styled/layout.styled';
import React, {useState} from 'react';
import { InputCode } from './styles';

const BACKSPACE = 8;
const LEFT_ARROW = 37;
const RIGHT_ARROW = 39;
const DELETE = 46;
const SPACEBAR = 32;

interface InputOtpProps {
  ref: any;
  isDigit?: boolean;
  maxChar?: number;
  fieldCount: number;
  vertical?: boolean;
  getOTPValue?: (value : string) => void;
  placeholder?: string;
  value?: string;
}


function InputOtp({ref, isDigit, fieldCount, maxChar = 1, vertical, placeholder = '0', getOTPValue}: InputOtpProps) {
  const [getInputCode, setInputCode] = useState<JSX.Element[]>([]);
  let [codes] = useState<string[]>([]);
  let [getFocusIndex, setFocusIndex] = useState<number>(1)
  React.useEffect(()=> {
    setInputCode(Array(fieldCount).fill('').map((_, inputIndex) => {
      codes.push('')
      return(
        <InputCode ref={ref} id={'InputOtp'+inputIndex} key={inputIndex} type={isDigit ? 'number' : 'text'} inputMode={isDigit ? 'numeric' : 'text'} maxLength={maxChar} required placeholder={placeholder}
          tabIndex={inputIndex}
          onInput={(e) => {
            e.currentTarget.select()
            getFocusIndex = e.currentTarget.tabIndex
            setFocusIndex(e.currentTarget.tabIndex)
            const digit = isNaN(Number.parseInt(e.currentTarget.value))
            if (isDigit) {
              if (digit) {
                e.currentTarget.value = '';
                e.preventDefault();
              }else{
                nextInputFocus()
              }
            }else{              
              nextInputFocus()
            }
          }}
          onFocus={(e) => {
            e.target.select()
            getFocusIndex = e.currentTarget.tabIndex
            setFocusIndex(e.currentTarget.tabIndex)
          }}
          onChange={(e) => {
            const digit = isNaN(Number.parseInt(e.target.value))
            if (e.target.value.length > e.target.maxLength) {
              if (isDigit && digit) {
                e.preventDefault();
              }
              e.target.value = e.target.value.slice(0, e.target.maxLength)
            }else{
              codes.fill(e.target.value, e.target.tabIndex, e.target.tabIndex+1)
              getFocusIndex = e.target.tabIndex
              setFocusIndex(e.target.tabIndex)
              nextInputFocus();
            }
            if(getOTPValue){
              getOTPValue(codes.join(''))
            }
          }}
          onKeyPress={(e) => {
            getFocusIndex = e.currentTarget.tabIndex
            setFocusIndex(e.currentTarget.tabIndex)
            const digit = Number.parseInt(String.fromCharCode(e.which))
            if (isDigit) {
              if (isNaN(digit)) {
                e.preventDefault();
              }
            }
          }}
          onKeyUp={(e) => {
            const digit = Number.parseInt(String.fromCharCode(e.which))
            if (isDigit) {
              if (isNaN(digit)) {
                e.preventDefault();
              }
            }
          }}></InputCode>
      )
    }))
  }, [fieldCount, getFocusIndex, codes])
  const prevInputFocus = () => {
    setFocusIndex(getFocusIndex--)
    const prevElemenId = getInputCode[getFocusIndex] ? getInputCode[getFocusIndex].props.id : 'InputOtp'+(fieldCount-1)
    document.getElementById(prevElemenId)?.focus()
  }
  const nextInputFocus = () => {
    setFocusIndex(getFocusIndex++)
    const nextElemenId = getInputCode[getFocusIndex] ? getInputCode[getFocusIndex].props.id : 'InputOtp0'
    document.getElementById(nextElemenId)?.focus()
  }
  document.onkeydown = (e)=> {
    if (e.keyCode === BACKSPACE || e.key === 'Backspace') {
      prevInputFocus();      
    } else if (e.keyCode === DELETE || e.key === 'Delete') {
      prevInputFocus();
    } else if (e.keyCode === LEFT_ARROW || e.key === 'ArrowLeft') {
      prevInputFocus();
    } else if (e.keyCode === RIGHT_ARROW || e.key === 'ArrowRight') {
      nextInputFocus();  
    } else if (e.keyCode === SPACEBAR || e.key === ' ' || e.key === 'Spacebar' || e.key === 'Space') {
      
    }
  }

  return (
    <Stack direction={vertical ? 'column' : 'row'} align='evenly'>
      {getInputCode && getInputCode.map((el) => {
        return el
      })}
    </Stack>
  );
}

export default InputOtp;
