import InvitationList from '../state/InvitationList';
import ListOfMyTeam from '../state/ListOfMyTeam';
import { CustomTabs, TabItem } from 'design/Tabs';
import React from 'react';

const MyTeamNav = (data: any) => {
  return (
    <>
      <CustomTabs
        activeTab={data?.selected_tab}
        sx={{ borderBottom: '1px solid #323437', bgcolor: '#1A1817', padding: '0px 0px 0px 15px' }}
      >
        <TabItem hash="#tim" title="Tim Saya">
          <ListOfMyTeam />
        </TabItem>
        <TabItem hash="#undangan" title="Undangan">
          <InvitationList />
        </TabItem>
      </CustomTabs>
    </>
  );
};

export default MyTeamNav;
