import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import ApiService from 'services/ApiService';
import _ from 'lodash';

export interface SeasonResponse {
    name:         string;
    split:        string;
    startAt:      Date;
    endAt:        Date;
}

export interface SeasonState {
    data?: SeasonResponse;
    loading: boolean;
    status: string | null;
    error: string | undefined | null;
}

const initialState: SeasonState = {
    loading: false,
    status: '',
    error: '',
};

export const fetchSeason = createAsyncThunk(
  'season',
  async () => {
    const url = `/setting-api/season/current`;
    const res = await ApiService.get(url);
    return res.data;
  },
);

const SeasonSlice = createSlice({
  name: 'season',
  initialState,
  reducers: {},
  extraReducers: builder => {
    builder.addCase(fetchSeason.fulfilled, (state, action) => {
        state.data = action.payload.data;
        state.loading = false;
      });
      builder.addCase(fetchSeason.pending, state => {
        state.error = ''
        state.loading = true;
      });
      builder.addCase(fetchSeason.rejected, (state, action) => {
        state.error = action?.error?.message ?? action?.error?.stack ?? '';
        state.loading = false;
      });
    },
});

export default SeasonSlice.reducer;
