import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import _ from 'lodash';
import { MatchHistory } from 'redux/teamProfile/slice';
import ApiService from 'services/ApiService';

export interface PublicProfileResponse {
  id: string;
  firstName: string;
  lastName: string;
  picture: string;
  generation: number;
  school: Schools;
  games: Games[];
}

export interface Games {
  nickName: string;
  gameProviderId: string;
  game: Schools;
}

export interface Schools {
  id: string;
  name: string;
  urlSlug: string;
  iconUrl?: string;
  city?: City;
}

export interface City {
  code: string;
  name: string;
  parentCode: string;
}

export interface PublicProfileState {
  data: PublicProfileResponse;
  games: Games[];
  school: Schools;
  city: City;
  tabTitle: string;
  loading: boolean;
  status: string | null;
  error: string | undefined | null;
  items: MatchHistory[] | null;
}

const initialState: PublicProfileState = {
  data: {
    id: '',
    firstName: '',
    lastName: '',
    picture: '',
    generation: 0,
    school: {
      id: '',
      name: '',
      urlSlug: '',
      city: {
        code: '',
        name: '',
        parentCode: '',
      },
    },
    games: [],
  },
  games: [],
  school: {
    id: '',
    name: '',
    urlSlug: '',
    city: {
      code: '',
      name: '',
      parentCode: '',
    },
  },
  city: {
    code: '',
    name: '',
    parentCode: '',
  },
  tabTitle: '',
  loading: false,
  status: '',
  error: '',
  items: [],
};

const parseError = (errors: any) => {
  if (errors) {
    let errorsList: string[] = [];
    Object.values(errors).forEach((prop: any) => {
      errorsList = errorsList.concat(_.map(prop, _.property('message')));
    });
    return errorsList.join('\n');
  }
  return null;
};

export const fetchPublicProfile = createAsyncThunk(
  'public-profile',
  async ({ urlSlug, version }: { urlSlug: string; version?: string }) => {
    const url = `/${version ? `v${version}/` : ''}user/player/public-profile/${urlSlug}`;
    const res = await ApiService.get<PublicProfileResponse>(url);
    return res.data;
  },
);

export const fetchProfileHistory = createAsyncThunk(
  'public-profile/history',
  async ({
    playerSlug,
    pageIndex,
    pageSize,
    version,
  }: {
    playerSlug: string;
    pageIndex: number;
    pageSize: number;
    version: string;
  }) => {
    try {
      const url = `/match-api/${
        version ? `v${version}/` : ''
      }history/player/${playerSlug}/${pageIndex}/${pageSize}`;
      const res = await ApiService.get(url);
      return res.data;
    } catch (error: any) {
      if (error.response) {
        return { isAxiosError: true, data: error.response.data };
      } else {
        return { isAxiosError: true, data: error };
      }
    }
  },
);

