//   if (errors) {
//     let errorsList: string[] = [];
//     Object.values(errors).forEach((prop: any) => {
//       errorsList = errorsList.concat(_.map(prop, _.property('message')));
//     });
//     return errorsList.join('\n');
//   }
//   return null;
// };

const PublicProfileSlice = createSlice({
  name: 'publicProfile',
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder
      .addCase(fetchPublicProfile.pending, (state, _action) => {
        state.status = 'loading';
      })
      .addCase(fetchPublicProfile.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.data = action.payload;
      })
      .addCase(fetchProfileHistory.pending, (state, _action) => {
        state.status = 'loading';
        state.items = [];
      })
      .addCase(fetchProfileHistory.fulfilled, (state, action) => {
        if (action.payload.isAxiosError || _.get(action.payload, 'data.errors') != null) {
          state.status = 'error';
          state.error =
            typeof action.payload.data === 'string'
              ? action.payload.data
              : parseError(action.payload.data.errors);
        } else {
          state.status = 'succeeded';
          state.items = [];
          (action.payload.items as MatchHistory[])?.forEach((item: MatchHistory) => {
            (state.items as MatchHistory[]).push(item);
          });
        }
      })
      .addCase(fetchProfileHistory.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.error.message;
        state.items = [];
      });
  },
});

// export const { historyLoaded } = PublicProfileSlice.actions;
export default PublicProfileSlice.reducer;
