import { Box as MUIBox, Stack as MUIStack } from '@mui/material';
import GradientButton from 'components/Buttons/GradientButton';
import MatchItem from 'components/Lists/MatchItem';
import MatchResultItem from 'components/Lists/MatchResultItem';
import MatchLayout from 'components/MatchLayout';
import Modal from 'components/layouts/Modal';
import { Circle, LayoutAnimation } from 'components/styled/animation.styled';
import { Avatar as AvatarStyle } from 'components/styled/avatar.styled';
import { Box, Stack } from 'components/styled/layout.styled';
import { GradientText, NormalText, SpanText } from 'components/styled/text.styled';
import { TeamHistory } from 'design/Cards/TeamHistory';
import { Text } from 'design/Text';
import _ from 'lodash';
import { IconSource } from 'modules/assetpath';
import {
  dateFormat,
  LogDev,
  parseTimeLeft, //delayExecute
} from 'modules/helpers';
import history from 'modules/history';
import React, { useEffect, useState } from 'react';
import Countdown from 'react-countdown';
import { useTranslation } from 'react-i18next';
import SVG from 'react-inlinesvg';
import { useAppSelector } from 'redux/hooks';
import { useAppThunkDispatch } from 'redux/store';
import { fetchTeamHistory, rejectInvitation, checkIn, MatchHistory } from 'redux/teamProfile/slice';
import AuthService from 'services/AuthService';
import {
  FrameScrimList,
  FrameAtasList,
  FrameIdScrim,
} from 'styles/TeamProfile/PagePertandingan/Frame';
import {
  TextIdScrim,
  TextTanggalScrim,
  TextDescripScrim,
  TextPenantang,
} from 'styles/TeamProfile/PagePertandingan/Text';
import { getPlayerProfileFromSession } from 'redux/account/AccountServices';

interface PertandinganProps {
  gameSlug: string;
  teamSlug: string;
  setPageTitle?: any;
}

const Pertandingan = ({ gameSlug, teamSlug, setPageTitle }: PertandinganProps) => {
  const { t } = useTranslation();
  const dispatch = useAppThunkDispatch();
  const auth = new AuthService();
  const isAuthenticated = auth.isAuthenticated();
  const [showModal, setShowModal] = useState(false);
  const [showErrorModal, setShowErrorModal] = useState(false);
  const [showCheckInModal, setShowCheckInModal] = useState(false);
  const [selectedMatch, setSelectedMatch] = useState<MatchHistory>();
  // const [checkinTimeEnd, setCheckinTimeEnd] = useState(false);
  const myProfileId = getPlayerProfileFromSession()?.id;

  const { playerInfo, teamHistory, teamHistoryStatus, teamHistoryError, teamDetail } =
    useAppSelector(({ account, teamProfile }) => ({
      playerInfo: account?.data,
      teamHistory: teamProfile?.teamHistory,
      teamHistoryStatus: teamProfile?.status?.teamHistory,
      teamHistoryError: teamProfile?.error?.teamHistory,
      teamDetail: teamProfile?.teamDetail,
    }));

  const myRole = isAuthenticated
    ? playerInfo?.games?.find(game => game?.team?.urlSlug === teamSlug)
    : undefined;
  const isCaptain = myRole?.team?.level?.value === 'Captain';

  const groupTournamentMatch = _.groupBy(teamHistory?.items ?? [], val => val.eventName);
  const scrimList = teamHistory?.items?.filter(val => val?.kind?.value == 'Scrim');

  const handleFetchTeamHistory = (
    teamSlug: string,
    pageIndex: number = 1,
    pageSize: number = 100,
  ) => {
    dispatch(
      fetchTeamHistory({
        teamSlug: teamSlug,
        pageIndex: pageIndex,
        pageSize: pageSize,
        version: '',
      }),
    );
  };

  useEffect(() => {
    handleFetchTeamHistory(teamSlug);
  }, []);

  useEffect(() => {
    if (teamHistoryError) {
      return setShowErrorModal(true);
    }
  }, [teamHistoryError]);

  const reject = async (matchId: string) => {
    setShowModal(false); // close previous modal;
    const thunk = rejectInvitation({ matchId });
    dispatch(thunk)
      .unwrap()
      .then((result: any) => {
        if (result && result.data.status >= 400) {
          setShowErrorModal(true);
        }
        handleFetchTeamHistory(teamSlug);
      })
      .catch(rejectedValueOrSerializedError => {
        LogDev('INFO', 'rejectedValueOrSerializedError', rejectedValueOrSerializedError);
      });
  };

  const checkInMatch = async (matchId: string) => {
    setShowCheckInModal(false); // close previous modal;
    const thunk = checkIn({ matchId });
    dispatch(thunk)
      .unwrap()
      .then((result: any) => {
        if (result && result.data.status >= 400) {
          setShowErrorModal(true);
          return;
        }
        history.push(`/scrims/${gameSlug}/${matchId}`);
      })
      .catch(rejectedValueOrSerializedError => {
        LogDev('INFO', 'rejectedValueOrSerializedError', rejectedValueOrSerializedError);
      });
  };

  const modalConfirmation = () => {
    return (
      <Box padding="24px 0px 24px 0px">
        <Stack direction="column" align="center">
          <NormalText
            textAlign="center"
            color="#FFFFFF"
            fontSize="16px"
            fontWeight="700"
            margin="16px 52px 0px 52px"
          >
            {t('player_scrim_match_scrim_accept_message')}
          </NormalText>
          <Box
            background="linear-gradient(295.84deg, rgba(213, 31, 64, 0.2) 22.62%, rgba(243, 112, 36, 0.2) 74.92%)"
            margin="16px 0px 0px 0px"
            style={{
              maxWidth: '500px',
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              flex: 'none',
              padding: '0px',
              width: '100%',
              height: 'auto',
            }}
          >
            <Stack direction="column" align="start" padding="16px">
              <NormalText fontSize="16px" fontWeight="700" color="white" margin="0px 0px 0px 16px">
                {dateFormat(selectedMatch?.startAt, 'Asia/Jakarta', 'id').format(
                  'dddd, DD MMM YYYY - HH:mm',
                )}{' '}
                WIB
              </NormalText>
              <Stack direction="row" align="start">
                <AvatarStyle
                  src={selectedMatch?.firstTeamAvatar}
                  height="24px"
                  borderRadius="50%"
                  margin="16px 10px 0px 16px"
                />
                <NormalText
                  fontSize="14px"
                  fontWeight="500"
                  color="white"
                  margin="20px 0px 0px 0px"
                >
                  {t('player_scrim_match_scrim_accept_team_name', {
                    team_name: selectedMatch?.firstTeam,
                  })}
                </NormalText>
              </Stack>
            </Stack>
          </Box>

          <Box margin="16px 0px 0px 0px">
            <Stack direction="column" align="center">
              <GradientButton
                variant="primary"
                onClick={() => {
                  setShowModal(false); // close previous modal;
                  history.push(
                    `/team-match/choose-player/${teamSlug}/${
                      selectedMatch?.id || ''
                    }/?state=direct&game=${gameSlug}`,
                  );
                }}
                styled={{ backgroundSize: '100%' }}
              >
                {t('player_scrim_match_scrim_accept_accept_scrim')}
              </GradientButton>
              <GradientButton variant="cta" onClick={() => reject(selectedMatch?.id || '')}>
                {t('player_scrim_match_scrim_accept_reject_scrim')}
              </GradientButton>
            </Stack>
          </Box>
        </Stack>
      </Box>
    );
  };

  const modalCheckIn = (
    <Box padding="8px" width="100%">
      <Stack direction="column" align="center">
        <NormalText
          textAlign="center"
          color="#FFFFFF"
          fontSize="16px"
          fontWeight="700"
          margin="8px 0px"
        >
          {t('player_scrim_match_popup_checkin_title')}
        </NormalText>
        <NormalText
          textAlign="center"
          color="rgba(221, 216, 216, 1)"
          fontSize="14px"
          fontWeight="400"
          margin="8px 0px"
          lineHeight="140%"
        >
          {t('player_scrim_match_popup_checkin_message_time', {
            timeLeft: (
              <span style={{ fontSize: '14px', fontWeight: 700, color: '#FFFFFF' }}>30:00</span>
            ),
          })}
          <br /> {t('player_scrim_match_popup_checkin_message')}
        </NormalText>
        <Box width="100%" margin="16px 0px 0px 0px">
          <Stack direction="column" align="center">
            <GradientButton
              styled={{ backgroundSize: '100%' }}
              variant="primary"
              onClick={() => checkInMatch(selectedMatch?.id || '')}
            >
              {t('player_scrim_match_popup_checkin_button_confirm')}
            </GradientButton>

            <GradientButton variant="cta" onClick={() => setShowCheckInModal(false)}>
              {t('player_scrim_match_popup_checkin_button_cancel')}
            </GradientButton>
          </Stack>
        </Box>
      </Stack>
    </Box>
  );

  const modalError = () => {
    return (
      <Box padding="24px 0px 24px 0px">
        <Stack direction="column" margin="16px">
          <NormalText
            textAlign="center"
            color="#FFFFFF"
            fontSize="16px"
            fontWeight="bold"
            margin="8px 0px"
          >
            Terjadi Kesalahan!
          </NormalText>
          <NormalText
            textAlign="center"
            color="red"
            fontSize="14px"
            fontWeight="normal"
            margin="8px 0px"
          >
            {teamHistoryError}
          </NormalText>
          <br />
          <Box padding="8px" width="100%">
            <Stack direction="column" align="center">
              <GradientButton
                onClick={() => setShowErrorModal(false)}
                styled={{
                  backgroundSize: '100%',
                  width: '180px',
                }}
                variant="primary"
              >
                {t('player_scrim_match_schedule_not_available_ok')}
              </GradientButton>
            </Stack>
          </Box>
        </Stack>
      </Box>
    );
  };

  const MatchEmptyPage = () => {
    return (
      <Stack
        align="center"
        position="relative"
        direction="column"
        margin="68px 0px 0px"
        disableDrag
        disableSelectText
      >
        <img
          style={{ backgroundRepeat: 'no-repeat' }}
          src={IconSource('mabar-typeface-fullwhite')}
          alt="mabar-typeface-fullwhite"
        />
        <NormalText
          color="#969393"
          textAlign="center"
          fontStyle="normal"
          fontWeight="400"
          fontSize="14px"
          margin="24px 0px"
        >
          {t('player_account_match_tournament_empty')}
        </NormalText>
        <GradientButton
          onClick={() => history.push('/explore/tournament')}
          variant="primary"
          padding="12.5px 35px 12.5px 35px"
          backgroundSize="100%"
          margin="43px 0px 0px"
        >
          {t('player_team_profile_button_search_match')}
        </GradientButton>
      </Stack>
    );
  };

  useEffect(() => {
    setPageTitle(
      t('mabar_page_title', {
        title: 'My Team > Match',
      }),
    );
  }, [setPageTitle]);

  const handleLoader = () => {
    if (teamHistoryStatus !== 'succeeded') {
      if ((teamHistory?.items ?? []).length > 0) return 'none';
      return '';
    }
    return 'none';
  };

  const handleContent = () => {
    if (teamHistoryStatus !== 'succeeded') {
      if ((teamHistory?.items ?? []).length > 0) return '';
      return 'none';
    }
    return '';
  };

  // conflict with loadmore
  // const delayArray: any = [5000, 10000, 15000, 20000, 25000, 30000, 35000];

  // useEffect(() => {
  //   if (checkinTimeEnd) {
  //     delayArray.map((time: number) => {
  //       delayExecute(time).promise.then((_msg: any) => {
  //         handleFetchTeamHistory(teamSlug, 1, (pageIndex > 1 ? pageIndex - 1 : pageIndex) * 10);
  //       });
  //     });
  //   }
  // }, [checkinTimeEnd]);

  const isPlayer = (players?: any) => {
    return players?.indexOf(myProfileId) > -1;
  };

  return (
    <Stack direction="column">
      <MUIStack
        justifyContent="center"
        alignItems="center"
        sx={{ mt: '80px', display: handleLoader() }}
      >
        <SVG src={IconSource('ic-loader')} />
      </MUIStack>

      <MUIBox sx={{ display: handleContent() }}>
        {teamHistory && Object.keys(teamHistory).length > 0 ? (
          <>
            {teamHistory && teamHistory?.items?.filter(val => val.eventName).length > 0 && (
              <>
                <Text
                  variant="labelSmall"
                  sx={{
                    fontSize: '14px',
                    letterSpacing: '0px',
                    lineHeight: '20px',
                    fontWeight: '600',
                    font: 'Lexend Deca',
                  }}
                >
                  Match
                </Text>
                <MUIBox
                  sx={{
                    backfaceVisibility: 'hidden',
                    transformStyle: 'preserve-3d',
                    transform: 'translate3d(0,0,0)',
                    mb: '23px',
                  }}
                >
                  {Object.keys(groupTournamentMatch).map(function (template_name) {
                    return (
                      <>
                        {template_name != 'undefined' && (
                          <MUIBox key={template_name} sx={{ pt: '16px' }}>
                            <MUIBox sx={{ mb: '10px' }}>
                              <Text
                                variant="labelSmall"
                                sx={{
                                  fontSize: '14px',
                                  lineHeight: '20px',
                                  letterSpacing: '0px',
                                  fontWeight: '600',
                                  font: 'Lexend Deca',
                                  mb: '10px',
                                }}
                              >
                                {template_name}
                              </Text>
                            </MUIBox>

                            {groupTournamentMatch[template_name].map(function (data) {
                              // var startTime = dateFormat(data.startAt, 'Asia/Jakarta', 'id').format(
                              //   'DD MMM yyyy, HH:mm z',
                              // );
                              // var endTime = dateFormat(data.endAt, 'Asia/Jakarta', 'id').format(
                              //   'DD MMM yyyy, HH:mm z',
                              // );
                              if (data.status.value == 'Scheduled') {
                                var status = data.status.value;
                                var statusLabel = 'Check In belum dibuka';
                              } else if (data.status.value == 'CheckIn') {
                                var status = data.status.value;
                                var statusLabel = '';
                              } else if (data.status.value == 'Dispute') {
                                var status = 'Inprogress';
                                var statusLabel = 'Dalam Validasi';
                              } else if (
                                data.status.value == 'Compete' ||
                                data.status.value == 'ResultUpload'
                              ) {
                                var status = 'Inprogress';
                                var statusLabel = 'Sedang Berlangsung';
                              } else if (data.status.value == 'Expired') {
                                var status = data.status.value;
                                var statusLabel = 'Data ini sudah Expired';
                              } else if (data.status.value == 'Reject') {
                                var status = data.status.value;
                                var statusLabel = 'Data ini sudah di Reject';
                              } else if (data.status.value == 'NotValid') {
                                var status = data.status.value;
                                var statusLabel = 'Tidak Valid';
                              } else if (
                                data?.status?.value == 'DataCompleted' &&
                                data?.resultType?.value == 'Walkover'
                              ) {
                                var status = 'Inprogress';
                                var statusLabel = 'Walk-out';
                              } else if (data?.secondTeam == undefined || !data.secondTeam) {
                                var status = 'WinBye';
                                var statusLabel = 'Win - BYE';
                              } else {
                                var status = data.status.value;
                                var statusLabel = 'None';
                              }
                              return (
                                <TeamHistory
                                  id={data?.id}
                                  winnerTeamId={data?.winnerTeamId}
                                  firstTeamId={data?.firstTeamId}
                                  secondTeamId={data?.secondTeamId}
                                  propTeamId={teamDetail?.id}
                                  firstTeam={data?.firstTeam}
                                  firstTeamAvatar={data?.firstTeamAvatar}
                                  firstTeamSchoolName={data?.firstTeamSchoolName}
                                  firstTeamScore={data?.firstTeamScore}
                                  secondTeam={data?.secondTeam}
                                  secondTeamAvatar={data?.secondTeamAvatar}
                                  secondTeamSchoolName={data?.secondTeamSchoolName}
                                  secondTeamScore={data?.secondTeamScore}
                                  status={status}
                                  statusLabel={statusLabel}
                                  checkInStartAt={data?.checkInStartAt}
                                  checkInEndAt={data?.checkInEndAt}
                                  gameSlug={gameSlug}
                                  eventSlug={data?.eventSlug}
                                  startAt={data?.startAt}
                                  myProfileId={playerInfo?.id}
                                  players={data?.players}
                                />
                              );
                            })}
                          </MUIBox>
                        )}
                      </>
                    );
                  })}
                </MUIBox>
              </>
            )}

            <MUIBox
              sx={{
                mt: '10px',
                mb: '10px',
                backfaceVisibility: 'hidden',
                transformStyle: 'preserve-3d',
                transform: 'translate3d(0,0,0)',
              }}
            >
              <MatchLayout showModal={showModal} modalContent={modalConfirmation()}>
                <Modal show={showErrorModal}>{modalError()}</Modal>
                <Modal show={showCheckInModal}>{modalCheckIn}</Modal>

                {scrimList && scrimList.length > 0 && (
                  <Text
                    variant="labelSmall"
                    sx={{
                      fontSize: '14px',
                      letterSpacing: '0px',
                      lineHeight: '20px',
                      fontWeight: '600',
                      font: 'Lexend Deca',
                    }}
                  >
                    Scrim
                  </Text>
                )}

                {(teamHistory?.items ?? []).length > 0 &&
                  teamHistory?.items.map(data => {
                    if (data?.kind?.value == 'Scrim') {
                      if (data?.status?.value === 'Invitation') {
                        if (data?.firstTeamUrlSlug !== teamSlug) {
                          return (
                            <FrameScrimList key={data.id}>
                              <FrameAtasList />
                              <header style={{ top: '0%' }}>
                                <FrameIdScrim>
                                  <TextIdScrim>
                                    {t('player_team_profile_scrim_no', { scrimId: data.id })}
                                  </TextIdScrim>
                                </FrameIdScrim>
                                <TextTanggalScrim>
                                  {dateFormat(data.startAt, 'Asia/Jakarta', 'id').format(
                                    'DD MMM yyyy, HH:mm',
                                  )}{' '}
                                  WIB
                                </TextTanggalScrim>
                              </header>
                              <TextDescripScrim>
                                {t('player_team_profile_scrim_message')}
                              </TextDescripScrim>
                              <img
                                src={data.firstTeamAvatar}
                                style={{
                                  position: 'absolute',
                                  top: '0%',
                                  bottom: '0%',
                                  border: '2px solid #F1EBEB',
                                  boxSizing: 'border-box',
                                  borderRadius: '12px',
                                  margin: '117px 20px',
                                  width: '25px',
                                  height: '25px',
                                }}
                                alt=""
                              />
                              <TextPenantang>
                                {t('player_scrim_invitation_opponent', {
                                  team_name: data.firstTeam,
                                })}
                              </TextPenantang>

                              <footer
                                style={{
                                  position: 'relative',
                                  marginLeft: isCaptain ? 'auto' : '0',
                                  marginRight: isCaptain ? 'auto' : '0',
                                }}
                              >
                                {isCaptain ? (
                                  <>
                                    <GradientButton
                                      onClick={() => {
                                        setSelectedMatch(data);
                                        setShowModal(true);
                                      }}
                                      variant="primary"
                                    >
                                      <span>{t('player_scrim_invitation_accept')}</span>
                                    </GradientButton>
                                    <GradientButton
                                      onClick={() => history.push(`/scrim/${gameSlug}/${data.id}`)}
                                      variant="secondary"
                                    >
                                      <span style={{ padding: '10px' }}>
                                        {t('player_scrim_invitation_see_detail')}
                                      </span>
                                    </GradientButton>
                                  </>
                                ) : (
                                  <SpanText
                                    color="#F37024"
                                    fontSize="12px"
                                    fontWeight="400"
                                    lineHeight="16.8px"
                                    fontStyle="normal"
                                    textAlign="center"
                                    wordWrap={'break-word'}
                                    margin="0px 0px 0px 16px"
                                  >
                                    {t('player_team_profile_scrim_approval_captain')}
                                  </SpanText>
                                )}
                              </footer>
                            </FrameScrimList>
                          );
                        }
                        return (
                          <MatchResultItem
                            key={data.id}
                            variant="Invitation"
                            onClick={() => history.push(`/scrims/${gameSlug}/${data.id}`)}
                            headerTitle={
                              <b>{t('player_team_profile_scrim_waiting_confirmation')}</b>
                            }
                            data={{
                              firstTeam: data.firstTeam,
                              firstTeamAvatar: data.firstTeamAvatar,
                              matchDate: data.startAt,
                              secondTeam: data.secondTeam,
                              secondTeamAvatar: data.secondTeamAvatar,
                              matchType: ` - ${data.type.value
                                .replace('BO3', t('player_explore_scrim_match_mode'))
                                .replace('BO5', t('player_explore_scrim_match_mode_bo5'))}`,
                            }}
                          />
                        );
                      }
                      if (data.status.value === 'Scheduled') {
                        return (
                          <MatchItem
                            key={data.id}
                            matchId={t('player_team_profile_scrim_no', { scrimId: data.id })}
                            data={data}
                            matchDate={dateFormat(data.startAt, 'Asia/Jakarta', 'id').format(
                              'DD MMM yyyy, HH:mm z',
                            )}
                            onClick={(_e, m) => {
                              history.push(`/scrims/${gameSlug}/${m?.id ?? data.id}`);
                            }}
                            noEnemyText={'Menuggu Lawan'}
                            boxRight={
                              <Stack direction="column" align="center">
                                <SpanText
                                  color="white"
                                  fontSize="16px"
                                  fontWeight="900"
                                  lineHeight="22.4px"
                                  fontStyle="normal"
                                  textAlign="center"
                                  wordWrap={'break-word'}
                                >
                                  {dateFormat(data.startAt, 'Asia/Jakarta', 'id').format('HH:mm')}{' '}
                                  WIB
                                </SpanText>
                                <Countdown
                                  date={data.startAt}
                                  autoStart
                                  intervalDelay={0}
                                  renderer={({
                                    formatted,
                                    seconds,
                                    minutes,
                                    hours,
                                    days,
                                    completed,
                                  }) => {
                                    if (completed) {
                                      // setCheckinTimeEnd(true);

                                      return false;
                                    } else {
                                      return (
                                        <SpanText
                                          color="#DDD8D8"
                                          fontSize="12px"
                                          fontWeight="400"
                                          textAlign="center"
                                        >
                                          {days >= 1
                                            ? `${days >= 10 ? formatted.days : days} hari ${
                                                formatted.hours
                                              } jam`
                                            : hours !== 0
                                            ? `${hours >= 10 ? formatted.hours : hours} jam ${
                                                formatted.minutes
                                              } menit`
                                            : minutes !== 0
                                            ? `${
                                                minutes >= 10 ? formatted.minutes : minutes
                                              } menit ${formatted.seconds} detik`
                                            : `${
                                                seconds >= 10 ? formatted.seconds : seconds
                                              } detik`}
                                        </SpanText>
                                      );
                                    }
                                  }}
                                />
                              </Stack>
                            }
                          />
                        );
                      }
                      if (data.status.value === 'CheckIn') {
                        if (isPlayer(data?.players)) {
                          return (
                            <MatchItem
                              key={data.id}
                              matchId={t('player_team_profile_scrim_no', { scrimId: data.id })}
                              data={data}
                              matchDate={dateFormat(data.startAt, 'Asia/Jakarta', 'id').format(
                                'DD MMM yyyy, HH:mm z',
                              )}
                              onClick={(_e, m) => {
                                history.push(`/scrims/${gameSlug}/${m?.id ?? data.id}`);
                              }}
                              noEnemyText={t('player_scrim_status_message_expired_opponent')}
                              boxRight={
                                <Stack direction="column" align="center">
                                  <Stack direction="row">
                                    <GradientText
                                      fontSize="16px"
                                      fontWeight="900"
                                      lineHeight="22.4px"
                                      fontStyle="normal"
                                      textAlign="right"
                                      wordWrap={'break-word'}
                                    >
                                      Check In
                                    </GradientText>
                                    <LayoutAnimation
                                      customAnimation={{
                                        duration: '2.5s',
                                        iterationCount: 'infinite',
                                        timingFunction: 'ease',
                                      }}
                                      margin="-2.5px 0px 0px 0px"
                                    >
                                      🔥
                                    </LayoutAnimation>
                                  </Stack>
                                  <Countdown
                                    date={data.checkInStartAt}
                                    autoStart
                                    intervalDelay={0}
                                    renderer={({ minutes }) => (
                                      <SpanText
                                        color="#DDD8D8"
                                        fontSize="12px"
                                        fontWeight="400"
                                        textAlign="center"
                                      >
                                        {minutes > 0 &&
                                          parseTimeLeft({
                                            dateTime: data.startAt,
                                            subtract: 10,
                                            locale: 'id',
                                          })}
                                      </SpanText>
                                    )}
                                  />
                                </Stack>
                              }
                            />
                          );
                        } else {
                          return (
                            <MatchItem
                              key={data.id}
                              matchId={t('player_team_profile_scrim_no', { scrimId: data.id })}
                              data={data}
                              matchDate={dateFormat(data.startAt, 'Asia/Jakarta', 'id').format(
                                'DD MMM yyyy, HH:mm z',
                              )}
                              onClick={(_e) => {
                                history.push(`#`);
                              }}
                              noEnemyText={t('player_scrim_status_message_expired_opponent')}
                              isPlayer={isPlayer(data?.players)}
                              boxRight={
                                    <GradientText
                                      color={'#969393'}
                                      fontSize="16px"
                                      fontWeight="900"
                                      lineHeight="22.4px"
                                      fontStyle="normal"
                                      textAlign="right"
                                      wordWrap={'break-word'}
                                    >
                                      {t('player_scrim_status_message_not_player')}
                                    </GradientText>
                              }
                            />
                          );
                        }
                      }
                      if (data.status.value === 'Compete') {
                        return (
                          <MatchResultItem
                            key={data.id}
                            variant="COMPETE"
                            onClick={() => history.push(`/scrims/${gameSlug}/${data.id}`)}
                            headerTitle={
                              <Stack direction="row" align="center">
                                <Circle
                                  background="#26BD87"
                                  borderRadius="50%"
                                  width="5px"
                                  height="5px"
                                  margin="0px 5px 0px 0px"
                                />
                                <b>{t('player_scrim_status_message_compete')}</b>
                              </Stack>
                            }
                            data={{
                              firstTeam: data.firstTeam,
                              firstTeamAvatar: data.firstTeamAvatar,
                              matchDate: data.startAt,
                              secondTeam: data.secondTeam,
                              secondTeamAvatar: data.secondTeamAvatar,
                            }}
                          />
                        );
                      }
                      if (data.status.value === 'Open' && data.firstTeamUrlSlug === teamSlug) {
                        return (
                          <MatchResultItem
                            key={data.id}
                            onClick={() => history.push(`/scrim/${gameSlug}/${data.id}`)}
                            variant="Invitation"
                            headerTitle={<b>{t('player_team_profile_scrim_waiting_opponent')}</b>}
                            data={{
                              firstTeam: data.firstTeam,
                              firstTeamAvatar: data.firstTeamAvatar,
                              matchDate: data.startAt,
                              secondTeam: data.secondTeam,
                              secondTeamAvatar: data.secondTeamAvatar,
                            }}
                          />
                        );
                      }
                      if (data.status.value === 'Dispute') {
                        return (
                          <MatchResultItem
                            onClick={() => history.push(`/scrims/${gameSlug}/${data.id}`)}
                            key={data.id}
                            variant="COMPETE"
                            headerTitle={<b>Menunggu verifikasi pemenang</b>}
                            data={{
                              firstTeam: data.firstTeam,
                              firstTeamAvatar: data.firstTeamAvatar,
                              firstTeamScore: data.firstTeamScore,
                              matchDate: data.startAt,
                              secondTeam: data.secondTeam,
                              secondTeamAvatar: data.secondTeamAvatar,
                              secondTeamScore: data.secondTeamScore,
                              matchType: ` - ${data.type.value
                                .replace('BO3', t('player_explore_scrim_match_mode'))
                                .replace('BO5', t('player_explore_scrim_match_mode_bo5'))}`,
                            }}
                          />
                        );
                      }
                      if (data.status.value === 'ResultUpload') {
                        return (
                          <MatchResultItem
                            onClick={() => history.push(`/scrims/${gameSlug}/${data.id}`)}
                            key={data.id}
                            variant="COMPETE"
                            headerTitle={
                              <Stack direction="row" align="center">
                                <b>{t('player_scrim_status_message_result_upload')}</b>
                              </Stack>
                            }
                            data={{
                              firstTeam: data.firstTeam,
                              firstTeamAvatar: data.firstTeamAvatar,
                              matchDate: data.startAt,
                              secondTeam: data.secondTeam,
                              secondTeamAvatar: data.secondTeamAvatar,
                            }}
                          />
                        );
                      }
                      if (
                        data.status.value === 'DataCompleted' ||
                        data.status.value === 'ResultCalculated'
                      ) {
                        const titleWin = (
                          <Stack align="center" direction="row">
                            &nbsp;
                            <b>
                              {t('player_scrim_status_message_result_lose', {
                                scrimMatchNo: data.id,
                              })}
                            </b>
                            &nbsp;🏆
                          </Stack>
                        );
                        const titleLose = (
                          <b>
                            {t('player_scrim_status_message_result_win', {
                              scrimMatchNo: data.id,
                            })}
                          </b>
                        );
                        const isWalkOver =
                          data.resultType &&
                          (data.resultType.key === '2' || data.resultType.value === 'Walkover');
                        const MyTeamWinOrLose = data.winnerTeamId
                          ? data.winnerTeamId === teamDetail?.id
                          : false;
                        const matchTitleResult = MyTeamWinOrLose ? titleWin : titleLose;
                        const matchVariantResult = MyTeamWinOrLose ? 'WIN' : 'LOSE';
                        return (
                          <MatchResultItem
                            onClick={() => history.push(`/scrims/${gameSlug}/${data.id}`)}
                            key={data.id}
                            variant={matchVariantResult}
                            headerTitle={matchTitleResult}
                            data={{
                              firstTeam: data.firstTeam,
                              firstTeamAvatar: data.firstTeamAvatar,
                              firstTeamScore:
                                !(data.winnerTeamId === data.firstTeamId) && isWalkOver
                                  ? 'WO'
                                  : data.firstTeamScore,
                              firstTeamWin: data.winnerTeamId
                                ? data.winnerTeamId === data.firstTeamId
                                : true,
                              matchDate: data.startAt,
                              secondTeam: data.secondTeam,
                              secondTeamAvatar: data.secondTeamAvatar,
                              secondTeamScore:
                                !(data.winnerTeamId === data.secondTeamId) && isWalkOver
                                  ? 'WO'
                                  : data.secondTeamScore,
                              secondTeamWin: data.winnerTeamId === data.secondTeamId,
                              matchType: data.type.value
                                ? ` - ${data.type.value
                                    .replace('BO3', 'Best of 3')
                                    .replace('BO5', 'Best of 5')}`
                                : undefined,
                            }}
                          />
                        );
                      }
                      if (data.status.value === 'Reject') {
                        return (
                          <MatchItem
                            key={data.id}
                            matchId={t('player_team_profile_scrim_no', { scrimId: data.id })}
                            matchDate={dateFormat(data.startAt, 'Asia/Jakarta', 'id').format(
                              'DD MMM yyyy, HH:mm z',
                            )}
                            data={{ ...data, secondTeam: undefined }}
                            noEnemyText={
                              <>
                                <b>{data.secondTeam}</b>&nbsp;
                                {t('player_scrim_detail_public_rejected_message')}
                              </>
                            }
                            onClick={(_e, m) => {
                              history.push(`/scrims/${gameSlug}/${m?.id ?? data.id}`);
                            }}
                            boxRight={
                              <Stack direction="column" align="center">
                                <SpanText
                                  color="#969393"
                                  fontSize="16px"
                                  fontWeight="900"
                                  textAlign="center"
                                  lineHeight="22.4px"
                                >
                                  Ditolak
                                </SpanText>
                              </Stack>
                            }
                          />
                        );
                      }
                      if (data.status.value === 'Expired') {
                        return (
                          <MatchItem
                            key={data.id}
                            matchId={t('player_team_profile_scrim_no', { scrimId: data.id })}
                            data={data}
                            matchDate={dateFormat(data.startAt, 'Asia/Jakarta', 'id').format(
                              'DD MMM yyyy, HH:mm z',
                            )}
                            noEnemyText={t('player_scrim_status_message_expired_opponent')}
                            boxRight={
                              <Stack direction="column" align="center">
                                <SpanText
                                  color="#969393"
                                  fontSize="16px"
                                  fontWeight="900"
                                  textAlign="center"
                                  lineHeight="22.4px"
                                >
                                  {t('player_scrim_status_message_expired')}
                                </SpanText>
                              </Stack>
                            }
                          />
                        );
                      }
                      if (data.status.value === 'NotValid') {
                        return (
                          <MatchResultItem
                            onClick={() => history.push(`/scrims/${gameSlug}/${data.id}`)}
                            key={data.id}
                            variant="NotValid"
                            headerTitle={<b>{t('player_scrim_match_dispute_message')}</b>}
                            data={{
                              firstTeam: data.firstTeam,
                              firstTeamAvatar: data.firstTeamAvatar,
                              matchDate: data.startAt,
                              secondTeam: data.secondTeam,
                              secondTeamAvatar: data.secondTeamAvatar,
                            }}
                          />
                        );
                      }
                      return null;
                    } else {
                      return null;
                    }
                  })}
              </MatchLayout>
            </MUIBox>
          </>
        ) : (
          <MatchEmptyPage />
        )}
      </MUIBox>
    </Stack>
  );
};
export default Pertandingan;
