import Member from '../state/Member';
import Pertandingan from '../state/Pertandingan';
import ProfileTournamentList from '../state/TournamentList';
import { Box } from '@mui/material';
import { Badge } from '@mui/material';
import { CustomTabs, TabItem } from 'design/Tabs';
import { IconSource } from 'modules/assetpath';
import React, { useState } from 'react';
import { use100vh } from 'react-div-100vh';
import { useAppSelector } from 'redux/hooks';

interface TeamProfileNavProps {
  gameSlug: string;
  teamSlug: string;
  setShowModalLoading: any;
  setPageTitle: any;
  hash: string;
  setOpenToast: (value: boolean) => void;
  setToastMessages: (value: string) => void;
  setOpenErrorToast: (value: boolean) => void;
  setToastErrorMessages: (value: string) => void;
}

export const TeamProfileNav = ({
  gameSlug,
  teamSlug,
  setShowModalLoading,
  setPageTitle,
  hash,
  setOpenToast,
  setToastMessages,
  setOpenErrorToast,
  setToastErrorMessages,
}: TeamProfileNavProps) => {
  const height = use100vh();
  const [activeTab, setActiveTab] = useState('');

  const { tournamentListMyTeam } = useAppSelector(({ tournament }) => ({
    tournamentListMyTeam: tournament?.tournamentListMyTeam,
  }));

  const handleActiveTab = (value: string) => {
    setActiveTab(value);
  };

  const notifInvitation =
    tournamentListMyTeam?.items?.filter(item => item.playerState === '0') ?? [];

  return (
    <CustomTabs
      tabIndex={value => handleActiveTab(value)}
      sx={{
        borderBottom: '1px solid #323437',
        bgcolor: 'neutral700',
        '.MuiTabs-indicator': { height: '2px' },
        '.MuiTabs-flexContainer': {
          a: { display: 'flex' },
        },
      }}
      activeTab={hash}
      tabSx={{ flexGrow: 1 }}
    >
      <TabItem
        hash="#match"
        title={
          <img
            src={IconSource(activeTab === '#match' ? 'ic-tp-duel-active' : 'ic-tp-duel')}
            alt="match"
          />
        }
        sx={{
          minHeight: height! - (56 + 180 + 53),
          height: !height ? 'calc(100vh - (56px + 180px + 53px))' : 'auto',
          bgcolor: 'neutral900',
        }}
      >
        <Box sx={{ p: '16px' }}>
          <Pertandingan gameSlug={gameSlug} teamSlug={teamSlug} setPageTitle={setPageTitle} />
        </Box>
      </TabItem>

      <TabItem
        hash="#tournament"
        title={
          <Badge
            invisible={notifInvitation?.length > 0 ? false : true}
            variant="dot"
            sx={theme => ({
              '.MuiBadge-badge': {
                background: theme.palette.primaryGradient,
                height: '12px',
                width: '12px',
                borderRadius: '50%',
                border: `3px solid ${theme.palette.neutral700}`,
              },
            })}
          >
            <img
              src={IconSource(
                activeTab === '#tournament' ? 'ic-tp-tournament-active' : 'ic-tp-tournament',
              )}
              alt="tournament"
            />
          </Badge>
        }
        sx={{
          minHeight: height! - (56 + 180 + 53),
          height: !height ? 'calc(100vh - (56px + 180px + 53px))' : 'auto',
          bgcolor: 'neutral900',
        }}
      >
        <Box sx={{ p: '16px' }}>
          <ProfileTournamentList
            teamSlug={teamSlug}
            setShowModalLoading={setShowModalLoading}
            setPageTitle={setPageTitle}
          />
        </Box>
      </TabItem>

      <TabItem
        hash="#member"
        title={
          <img
            src={IconSource(activeTab === '#member' ? 'ic-tp-team-active' : 'ic-tp-team')}
            alt="team"
          />
        }
        sx={{
          minHeight: height! - (56 + 180 + 53),
          height: !height ? 'calc(100vh - (56px + 180px + 53px))' : 'auto',
          bgcolor: 'neutral900',
        }}
      >
        <Box sx={{ p: '16px' }}>
          <Member
            gameSlug={gameSlug}
            teamSlug={teamSlug}
            setPageTitle={setPageTitle}
            setOpenToast={setOpenToast}
            setToastMessages={setToastMessages}
            setOpenErrorToast={setOpenErrorToast}
            setToastErrorMessages={setToastErrorMessages}
          />
        </Box>
      </TabItem>
    </CustomTabs>
  );
};
