ReactDOM.render(
  <I18nextProvider i18n={i18n}>
    <PersistGate persistor={persistor}>
      <Provider store={store}>
        <ErrorHandler>
          <HelmetProvider>
            <ScrollProvider>
              <Root />
            </ScrollProvider>
          </HelmetProvider>
        </ErrorHandler>
        <GlobalStyles />
      </Provider>
    </PersistGate>
  </I18nextProvider>,
  document.getElementById('root'),
);

/* istanbul ignore next */
register({
  onUpdate: () => {
    store.dispatch(showAlert(<Reload />, { id: 'sw-update', icon: 'bolt', timeout: 0 }));
  },
});

