import { HeaderStyled } from './styles';
import { Box, Toolbar, Menu, MenuItem, Stack, IconButton } from '@mui/material';
import { Text } from 'design/Text';
import { IconSource } from 'modules/assetpath';
import history from 'modules/history';
import React, { useState, ReactNode } from 'react';
import SVG from 'react-inlinesvg';
import { Link } from 'react-router-dom';

export interface HeaderProps {
  logo?: boolean;
  title?: string | ReactNode;
  share?: (shareOpen: boolean, handleShare: () => void) => void;
  menu?: Array<any>;
  notification?: {
    show?: boolean;
    haveNotifUnread?: boolean;
    onClick?: () => void;
  };
  back?: {
    to?: string | 'goback';
    hash?: string;
    state?: {};
  };
  background?: string;
  cta?: ReactNode;
  separator?: boolean;
}

export const Header = ({
  logo,
  title,
  share,
  menu,
  notification,
  back,
  cta,
  background,
  separator,
}: HeaderProps) => {
  const [menuOpen, setMenuOpen] = useState<null | HTMLElement>(null);
  const [shareOpen, setShareOpen] = useState<boolean>(false);

  const handleOpenMenu = (event: React.MouseEvent<HTMLElement>) => {
    setMenuOpen(event.currentTarget);
    setShareOpen(false);
  };

  const handleCloseMenu = () => {
    setMenuOpen(null);
    setShareOpen(false);
  };

  const handleShare = () => {
    setShareOpen(!shareOpen);
  };

  const handleBack = () => {
    if (back?.to) {
      if (back?.to === 'goback') {
        return history.goBack();
      }
      return history.push({ pathname: back?.to, hash: back?.hash, state: back?.state });
    }
  };

  const handleClick = (dest: string, onClick: () => void) => {
    if (dest) return history.push(dest);
    return onClick();
  };

  return (
    <HeaderStyled background={background} separator={separator}>
      <Toolbar sx={{ justifyContent: 'space-between' }} disableGutters>
        <Stack direction="row" sx={{ position: 'relative', zIndex: 1 }}>
          {back && (
            <IconButton onClick={handleBack} size="small">
              <SVG src={IconSource('ic-arrow-back-light')} />
            </IconButton>
          )}

          {logo && (
            <Link to="">
              <Stack sx={{ ml: '2px' }}>
                <img src={IconSource('logo')} alt="mabar" />
              </Stack>
            </Link>
          )}
        </Stack>

        {title && (
          <Text
            variant="h3"
            sx={{ fontWeight: 700, position: 'absolute', width: '100%', textAlign: 'center' }}
          >
            {title}
          </Text>
        )}

        <Stack direction="row" spacing="4px">
          {share && (
            <>
              <IconButton onClick={handleShare} size="small">
                <SVG src={IconSource('ic-share-social-white')} />
              </IconButton>
              {share && share(shareOpen!, handleShare!)}
            </>
          )}

          {notification?.show && (
            <IconButton onClick={notification?.onClick} size="small" sx={{ mx: '4px' }}>
              <SVG
                src={IconSource(
                  notification?.haveNotifUnread
                    ? 'ic-notifications-filled'
                    : 'ic-notifications-white',
                )}
              />
            </IconButton>
          )}

          {menu && (
            <Box sx={{ flexGrow: 0 }}>
              <IconButton onClick={handleOpenMenu} size="small">
                <SVG src={IconSource('ic-menu-options-white')} />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={menuOpen}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={Boolean(menuOpen)}
                onClose={handleCloseMenu}
                sx={{
                  mt: '40px',
                  '.MuiPaper-root': { borderRadius: '8px' },
                  '.MuiList-root': { py: 0 },
                  a: {
                    textDecoration: 'none',
                  },
                }}
              >
                {menu.map(menu => (
                  <Box key={menu.title} onClick={() => handleClick(menu?.dest, menu?.onClick)}>
                    <MenuItem
                      key={menu.title}
                      onClick={handleCloseMenu}
                      sx={{ minWidth: '188px', bgcolor: 'neutral600', p: '8px 12px' }}
                    >
                      <SVG src={IconSource(menu.icon)} />
                      <Text
                        variant="body"
                        sx={{ color: 'neutral200', fontWeight: 400, m: '0 0 0 8px' }}
                      >
                        {menu.title}
                      </Text>
                    </MenuItem>
                  </Box>
                ))}
              </Menu>
            </Box>
          )}

          {cta && cta}
        </Stack>
      </Toolbar>
    </HeaderStyled>
  );
};
