import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import ApiService from 'services/ApiService';
import Game from 'types/game';
import School from 'types/school';

export interface Kind {
  key: string;
  value: string;
}

export interface Type {
  key: string;
  value: string;
}

export interface ResultType {
  key: string;
  value: string;
}

export interface Status {
  key: string;
  value: string;
}

export interface Level {
  key: string;
  value: string;
}

export interface Player {
  id: string;
  urlSlug: string;
  name: string;
  nickName: string;
  picture: string | null;
  gameProviderId: string;
}

export interface MatchHistory {
  [x: string]: any;
  id?: string;
  kind: Kind;
  startAt: Date;
  endAt: Date;
  checkInStartAt: Date;
  checkInEndAt: Date;
  firstTeamId?: string;
  firstTeam?: string;
  firstTeamUrlSlug?: string;
  firstTeamAvatar?: string;
  firstTeamScore: number;
  firstTeamSchoolName: string;
  secondTeamId?: string;
  secondTeam?: string;
  secondTeamUrlSlug?: string;
  secondTeamAvatar?: string;
  secondTeamScore: number;
  secondTeamSchoolName?: string;
  winnerTeamId?: string;
  eventId?: string;
  eventName?: string;
  eventSlug?: string;
  resultType: ResultType;
  type: Type;
  status: Status;
  players?: any | [];
}

export interface TeamMember {
  teamId: string;
  level: Level;
  status: Status;
  type: Type;
  joinAt: Date;
  player: Player;
}

export interface TeamDetail {
  id: string;
  name: string;
  urlSlug: string;
  avatarUrl: string;
  openRecruitment: boolean;
  whatsAppGroupUrl: string;
  whatsAppPhoneNumber: string;
  discordUrl: string;
  disband: boolean;
  blocked: boolean;
  blockMessage: string;
  game: Game;
  school: School;
  memberCount: number;
  matchCount: number;
  createBy: string;
  createAt: Date;
  modifyBy: string;
  modifyAt: Date;
}

export interface DetailData<T> {
  pageIndex: number;
  pageSize: number;
  count: number;
  totalCount: number;
  totalPages: number;
  hasNextPage: boolean;
  hasPreviousPage: boolean;
  items: T;
}

export interface TeamProfileState {
  teamDetail: TeamDetail | undefined;
  teamMember: DetailData<TeamMember[]> | undefined;
  teamHistory: DetailData<MatchHistory[]> | undefined;
  status: {
    teamDetail: 'idle' | 'loading' | 'succeeded' | 'failed' | 'error';
    teamMember: 'idle' | 'loading' | 'succeeded' | 'failed' | 'error';
    teamHistory: 'idle' | 'loading' | 'succeeded' | 'failed' | 'error';
  };
  error: {
    teamDetail: string | object | undefined;
    teamMember: string | object | undefined;
    teamHistory: string | object | undefined;
  };
}

const initialState: TeamProfileState = {
  teamDetail: undefined,
  teamMember: undefined,
  teamHistory: undefined,
  status: {
    teamDetail: 'idle',
    teamMember: 'idle',
    teamHistory: 'idle',
  },
  error: {
    teamDetail: undefined,
    teamMember: undefined,
    teamHistory: undefined,
  },
};

export const fetchTeamDetail = createAsyncThunk(
  'team-profile/team-detail',
  async ({ teamSlug, version }: { teamSlug: string; version?: string }) => {
    const url = `/${version ? `v${version}/` : ''}team/${teamSlug}`;
    const res = await ApiService.get<TeamDetail>(url);
    return res.data;
  },
);

export const fetchTeamMember = createAsyncThunk(
  'team-profile/team-member',
  async ({
    teamSlug,
    pageIndex,
    pageSize,
    version,
    approved,
  }: {
    teamSlug: string;
    pageIndex: number;
    pageSize: number;
    version: string;
    approved: boolean;
  }) => {
    const url = `/${version ? `v${version}/` : ''}team-member${
      approved ? '/approved' : ''
    }/${teamSlug}/${pageIndex}/${pageSize}`;
    const res = await ApiService.get(url);
    return res.data;
  },
);

export const fetchTeamHistory = createAsyncThunk(
  'team-profile/history',
  async ({
    teamSlug,
    pageIndex,
    pageSize,
    version,
  }: {
    teamSlug: string;
    pageIndex: number;
    pageSize: number;
    version: string;
  }) => {
    const url = `/match-api/${
      version ? `v${version}/` : ''
    }history/team/${teamSlug}/${pageIndex}/${pageSize}`;
    const res = await ApiService.get(url);
    return res.data;
  },
);

export const directApproval = createAsyncThunk(
  'team-profile/direct-approval',
  async ({ matchId, players }: { matchId: string; players: string[] }) => {
    try {
      const url = `/scrim/direct-approval/${matchId}`;
      const res = await ApiService.put(url, {
        id: matchId,
        players,
      });
      return res.data;
    } catch (error: any) {
      if (error.response) {
        return { isAxiosError: true, data: error.response.data };
      }
    }
  },
);

