import { Stack as StackMui, Box as BoxMui } from '@mui/material';
import Avatar from '@mui/material/Avatar';
import useMediaQuery from '@mui/material/useMediaQuery';
import { PrimaryButton } from 'design/Buttons/PrimaryButton';
import { SecondaryButton } from 'design/Buttons/SecondaryButton';
import { Text } from 'design/Text';
import { dateFormat, parseTimeLeftCheckIn, parseTimeLeftDay, limitedChar } from 'modules/helpers';
import history from 'modules/history';
import React from 'react';
import Countdown from 'react-countdown';
import { useLocation } from 'react-router-dom';

export interface TeamHistoryType {
  id?: string | undefined;
  winnerTeamId?: string | undefined;
  firstTeamId?: string | undefined;
  secondTeamId?: string | undefined;
  propTeamId?: string | undefined;
  firstTeam?: string | undefined;
  firstTeamUrlSlug?: string | undefined;
  firstTeamAvatar?: string | undefined;
  firstTeamSchoolName?: string | undefined;
  firstTeamScore: number | 0;
  secondTeam?: string | undefined;
  secondTeamAvatar?: string | undefined;
  secondTeamSchoolName?: string | undefined;
  secondTeamScore: number | 0;
  secondTeamUrlSlug?: string | undefined;
  status?: string | undefined;
  statusLabel?: string | undefined;
  checkInStartAt?: Date | undefined;
  checkInEndAt?: Date | undefined;
  link?: any | undefined;
  currentTeam?: any | [];
  gameSlug?: string | undefined;
  eventSlug?: string | undefined;
  startAt?: Date | undefined;
  myProfileId?: string | undefined;
  players?: any | [];
}

export const TeamHistory = ({
  id,
  winnerTeamId,
  firstTeamId,
  propTeamId,
  secondTeamId,
  firstTeam,
  firstTeamAvatar,
  firstTeamSchoolName,
  firstTeamScore,
  secondTeam,
  secondTeamAvatar,
  secondTeamSchoolName,
  secondTeamScore,
  status,
  statusLabel,
  checkInStartAt,
  checkInEndAt,
  gameSlug,
  eventSlug,
  startAt,
  myProfileId,
  players,
}: TeamHistoryType) => {
  const location = useLocation<any>();

  const widthMax760 = useMediaQuery('(max-width: 760px)');
  let isTimeLeftStartAt: boolean = true;
  let isDayTimeLeftStartAt: boolean = true;
  let teamDrawScore: boolean = true;
  let teamAvatar1Color: string = '#D4D6D8';
  let teamAvatar2Color: string = '#D4D6D8';

  const getDayTimeLeft = () => {
    const timeLeft = parseTimeLeftDay({
      dateTime: startAt,
      locale: 'id',
    });

    if (timeLeft == 0) return isDayTimeLeftStartAt;

    return (isDayTimeLeftStartAt = false);
  };

  const timeLeftCheckinStartAtButton = () => {
    const timeLeft = parseTimeLeftCheckIn({
      dateTime: checkInStartAt,
      locale: 'id',
    });

    if (timeLeft) return isTimeLeftStartAt;

    return (isTimeLeftStartAt = false);
  };

  const drawTeam = () => {
    if (secondTeamScore == firstTeamScore) return teamDrawScore;

    return (teamDrawScore = false);
  };

  const checkColorAvatar1 = () => {
    if (winnerTeamId == firstTeamId) {
      if (propTeamId != winnerTeamId) {
        return teamAvatar1Color;
      }

      if (winnerTeamId == firstTeamId && firstTeamScore < secondTeamScore)
        return (teamAvatar1Color = '#D4D6D8');

      return (teamAvatar1Color = '#26BD87');
    } else {
      if (propTeamId != winnerTeamId && !drawTeam()) return (teamAvatar1Color = '#F45A64');

      if (winnerTeamId == secondTeamId) return (teamAvatar1Color = '#D4D6D8');

      // if(winnerTeamId == propTeamId && firstTeamScore < secondTeamScore)
      // return (teamAvatar1Color = '#F45A64');

      if (drawTeam()) return teamAvatar1Color;

      return (teamAvatar1Color = '#D4D6D8');
    }
  };

  const checkColorAvatar2 = () => {
    if (winnerTeamId == secondTeamId) {
      if (winnerTeamId != propTeamId) return teamAvatar2Color;

      if (winnerTeamId == secondTeamId) return (teamAvatar2Color = '#26BD87');

      return (teamAvatar2Color = '#F45A64');
    } else {
      if (propTeamId != secondTeamId) return teamAvatar2Color;

      if (winnerTeamId == firstTeamId) return (teamAvatar2Color = '#F45A64');

      if (drawTeam()) return teamAvatar2Color;

      return teamAvatar2Color;
    }
  };

  const isPlayer = () => {
    return players?.indexOf(myProfileId) > -1;
  };

  const isLinkCard = () => {
    if (status == 'CheckIn' || status == 'WinBye') return null;

    return history.push({
      pathname: `/tournament/${gameSlug}/${eventSlug}/match/${id}`,
      state: { pathname: location.pathname },
    });
  };

  const handleCursor = () => {
    if (status === 'CheckIn' || status === 'WinBye') return 'auto';
    return 'pointer';
  };

  return (
    <BoxMui
      sx={{ cursor: handleCursor() }}
      onClick={() => {
        isLinkCard();
      }}
    >
      <BoxMui
        sx={{
          height:
            status == 'WinBye'
              ? '93px'
              : status == 'Invitation'
              ? '125px'
              : status != 'WinBye' && status != 'Invitation' && widthMax760
              ? '148px'
              : '142px',
          borderRadius: '4px',
          boxShadow: '#242628 0px 0px 0px 1px',
          bgcolor: '#1A1817',
          mb: '10px',
        }}
      >
        {status != undefined && status != 'Invitation' ? (
          <StackMui direction="row">
            <StackMui sx={{ width: '50%', ml: '12px', mt: '12px' }} direction="row">
              <Avatar
                variant="rounded"
                alt="Remy Sharp"
                src={firstTeamAvatar}
                sx={{
                  width: '35.9px',
                  height: '36px',
                  boxShadow:
                    status == 'Inprogress' ||
                    status == 'CheckIn' ||
                    status == 'Scheduled' ||
                    status == 'NotValid' ||
                    status == 'WinBye'
                      ? '#F1EBEB 0px 0px 0px 2px'
                      : checkColorAvatar1() + ' 0px 0px 0px 2px',
                  borderRadius: '7px',
                }}
              />
              <StackMui direction="column">
                <Text
                  variant="labelSmall"
                  sx={{
                    cursor: handleCursor(),
                    fontSize: '12px',
                    lineHeight: '16.8px',
                    fontWeight: '600',
                    letterSpacing: '0px',
                    font: 'Lexend Deca',
                    ml: '12px',
                    mb: '2px',
                    color: '#D4D6D8',
                  }}
                >
                  {limitedChar(firstTeam, 30)}
                </Text>
                <Text
                  variant="labelSmall"
                  sx={{
                    cursor: handleCursor(),
                    fontSize: '10px',
                    lineHeight: '14px',
                    fontWeight: '400',
                    fontFamily: 'Inter',
                    letterSpacing: '0.16px',
                    ml: '12px',
                    color: '#94989E',
                  }}
                >
                  {limitedChar(firstTeamSchoolName, 30)}
                </Text>
              </StackMui>
            </StackMui>
            {(status != undefined && status == 'Compete') ||
            status == 'ResultCalculated' ||
            status == 'DataCompleted' ||
            status == 'Dispute' ? (
              <StackMui
                justifyContent="flex-end"
                alignItems="flex-end"
                sx={{
                  width: '50%',
                  mt: '15px',
                  mr: '12px',
                  textAlign: 'right',
                }}
              >
                <Text
                  variant="h2"
                  sx={{
                    fontSize: '24px',
                    lineHeight: '32px',
                    color: checkColorAvatar1(),
                  }}
                >
                  {firstTeamScore}
                </Text>
              </StackMui>
            ) : (
              ''
            )}

            {(status != undefined && status == 'CheckIn') ||
            status == 'Expired' ||
            status == 'Scheduled' ||
            status == 'Reject' ? (
              <StackMui
                justifyContent="flex-end"
                alignItems="flex-end"
                sx={{
                  width: '50%',
                  mt: '28px',
                  mr: '8px',
                  textAlign: 'right',
                }}
              >
                {status == 'Expired' || status == 'Reject' ? (
                  <PrimaryButton variant="small" disabled>
                    Check In
                  </PrimaryButton>
                ) : (
                  <>
                    {status == 'Scheduled' && !timeLeftCheckinStartAtButton() && isPlayer() ? (
                      <Countdown
                        date={checkInEndAt}
                        autoStart
                        intervalDelay={0}
                        renderer={({ minutes }) => (
                          <>
                            {minutes > 0 ? (
                              <PrimaryButton
                                variant="small"
                                onClick={() =>
                                  history.push(`/tournament/${gameSlug}/${eventSlug}/match/${id}`)
                                }
                              >
                                Check In
                              </PrimaryButton>
                            ) : (
                              <PrimaryButton variant="small" disabled>
                                Check In
                              </PrimaryButton>
                            )}
                          </>
                        )}
                      />
                    ) : status == 'CheckIn' && !timeLeftCheckinStartAtButton() && isPlayer() ? (
                      <Countdown
                        date={checkInEndAt}
                        autoStart
                        intervalDelay={0}
                        renderer={({ minutes }) => (
                          <>
                            {minutes > 0 ? (
                              <PrimaryButton
                                variant="small"
                                onClick={() =>
                                  history.push(`/tournament/${gameSlug}/${eventSlug}/match/${id}`)
                                }
                              >
                                Check In
                              </PrimaryButton>
                            ) : (
                              <PrimaryButton variant="small" disabled>
                                Check In
                              </PrimaryButton>
                            )}
                          </>
                        )}
                      />
                    ) : (
                      <PrimaryButton
                        variant="small"
                        onClick={() =>
                          history.push(`/tournament/${gameSlug}/${eventSlug}/match/${id}`)
                        }
                        disabled
                      >
                        Check In
                      </PrimaryButton>
                    )}
                  </>
                )}

                {status == 'Scheduled' ? (
                  <Countdown
                    date={checkInEndAt}
                    autoStart
                    intervalDelay={0}
                    renderer={({ minutes }) => (
                      <>
                        {minutes > 0 && isPlayer() && !timeLeftCheckinStartAtButton() ? (
                          <Text
                            variant="labelSmall"
                            sx={{
                              fontSize: '12px',
                              lineHeight: '17px',
                              fontWeight: '400, Regular',
                              fontFamily: 'Inter',
                              color: '#94989E',
                              letterSpacing: '0px',
                              mt: '4px',
                            }}
                          >
                            {minutes > 0 &&
                              parseTimeLeftCheckIn({
                                dateTime: checkInEndAt,
                                // subtract: 10,
                                locale: 'id',
                              })}{' '}
                          </Text>
                        ) : (
                          <Text
                            variant="labelSmall"
                            sx={{
                              fontSize: '12px',
                              lineHeight: '17px',
                              fontWeight: '400, Regular',
                              fontFamily: 'Inter',
                              color: '#94989E',
                              letterSpacing: '0px',
                              mt: '4px',
                            }}
                          >
                            {isPlayer() ? 'Check In belum dibuka' : 'Kamu bukan peserta'}
                          </Text>
                        )}
                      </>
                    )}
                  />
                ) : status == 'CheckIn' ? (
                  <Countdown
                    date={checkInEndAt}
                    autoStart
                    intervalDelay={0}
                    renderer={({ minutes }) => (
                      <>
                        {minutes > 0 && isPlayer() ? (
                          <Text
                            variant="labelSmall"
                            sx={{
                              fontSize: '12px',
                              lineHeight: '17px',
                              fontWeight: '400, Regular',
                              fontFamily: 'Inter',
                              color: '#94989E',
                              letterSpacing: '0px',
                              mt: '4px',
                            }}
                          >
                            {minutes > 0 &&
                              parseTimeLeftCheckIn({
                                dateTime: checkInEndAt,
                                // subtract: 10,
                                locale: 'id',
                              })}{' '}
                          </Text>
                        ) : (
                          <Text
                            variant="labelSmall"
                            sx={{
                              fontSize: '12px',
                              lineHeight: '17px',
                              fontWeight: '400, Regular',
                              fontFamily: 'Inter',
                              color: '#94989E',
                              letterSpacing: '0px',
                              mt: '4px',
                            }}
                          >
                            {isPlayer() ? 'Check In sudah di tutup' : 'Kamu bukan peserta'}
                          </Text>
                        )}
                      </>
                    )}
                  />
                ) : (
                  <Text
                    variant="labelSmall"
                    sx={{
                      fontSize: '12px',
                      lineHeight: '17px',
                      fontWeight: '400, Regular',
                      fontFamily: 'Inter',
                      color: '#94989E',
                      letterSpacing: '0px',
                      mt: '4px',
                    }}
                  >
                    {statusLabel ?? ''}
                  </Text>
                )}
              </StackMui>
            ) : (
              ''
            )}

            {(status != undefined && status == 'Inprogress') ||
            status == 'WinBye' ||
            status == 'NotValid' ? (
              <StackMui
                justifyContent="flex-end"
                alignItems="flex-end"
                sx={{
                  width: '50%',
                  mr: '8px',
                  mb: widthMax760 ? '25px' : '15px',
                  mt: '9px',
                  textAlign: 'right',
                }}
              >
                <Text
                  variant="button"
                  sx={{
                    fontSize: '12px',
                    fontFamily: 'Lexend Deca',
                    lineHeight: '24px',
                    fontWeight: '600',
                  }}
                >
                  {statusLabel}
                </Text>
              </StackMui>
            ) : (
              ''
            )}
          </StackMui>
        ) : (
          ''
        )}

        {status != undefined && status == 'Invitation' ? (
          <BoxMui sx={{ ml: '12px', mt: '12px', pt: '10px' }}>
            <Text
              variant="labelSmall"
              sx={{
                fontSize: '14px',
                lineHeight: '20px',
                fontWeight: '400',
                fontFamily: 'Inter',
                letterSpacing: '0px',
                color: '#D4D6D8',
              }}
            >
              Tim kamu ditantang tanding Scrim!
            </Text>
          </BoxMui>
        ) : (
          ''
        )}
        {status != undefined && status != 'WinBye' ? (
          <StackMui
            sx={{
              mt:
                status == 'CheckIn' ||
                status == 'Expired' ||
                status == 'Reject' ||
                status == 'Scheduled'
                  ? '-32px'
                  : '0px',
            }}
            direction="row"
          >
            <StackMui sx={{ width: '70%', ml: '12px', mt: '12px' }} direction="row">
              <Avatar
                variant="rounded"
                alt="Remy Sharp"
                src={secondTeamAvatar}
                sx={{
                  width: '35.9px',
                  height: '36px',
                  boxShadow:
                    status == 'Inprogress' ||
                    status == 'CheckIn' ||
                    status == 'Scheduled' ||
                    status == 'NotValid' ||
                    status == 'WinBye'
                      ? '#F1EBEB 0px 0px 0px 2px'
                      : checkColorAvatar2() + ' 0px 0px 0px 2px',
                  borderRadius: '7px',
                }}
              />

              <StackMui direction="column">
                <Text
                  variant="labelSmall"
                  sx={{
                    cursor: handleCursor(),
                    fontSize: '12px',
                    lineHeight: '16.8px',
                    fontWeight: '600',
                    font: 'Lexend Deca',
                    letterSpacing: '0px',
                    ml: '12px',
                    mb: '2px',
                    color: '#D4D6D8',
                  }}
                >
                  {limitedChar(secondTeam, 30)}
                </Text>
                <Text
                  variant="labelSmall"
                  sx={{
                    cursor: handleCursor(),
                    fontSize: '10px',
                    lineHeight: '14px',
                    fontWeight: '400',
                    letterSpacing: '0.16px',
                    fontFamily: 'Inter',
                    ml: '12px',
                    color: '#94989E',
                  }}
                >
                  {limitedChar(secondTeamSchoolName, 30)}
                </Text>
              </StackMui>
            </StackMui>

            {(status != undefined && status == 'Compete') ||
            status == 'ResultCalculated' ||
            status == 'DataCompleted' ||
            status == 'Dispute' ? (
              <StackMui
                justifyContent="flex-end"
                alignItems="flex-end"
                sx={{
                  mt: '16px',
                  mr: '12px',
                  width: '50%',
                  textAlign: 'right',
                }}
              >
                <Text
                  variant="h2"
                  sx={{
                    fontSize: '24px',
                    lineHeight: '32px',
                    color: checkColorAvatar2(),
                  }}
                >
                  {secondTeamScore}
                </Text>
              </StackMui>
            ) : status == 'Invitation' ? (
              <StackMui
                direction="row"
                justifyContent="flex-end"
                alignItems="flex-end"
                sx={{
                  width: '50%',
                  mr: '8px',
                  mb: '15px',
                  mt: '12px',
                  textAlign: 'right',
                }}
              >
                <SecondaryButton variant="small" sx={{ mr: '7px' }}>
                  Detail
                </SecondaryButton>
                <PrimaryButton variant="small">Terima</PrimaryButton>
              </StackMui>
            ) : (
              ''
            )}
          </StackMui>
        ) : (
          ''
        )}

        <BoxMui
          sx={{
            backgroundImage: `url(${`${process.env.PUBLIC_URL}/media/images/myteam/Line1.png`})`,
            backgroundRepeat: 'no-repeat',
            backgroundSize: '100%',
            height: '1px',
            mt:
              status == 'Invitation'
                ? '0px'
                : (widthMax760 && status == 'Inprogress') || (widthMax760 && status == 'NotValid')
                ? '8px'
                : (widthMax760 && status == 'ResultCalculated') ||
                  (widthMax760 && status == 'DataCompleted') ||
                  (widthMax760 && status == 'Dispute')
                ? '7px'
                : widthMax760 && status == 'WinBye'
                ? '5px'
                : '13px',
          }}
        >
          <StackMui
            direction="row"
            sx={{
              ml: '12px',
              mr: '12px',
            }}
          >
            <BoxMui sx={{ width: '55%', textAlign: 'left', paddingTop: '5px' }}>
              <Text
                variant="labelSmall"
                sx={{
                  cursor: handleCursor(),
                  fontSize: '12px',
                  lineHeight: '16.8px',
                  fontWeight: 'bold',
                  color: '#EAEAEC',
                  textAlign: 'left',
                  letterSpacing: '0px',
                  fontFamily: 'Inter',
                  fontStyle: 'Regular',
                  mr: '5px',
                }}
              >
                Group Stage
              </Text>
              <Text
                variant="labelSmall"
                sx={{
                  cursor: handleCursor(),
                  fontSize: '12px',
                  lineHeight: '16.8px',
                  fontWeight: '400',
                  color: '#94989E',
                  textAlign: 'left',
                  letterSpacing: '0px',
                  fontFamily: 'Inter',
                  fontStyle: 'Regular',
                }}
              >
                #{id}
              </Text>
            </BoxMui>

            <BoxMui sx={{ width: '45%', textAlign: 'right', paddingTop: '5px' }}>
              <Text
                variant="labelSmall"
                sx={{
                  cursor: handleCursor(),
                  fontSize: widthMax760 && status == 'Scheduled' ? '10px' : '12px',
                  lineHeight: '16.8px',
                  fontWeight: status == 'CheckIn' || status == 'Scheduled' ? 'bold' : '400',
                  color: status == 'CheckIn' || status == 'Scheduled' ? '#D4D6D8' : '#94989E',
                  textAlign: 'right',
                  fontFamily: 'Inter',
                  fontStyle: 'normal',
                  letterSpacing: '0px',
                }}
              >
                {getDayTimeLeft()
                  ? 'Hari ini, ' + dateFormat(startAt, 'Asia/Jakarta', 'id').format('HH:mm')
                  : dateFormat(startAt, 'Asia/Jakarta', 'id').format('DD MMM yyyy, HH:mm')}{' '}
                WIB
              </Text>
            </BoxMui>
          </StackMui>
        </BoxMui>
      </BoxMui>
    </BoxMui>
  );
};
