import { NormalText } from 'components/styled/text.styled';
import { IconSource } from 'modules/assetpath';
import React from 'react';
import { useTranslation } from 'react-i18next';

const InvitationEmptyPage = () => {
  const { t } = useTranslation();
  return (
    <div
      style={{
        display: 'grid',
        position: 'relative',
        alignItems: 'center',
        justifyContent: 'center',
        width: '100%',
        marginTop: '48px',
      }}
    >
      <div
        style={{
          display: 'flex',
          alignContent: 'center',
          alignItems: 'center',
          justifyContent: 'center',
          width: '100%',
        }}
      >
        <img
          style={{ backgroundRepeat: 'no-repeat' }}
          src={IconSource('mail_invitation', 'png')}
          alt={t('player_team_detail_invitation_empty_state')}
        />
      </div>
      <div style={{ marginTop: '8px', marginLeft: '0%', marginRight: '0%' }}>
        <NormalText
          color="#8D8784"
          textAlign="center"
          fontStyle="normal"
          fontWeight="400"
          fontSize="12px"
          fontFamily="Inter"
        >
          {t('player_team_detail_invitation_empty_state')}
        </NormalText>
      </div>
    </div>
  );
};

export default InvitationEmptyPage;
