import { TabStyled, TabsStyled, TabPanelStyled } from './styles';
import { Theme, Box } from '@mui/material';
import { SxProps } from '@mui/system';
import history from 'modules/history';
import React, { ReactNode, useEffect } from 'react';
import { useLocation } from 'react-router-dom';

interface TabsProps {
  children?: ReactNode;
  sx?: SxProps<Theme>;
  tabSx?: SxProps<Theme>;
  tabIndex?: (value: any) => void;
  handleChange?: any;
  activeTab?: number | string;
}

export const TabItem: any = TabPanelStyled;

export const CustomTabs = ({ children, tabSx, tabIndex, activeTab, ...props }: TabsProps) => {
  const location = useLocation<any>();
  const [value, setValue] = React.useState(activeTab ?? 0);

  const onChangeHandler = (_event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
    if (props.handleChange) {
      props.handleChange(newValue);
    }
  };

  useEffect(() => {
    if (tabIndex) tabIndex(value);
  }, [value]);

  useEffect(() => {
    if (activeTab) {
      setValue(activeTab);
    }
  }, []);

  const handleHash = (value: string) => {
    return history.push({ hash: value, state: location.state });
  };

  return (
    <Box sx={{ width: '100%' }}>
      <TabsStyled value={value} onChange={onChangeHandler} aria-label="Tabs Component" {...props}>
        {React.Children.map(children, (child, index) => {
          if (React.isValidElement(child)) {
            return (
              <TabStyled
                key={index}
                component="a"
                label={child?.props?.title}
                icon={child?.props?.icon}
                sx={tabSx}
                onClick={() => (child?.props?.hash ? handleHash(child?.props?.hash) : null)}
                value={child?.props?.hash ?? index}
              />
            );
          }
          return false;
        })}
      </TabsStyled>
      {React.Children.map(children, (child, index) => {
        if (React.isValidElement(child)) {
          const customChild = {
            ...child,
            props: {
              ...child.props,
              title: '',
            },
          };
          return (
            (customChild?.props?.hash ?? index) === value && <Box key={index}>{customChild}</Box>
          );
        }
        return false;
      })}
    </Box>
  );
};
