import { Box, Stack } from '@mui/material';
import { NormalText } from 'components/styled/text.styled';
import { LayoutContext } from 'context/LayoutContext';
import { PrimaryButton } from 'design/Buttons/PrimaryButton';
import { SecondaryButton } from 'design/Buttons/SecondaryButton';
import { InfoModal } from 'design/Cards/InfoModal';
import { TeamSchool as TeamSchoolBox } from 'design/Cards/TeamSchool';
import { TextInputSearch } from 'design/Forms/TextInputSearch';
import { Modal } from 'design/Modal';
import { Text } from 'design/Text';
import _ from 'lodash';
import { IconSource, ImageSource } from 'modules/assetpath';
import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { fetchPlayerProfile, getPlayerProfileFromSession } from 'redux/account/AccountServices';
import { useAppDispatch, useAppSelector } from 'redux/hooks';
import { useAppThunkDispatch } from 'redux/store';
import { getMyTeamAction } from 'redux/team/MyTeamReducer';
import { kickMember } from 'redux/team/TeamApproval';
import { setTeamSelectedAction } from 'redux/team/TeamDraftReducer';
import {
  fetchTeamSuggestionList,
  TeamSchool,
  TeamSchoolResponse,
} from 'redux/team/TeamListReducer';
import { ErrorResponse, Errors, requestJoinTeam } from 'redux/team/TeamMemberReducer';
import { getMyCurrentTeamFromSession } from 'redux/team/TeamService';

const TeamListPage = () => {
  const { t } = useTranslation();
  const history = useHistory();
  const dispatch = useAppDispatch();
  const thunkDispatch = useAppThunkDispatch();

  const playerInfo = getPlayerProfileFromSession();
  const currentTeam = getMyCurrentTeamFromSession();
  const { teamSchool, isTeamLoading, gameTeamDraft } = useAppSelector(
    ({ teamList, teamDraft, teamMember }) => ({
      isTeamLoading: teamList.status === 'loading',
      teamSchool: teamList.teamSchool.items,
      gameTeamDraft: teamDraft.game,
      isJoinLoading: teamMember.status.data === 'loading',
    }),
  );

  const { data } = useAppSelector(state => state.myTeam);
  const { items: itemsDataMyTeam } = data;

  const [isSearching, setIsSearching] = useState(false);

  const [isDisableButtonjoin, setDisableButtonjoin] = useState(false);
  const [getConfirmModal, setConfirmModal] = useState(false);
  const [getConfirmModalFail, setConfirmModalFail] = useState(false);
  const [getConfirmModalSuccsess, setConfirmModalSuccsess] = useState(false);
  const [getConfirmModalRejectJoin, setConfirmModalRejectJoin] = useState(false);
  const [getConfirmModalRejectJoinFail, setConfirmModalRejectJoinFail] = useState(false);
  const [getConfirmGameModal, setConfirmGameModal] = useState(false);

  const [getTeamSelected, setTeamSelected] = useState<TeamSchool>();
  const [getErrorList, setErrorList] = useState<ErrorResponse | any>();

  const [getTeamSchoolList, setTeamSchoolList] = useState<TeamSchoolResponse>({
    items: teamSchool,
  });

  const [getPlayerProfile, setPlayerProfile] = useState(playerInfo);
  const playerProfileThunk = fetchPlayerProfile({
    username: getPlayerProfileFromSession().userName,
  });

  const fetchAccountProfile = () => {
    thunkDispatch(playerProfileThunk)
      .unwrap()
      .then((res: any) => {
        res && res.id && setPlayerProfile(res);
      });
  };

  const getTeamSchool = React.useCallback(
    (u: string, pI: number, pS: number, q?: string, v?: string) => {
      thunkDispatch(
        fetchTeamSuggestionList({
          username: u,
          pageIndex: pI,
          pageSize: pS,
          search: q,
          version: v,
        }),
      )
        .unwrap()
        .then(res => {
          if ((res && res?.isAxiosError) || res.errors) {
            setErrorList(res.errors);
          } else {
            setTeamSchoolList(res);
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    [thunkDispatch, teamSchool],
  );

  const joinTeam = React.useCallback(
    (id: string, ts: TeamSchool, v?: string) => {
      setErrorList({});
      setDisableButtonjoin(true);
      thunkDispatch(requestJoinTeam({ teamId: ts.id, playerId: id, version: v }))
        .unwrap()
        .then(res => {
          if ((res && res?.isAxiosError) || res.errors) {
            setErrorList(res.errors);
            setConfirmModalFail(true);
            setDisableButtonjoin(false);
          } else {
            setDisableButtonjoin(false);
            setErrorList(undefined as any);
            setConfirmModal(false);
            saveTeamSelected(ts);
            setConfirmModalSuccsess(true);
            //  getTeamSchool(getPlayerProfile?.userName ?? '', 1, 100);
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    [thunkDispatch, teamSchool],
  );

  const rejectJoin = React.useCallback(
    (id: string, ts: TeamSchool) => {
      setErrorList({});
      setDisableButtonjoin(true);
      thunkDispatch(kickMember({ playerId: id, teamId: ts.id }))
        .unwrap()
        .then(res => {
          if ((res && res?.isAxiosError) || res.errors) {
            setErrorList(res.errors);
            setConfirmModalRejectJoinFail(true);
            setDisableButtonjoin(false);
          } else {
            setErrorList(undefined as any);
            saveTeamSelected(ts);

            dispatch(
              getMyTeamAction({ pageIndex: 1, pageSize: 20, playerSlug: playerInfo.userName }),
            );
            getTeamSchool(getPlayerProfile?.userName ?? '', 1, 100);
            setDisableButtonjoin(false);
            setConfirmModalRejectJoin(false);
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    [thunkDispatch],
  );

  const saveTeamSelected = React.useCallback(
    (team: TeamSchool) => dispatch(setTeamSelectedAction({ teamSelected: team })),
    [dispatch],
  );

  const handleTeamSelected = (e: TeamSchool) => {
    setErrorList(undefined as any);
    setConfirmModal(true);
    setTeamSelected(e);
  };

  const handleTeamRejectSelected = (e: any) => {
    setErrorList(undefined as any);
    setConfirmModalRejectJoin(true);
    setTeamSelected(e);
  };

  async function searchTeamInMySchool(search: string) {
    setIsSearching(true);
    getTeamSchool(getPlayerProfile?.userName ?? '', 1, 100, search);
  }

  const parseError = (errors: Errors | any) => {
    let err = errors as Errors;
    if (err) {
      let errorsList: string[] = [];
      if (err?.playerId) {
        errorsList = errorsList.concat(
          err.playerId.map(e => {
            if (e.code === 'RX1302') {
              console.error('e.code', e);
              if (e.formatedValue) {
                console.error('e.formatedValue', e.formatedValue);
                if (e.formatedValue?.playerId && e.formatedValue?.gameId) {
                  console.error(
                    'e.formatedValue?.playerId && e.formatedValue?.gameId',
                    e.formatedValue,
                  );
                  const myTeam = currentTeam?.find(v => v.gameId === e.formatedValue.gameId);
                  console.error('myTeam', myTeam);
                  if (e.formatedValue.gameId === myTeam?.gameId) {
                    return `Kamu sudah mempunyai tim di Game ${getTeamSelected?.game?.name}`;
                  }
                }
              } else {
                Object.values(err).forEach((prop: any) => {
                  return _.map(prop, _.property('message'));
                });
              }
            } else {
              Object.values(err).forEach((prop: any) => {
                return _.map(prop, _.property('message'));
              });
            }
            return e.message;
          }),
        );
        return errorsList.join('\r\n');
      } else {
        Object.values(err).forEach((prop: any) => {
          return _.map(prop, _.property('message'));
        });
      }
      return errorsList.join('\r\n');
    }
    return null;
  };

  const { setValue } = useContext(LayoutContext);

  useEffect(() => {
    if (!gameTeamDraft) {
      return setConfirmGameModal(true);
    }

    fetchAccountProfile();
    if (getPlayerProfile && !isSearching) {
      getTeamSchool(getPlayerProfile?.userName ?? '', 1, 100);
    }

    dispatch(getMyTeamAction({ pageIndex: 1, pageSize: 20, playerSlug: playerInfo.userName }));

    setValue({
      pageTitle: t('team_page_title', { title: gameTeamDraft?.name }),
      background: { color: '#0a0a0b' },
      appBar: {
        title: t('team_page_title', { title: gameTeamDraft?.name }),
        logo: false,
        back: { to: '/my-team' },
        separator: true,
      },
      padding: '0',
      bottomBar: {
        show: false,
      },
    });
  }, [gameTeamDraft]);

  return (
    <>
      <Modal show={getConfirmGameModal}>
        <InfoModal
          cta={
            <Stack direction="row" justifyContent="flex-end" spacing={1} sx={{ width: '100%' }}>
              <PrimaryButton onClick={() => history.push(`/my-team`)}>Back</PrimaryButton>
            </Stack>
          }
        >
          <Box sx={{ width: '100%' }}>
            <Text variant="h2" mb="12px">
              Warning!!
            </Text>
            <Text>Please choice the game</Text>
          </Box>
        </InfoModal>
      </Modal>

      <Modal show={getConfirmModal}>
        <InfoModal
          cta={
            <Stack direction="row" justifyContent="flex-end" spacing={1} sx={{ width: '100%' }}>
              <SecondaryButton onClick={() => setConfirmModal(false)}>Batal</SecondaryButton>
              <PrimaryButton
                onClick={() => joinTeam(getPlayerProfile?.id ?? '', getTeamSelected!)}
                disabled={isDisableButtonjoin}
              >
                {isDisableButtonjoin ? 'Please wait..' : 'Join'}
              </PrimaryButton>
            </Stack>
          }
        >
          <Box sx={{ width: '100%' }}>
            <Text variant="h2" mb="12px">
              Join Tim
            </Text>
            <Text>
              Apakah kamu yakin ingin join tim <b>{getTeamSelected?.name ?? ''}?</b>
            </Text>
          </Box>
        </InfoModal>
      </Modal>

      <Modal show={getConfirmModalFail}>
        <InfoModal
          icon={ImageSource('Warning', 'svg')}
          cta={
            <PrimaryButton wide onClick={() => setConfirmModalFail(false)}>
              Oke, kembali ke halaman
            </PrimaryButton>
          }
        >
          <Text variant="h2" textAlign="center" mb="8px">
            Undangan gagal terkirim
          </Text>
          <Text textAlign="center">Mohon maaf undangan gagal terkirim, mohon di coba lagi..</Text>
        </InfoModal>
      </Modal>

      <Modal show={getConfirmModalRejectJoinFail}>
        <InfoModal
          icon={ImageSource('Warning', 'svg')}
          cta={
            <PrimaryButton wide onClick={() => setConfirmModalRejectJoinFail(false)}>
              Oke, kembali ke halaman
            </PrimaryButton>
          }
        >
          <Text variant="h2" textAlign="center" mb="8px">
            Reject team gagal terkirim
          </Text>
          <Text textAlign="center">
            Mohon maaf reject team gagal terkirim, mohon di coba lagi..
          </Text>
        </InfoModal>
      </Modal>

      <Modal show={getConfirmModalSuccsess}>
        <InfoModal
          icon={ImageSource('Paperplane', 'svg')}
          cta={
            <PrimaryButton wide onClick={() => history.push('/my-team')}>
              Kembali ke Tim Saya
            </PrimaryButton>
          }
        >
          <Text variant="h2" textAlign="center" mb="8px">
            Permintaan Join terkirim
          </Text>
          <Text textAlign="center">Tunggu konfirmasi dari kapten tim</Text>
        </InfoModal>
      </Modal>

      <Modal show={getConfirmModalRejectJoin}>
        <InfoModal
          cta={
            <Stack direction="row" justifyContent="flex-end" spacing={1} sx={{ width: '100%' }}>
              <SecondaryButton onClick={() => setConfirmModalRejectJoin(false)}>
                Batal
              </SecondaryButton>
              <PrimaryButton
                onClick={() => rejectJoin(getPlayerProfile?.id ?? '', getTeamSelected!)}
                disabled={isDisableButtonjoin}
                sx={theme => ({ background: theme.palette.danger600 })}
              >
                {isDisableButtonjoin ? 'Please wait..' : 'Batalkan Request'}
              </PrimaryButton>
            </Stack>
          }
        >
          <Box sx={{ width: '100%' }}>
            <Text variant="h2" mb="12px">
              Batal Join
            </Text>
            <Text>
              Apakah kamu yakin ingin membatalkan request join dengan tim{' '}
              <b>{getTeamSelected?.name ?? ''}?</b>
            </Text>
          </Box>
        </InfoModal>
      </Modal>

      <Box sx={{ m: '0px 16px 16px 16px' }}>
        {itemsDataMyTeam ? (
          <Box sx={{ pt: '17px' }}>
            <>
              {itemsDataMyTeam.map(item => {
                if (item.status.key == '1') {
                  return (
                    <TeamSchoolBox
                      id={'itemsDataMyTeam'}
                      key={'waiting' + item.id}
                      image={item.avatarUrl}
                      isCaptain={item?.captain ?? ''}
                      title={item.name}
                      subTitle={'Menunggu konfirmasi'}
                      currentPlayer={item.memberCount ?? 0}
                      maxPlayer={gameTeamDraft?.maxMember ?? 0}
                      status={'waiting'}
                      marginBox={'0px'}
                      isHaveTeam={itemsDataMyTeam}
                      onClicklink={() => {
                        handleTeamRejectSelected(item);
                      }}
                    />
                  );
                } else {
                  return (
                    <TeamSchoolBox
                      key={'create_new'}
                      image={gameTeamDraft?.iconUrl ?? IconSource('game_logo_freefire', 'png')}
                      // isCaptain={true}
                      currentPlayer={0}
                      maxPlayer={0}
                      title={'Buat Tim Baru'}
                      subTitle={'Dengan membuat tim baru kamu akan menjadi kapten'}
                      status={'create_new'}
                      link={'create_new'}
                      marginBox={'0px'}
                      onClickCardNew={() => {
                        history.push('/create-team');
                      }}
                    />
                  );
                }
              })}
            </>
          </Box>
        ) : (
          <Box sx={{ pt: '17px' }}>
            <TeamSchoolBox
              key={'create_new_2'}
              image={gameTeamDraft?.iconUrl ?? IconSource('game_logo_freefire', 'png')}
              // isCaptain={true}
              currentPlayer={0}
              maxPlayer={0}
              title={'Buat Tim Baru'}
              subTitle={'Dengan membuat tim baru kamu akan menjadi kapten'}
              status={'create_new'}
              link={'create_new'}
              marginBox={'0px'}
              onClickCardNew={() => {
                history.push('/create-team');
              }}
            />
          </Box>
        )}

        <Box sx={{ mt: '24px' }}>
          <Text variant="label" sx={{ fontSize: '16px' }}>
            Tim di Sekolah mu
          </Text>
        </Box>

        <Box sx={{ mt: '16px' }}>
          <TextInputSearch
            id={'search-team-school'}
            placeholder={'Cari tim yang ada di sekolahmu'}
            onChange={(value: any) => searchTeamInMySchool(value)}
          />
        </Box>

        {isTeamLoading && (getTeamSchoolList?.count ?? 0) <= 0 ? (
          <>
            <Stack
              sx={{ mt: '16px' }}
              direction="column"
              justifyContent="center"
              alignItems="center"
            >
              <img alt="" src={IconSource('ic-loader')} />
            </Stack>
          </>
        ) : !isTeamLoading && (getTeamSchoolList?.count ?? 0) == 0 ? (
          <Stack sx={{ mt: '16px' }} direction="column" justifyContent="center" alignItems="center">
            <img alt="" src={IconSource('mabar-typeface-fullwhite')} />
            <NormalText
              fontSize="14px"
              fontWeight="400"
              color={getErrorList && getErrorList?.errors ? '#FF4D6D' : '#969393'}
              lineHeight="20.44px"
              textAlign="center"
            >
              {isSearching
                ? `Tidak ada tim '${
                    (document.getElementById('search-team-school') as HTMLInputElement).value
                  }' di sekolah kamu`
                : getErrorList && getErrorList.errors
                ? parseError(getErrorList.errors)
                : 'Sekolah kamu belum memiliki tim. Ayo buat tim kamu sekarang'}
            </NormalText>
          </Stack>
        ) : (
          (getTeamSchoolList?.count ?? 0) >= 0 && (
            <>
              <Stack sx={{ mt: '16px' }} direction="column">
                {getTeamSchoolList?.items.map((item, index) => {
                  return (
                    <TeamSchoolBox
                      key={index}
                      id={item.id}
                      image={item.avatarUrl}
                      isCaptain={item?.captain ?? ''}
                      title={item.name}
                      subTitle={item?.captain ?? ''}
                      currentPlayer={item.memberCount ?? 0}
                      maxPlayer={gameTeamDraft?.maxMember ?? 0}
                      marginBox={'0px'}
                      isHaveTeam={itemsDataMyTeam}
                      onClicklink={() => {
                        handleTeamSelected(item);
                      }}
                    />
                  );
                })}
              </Stack>
            </>
          )
        )}
      </Box>
    </>
  );
};

export default TeamListPage;
