import { NormalText } from 'components/styled/text.styled';
import MessageModal from 'components/UniPopup/Modals/MessageModal';
import { LogDev } from 'modules/helpers';
import React, { useEffect, useState } from 'react'
import { useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { AccountValidationProblemDetails } from 'redux/account/AccountResponse';
import { changeEmail, verifyEmail } from 'redux/account/AccountServices';
import { useAppSelector } from 'redux/hooks';
import { useAppThunkDispatch } from 'redux/store';
import NotFound from 'routes/NotFound';
import { EditProfileFormValues, inputEmailFormOptions, inputOTPCodeFormOptions } from 'validation/account/EditProfileValidation';
import InputEmailModal from './InputEmailModal';
import VerificationCodeModal from './VerificationCodeModal';

interface ModalStateType {
    step: 'input-email' | 'verif-email' | 'success' | 'failed';
}

const InputEmailModalContent = (props: { getShowModal: (val: boolean) => void, defaultEmail: string, onChangeEmail: (val: string) => void, modalType :ModalStateType }) => {
    const { t } = useTranslation()
    const { isLoading, isReduxError } = useAppSelector(({ account }) => ({
        isLoading: account.status === 'loading' ? true : false,
        isReduxError: account.status === 'error' ? true : false
    }));
    const currentTime = new Date().getTime()
    const [getEmailInput, setEmailInput] = useState<string>(props.defaultEmail);
    let [getVerifyTime, setVerifyTime] = useState(currentTime)
    const [getTokenCode, setTokenCode] = useState<number>(0);
    const [modalState, setModalState] = useState<ModalStateType>(props.modalType ??{ step: 'input-email' });
    const { control, handleSubmit } = useForm(modalState.step === 'input-email' ? inputEmailFormOptions : modalState.step === 'verif-email' ? inputOTPCodeFormOptions : undefined); 
    let [getValidationForm, setValidationForm] = useState<EditProfileFormValues | AccountValidationProblemDetails>({})
    let formError = getValidationForm ? (getValidationForm as EditProfileFormValues) : {}
    let respError = isReduxError ? (getValidationForm as AccountValidationProblemDetails) : {}
    let isInputEmailError = formError?.newEmail ? true : respError?.status ?? 204 >= 400 ? true : false;
    let inputEmailErrorMsg = formError?.newEmail?.message ? formError?.newEmail?.message :
        respError ? respError.errors?.newEmail ? respError.errors?.newEmail[0].message :
            respError.detail ? respError.detail : '' : ''
    let isVerifyOTPError = formError?.token ? true : respError?.status ?? 204 >= 400 ? true : false; 
    let inputVerifyOTPErrorMsg = formError?.token?.message ? formError?.token?.message :
    respError ? respError.title ? respError.title : respError.detail ? respError.detail : undefined : undefined         
    const dispatch = useAppThunkDispatch();
    const changeEmailThunk = changeEmail({ currentEmail: props.defaultEmail ? props.defaultEmail : undefined, newEmail: getEmailInput })
    const verifyEmailThunk = verifyEmail({ newEmail: getEmailInput, token: getTokenCode })
    function onFormSubmitInvalid(e?: EditProfileFormValues) {
        e && setValidationForm(e)
    }
    async function confirmChangeEmail() {
        props.onChangeEmail(getEmailInput)
        await dispatch(changeEmailThunk).unwrap().then((res) => {
            if (res && res.isAxiosError && res.data?.status >= 400) {
                setValidationForm(res.data as AccountValidationProblemDetails)
                if(res.data.status >= 500)                 
                    setModalState({ step: 'failed' })
            } else {
                setModalState({ step: 'verif-email' })
            }
        }).catch((err) => {
            if (process.env.NODE_ENV === 'development')
                console.error(err)
        })
    }
    async function resendOTPCode() {
        setVerifyTime(new Date().getTime())
        await dispatch(changeEmailThunk).unwrap().then((res) => {
            if (res && res.isAxiosError && res.data?.status >= 400) {
                setValidationForm(res.data as AccountValidationProblemDetails)
            } else {
                if (process.env.NODE_ENV === 'development')
                LogDev('INFO',res)
            }
        }).catch((err) => {
            if (process.env.NODE_ENV === 'development')
                console.error(err)
        })
    }
    async function verifyOTPCode() {
        await dispatch(verifyEmailThunk).unwrap().then((res) => {
            if (res && res.isAxiosError && res.data?.status >= 400) {
                setValidationForm(res.data as AccountValidationProblemDetails)                   
                if (process.env.NODE_ENV === 'development')
                    console.error(res)             
                if(res.data.status >= 500)                 
                    setModalState({ step: 'failed' })
            } else {
                if (process.env.NODE_ENV === 'development')
                    LogDev('INFO',res)
                    setModalState({ step: 'success' })
            }
        }).catch((err) => {
            if (process.env.NODE_ENV === 'development')
                console.error(err)
        })
    }    
    useEffect(() => {
        if(props.modalType.step === 'verif-email'){
            resendOTPCode()
        }
    }, [props.modalType.step])
    return (modalState?.step === 'input-email' ?
        (<form onSubmit={handleSubmit(confirmChangeEmail, onFormSubmitInvalid)} noValidate style={{ width: '100%' }}>
            <InputEmailModal
                formControl={control}
                isLoading={isLoading}
                isError={isInputEmailError}
                errorMessage={inputEmailErrorMsg}
                defaultEmail={getEmailInput}
                onConfirmClick={() => null}
                onCancelClick={() => props.getShowModal(false)}
                onChangeEmail={(e) => {
                    setEmailInput(e.target.value)
                    setValidationForm({})
                }} />
        </form>
        ) : modalState?.step === 'verif-email' ?
            (<form onSubmit={handleSubmit(verifyOTPCode, onFormSubmitInvalid)} noValidate style={{ width: '100%' }}>
                <VerificationCodeModal
                    formControl={control}
                    isError={isVerifyOTPError}
                    errorMessage={inputVerifyOTPErrorMsg}                    
                    isLoading={isLoading}
                    isEmailAddress
                    verifyTime={getVerifyTime}
                    onConfirmClick={() => null}
                    emailAddress={getEmailInput}
                    onChangeCode={(v) => setTokenCode(parseInt(v))}
                    onChangeClick={() => setModalState({ step: 'input-email' })}
                    onResendClick={async () => await resendOTPCode()}
                    onCancelClick={() => props.getShowModal(false)} />
            </form>
            ) : modalState?.step === 'success' ? (
                <MessageModal title={t('player_account_verification_flow_email_success_title')}
                    onConfirmClick={() => props.getShowModal(false)}
                    btnConfirmText={
                        <NormalText
                            color="white"
                            fontWeight="700"
                            fontSize="14px"
                            textAlign="center"
                            padding="0px 80px 0px 80px">
                            {t('player_account_verification_flow_email_success_button')}
                        </NormalText>
                    }>
                    <NormalText
                        textAlign="center"
                        color="#969393"
                        fontSize="14px"
                        fontWeight="normal"
                        margin="8px 0px">
                        {t('player_account_verification_flow_email_success_message')}
                    </NormalText>
                </MessageModal>
            ) : modalState?.step === 'failed' ? (                 
                <MessageModal title={t('player_account_verification_flow_email_failed_title')}
                    onConfirmClick={() => props.getShowModal(false)}
                    btnConfirmText={
                        <NormalText
                            color="white"
                            fontWeight="700"
                            fontSize="14px"
                            textAlign="center"
                            padding="0px 80px 0px 80px">
                            {t('player_account_verification_flow_email_success_button')}
                        </NormalText>
                    }>
                    <NormalText
                        textAlign="center"
                        color="#FF4D6D"
                        fontSize="14px"
                        fontWeight="500"
                        margin="8px 0px">
                        {t('player_account_verification_flow_email_failed_message')}
                    </NormalText>
                </MessageModal>
            ) : <NotFound/>    

    )
}

export default InputEmailModalContent;