import { Stack as StackMui } from '@mui/material';
import Avatar from '@mui/material/Avatar';
import { Box as BoxMui } from '@mui/system';
import { Text } from 'design/Text';
import { IconSource } from 'modules/assetpath';
import React from 'react';

export interface TeamSchoolItemProp {
  id?: string | undefined;
  image?: string;
  title?: string;
  subTitle?: string;
  isCaptain?: string;
  currentPlayer: number;
  maxPlayer: number;
  link?: string;
  status?: string | undefined;
  marginBox?: string | undefined;
  isHaveTeam?: any | undefined;
  onClicklink?: () => void;
  onClickCardNew?: () => void;
}

export const TeamSchool = ({
  id,
  image,
  title,
  subTitle,
  currentPlayer,
  maxPlayer,
  status,
  marginBox,
  isHaveTeam,
  onClicklink,
  onClickCardNew,
}: TeamSchoolItemProp) => {
  let isFullTeam: boolean = false;

  const isHave = isHaveTeam?.find((e: { id: any }) => e.id === id);

  if (currentPlayer >= maxPlayer) {
    isFullTeam = true;
  } else {
    isFullTeam;
  }

  return (
    <BoxMui sx={{ m: marginBox ?? '0 auto', position: 'relative' }}>
      {status == 'create_new' ? (
        <>
          <BoxMui
            sx={{
              minHeight: '87px',
              borderRadius: '8px',
              boxShadow:
                '-2px 4px 8px rgba(208, 43, 32, 0.25), 2px 4px 8px rgba(243, 112, 36, 0.25)',
              border: '1px solid #F37024',
              bgcolor: '#27282B',
              mb: '16px',
            }}
            onClick={onClickCardNew}
          >
            <StackMui direction="row">
              <Avatar
                variant="rounded"
                alt="Remy Sharp"
                src={image}
                sx={{
                  width: '87px',
                  height: '87px',
                  borderRight: '1px solid #F37024',
                  borderRadius: '0',
                  borderTopLeftRadius: '8px',
                  borderBottomLeftRadius: '8px',
                  background: '#1A1817',
                  img: {
                    objectFit: 'contain',
                  },
                }}
              />
              <StackMui direction="column" sx={{ ml: '12px', mr: '5px' }}>
                <Text
                  variant="labelSmall"
                  sx={{
                    fontFamily: 'Lexend Deca',
                    fontStyle: 'SemiBold',
                    fontSize: '16px',
                    lineHeight: '22px',
                    fontWeight: '600',
                    letterSpacing: '0px',
                    mt: '8px',
                    color: '#D4D6D8',
                  }}
                >
                  {title}
                </Text>
                <Text
                  variant="subtitle"
                  sx={{
                    fontFamily: 'Inter',
                    fontWeight: '400',
                    fontSize: '12px',
                    lineHeight: '16px',
                    mt: '4px',
                    color: '#94989E',
                    letterSpacing: '0px',
                  }}
                >
                  {subTitle}
                </Text>
              </StackMui>
              <StackMui direction="column" justifyContent="center" alignItems="center">
                <Avatar
                  variant="rounded"
                  sx={{
                    width: '26.67px',
                    height: '26.67px',
                    mr: '13.67px',
                  }}
                  alt="ADD"
                  src={IconSource('ic-add-plus')}
                />
              </StackMui>
            </StackMui>
          </BoxMui>
        </>
      ) : (
        <>
          {isHave && status != 'waiting' ? (
            <>
              <BoxMui
                sx={{
                  minHeight: '90px',
                  borderRadius: '8px',
                  boxShadow: '#2E3033 0px 0px 0px 1px',
                  bgcolor: isHave
                    ? 'rgba(26, 24, 23, 0.5)'
                    : isFullTeam
                    ? 'rgba(26, 24, 23, 0.5)'
                    : 'rgba(26, 24, 23, 1)',
                  position: 'absolute',
                  zIndex: 2,
                  width: '100%',
                  display: 'block',
                }}
              ></BoxMui>
            </>
          ) : isFullTeam && status != 'waiting' ? (
            <>
              <BoxMui
                sx={{
                  minHeight: '90px',
                  borderRadius: '8px',
                  boxShadow: '#2E3033 0px 0px 0px 1px',
                  bgcolor: isHave
                    ? 'rgba(26, 24, 23, 0.5)'
                    : isFullTeam
                    ? 'rgba(26, 24, 23, 0.5)'
                    : 'rgba(26, 24, 23, 1)',
                  position: 'absolute',
                  zIndex: 2,
                  width: '100%',
                  display: 'block',
                }}
              ></BoxMui>
            </>
          ) : (
            <></>
          )}

          <BoxMui
            sx={{
              minHeight: '90px',
              borderRadius: '8px',
              boxShadow: '#2E3033 0px 0px 0px 1px',
              bgcolor: isHave
                ? 'rgba(26, 24, 23, 0.5)'
                : isFullTeam
                ? 'rgba(26, 24, 23, 0.5)'
                : 'rgba(26, 24, 23, 1)',
              mb: '16px',
            }}
          >
            <StackMui sx={{ mb: '16px' }} direction="row">
              <Avatar
                variant="rounded"
                alt="Remy Sharp"
                src={image}
                sx={{
                  width: '87px',
                  height: '87px',
                  border: status == 'create_new' ? '#F37024 solid 2px' : '#F1EBEB solid 2px',
                  borderRadius: '8px',
                }}
              />
              <StackMui direction="column" sx={{ width: '100%', pl: '12px' }}>
                <Text
                  variant="labelSmall"
                  sx={{
                    fontFamily: 'Lexend Deca',
                    fontStyle: 'SemiBold',
                    fontSize: '16px',
                    lineHeight: '22px',
                    fontWeight: '600',
                    letterSpacing: '0px',
                    mt: '8px',
                  }}
                >
                  {title}
                </Text>

                <StackMui sx={{ mt: '4px' }} direction="row">
                  {status == 'waiting' ? (
                    <>
                      <Avatar
                        variant="rounded"
                        sx={{
                          width: '16px',
                          height: '16px',
                          mr: '4.75px',
                        }}
                        alt="Captain"
                        src={IconSource('label-loading-waiting')}
                      />
                      <Text
                        variant="subtitle"
                        sx={{
                          font: 'Lexend Deca',
                          fontWeight: '600',
                          fontSize: '12px',
                          lineHeight: '16px',
                          color: '#F68F54',
                        }}
                      >
                        {subTitle}
                      </Text>
                    </>
                  ) : (
                    <>
                      <Avatar
                        variant="rounded"
                        sx={{
                          width: '16px',
                          height: '16px',
                          mr: '6.5px',
                        }}
                        alt="Captain"
                        src={IconSource('ic-captain')}
                      />
                      <Text
                        variant="subtitle"
                        sx={{
                          fontFamily: 'Inter',
                          fontStyle: 'Regular',
                          fontSize: '12px',
                          lineHeight: '16px',
                          fontWeight: '400',
                          letterSpacing: '0px',
                          color: '#969393',
                        }}
                      >
                        {subTitle}
                      </Text>
                    </>
                  )}
                </StackMui>

                <StackMui sx={{ mt: '12px' }} direction="row">
                  <BoxMui sx={{ width: status == 'waiting' ? '0' : '60%', textAlign: 'left' }}>
                    <Text
                      variant="subtitle"
                      sx={{
                        fontFamily: 'Inter',
                        fontStyle: 'Regular',
                        fontSize: '12px',
                        lineHeight: '16px',
                        fontWeight: '400',
                        letterSpacing: '0px',
                        mb: '12px',
                        color: '#969393',
                      }}
                    >
                      {status == 'waiting' ? '' : currentPlayer + '/' + maxPlayer + ' Anggota'}
                    </Text>
                  </BoxMui>

                  <BoxMui sx={{ width: status == 'waiting' ? '100%' : '40%', textAlign: 'right' }}>
                    <StackMui
                      direction="row"
                      justifyContent="flex-end"
                      alignItems="center"
                      sx={{ mr: '12px' }}
                    >
                      {isFullTeam && status != 'waiting' ? (
                        <Text
                          variant="button"
                          sx={{
                            fontStyle: 'SemiBold',
                            fontWeight: '600',
                            fontSize: '12px',
                            lineHeight: '24px',
                            textAlign: 'right',
                            letterSpacing: '0px',
                            background: status == 'waiting' ? '#D4D6D8' : '#4E5156',
                            WebkitBackgroundClip: 'text',
                            WebkitTextFillColor: 'transparent',
                          }}
                        >
                          <a href="javascript:void(0)">Join Tim</a>
                        </Text>
                      ) : (
                        <Text
                          variant="button"
                          sx={{
                            fontStyle: 'SemiBold',
                            fontWeight: '600',
                            fontSize: '12px',
                            lineHeight: '24px',
                            textAlign: 'right',
                            letterSpacing: '0px',
                            background:
                              status == 'waiting'
                                ? '#D4D6D8'
                                : isHave != undefined
                                ? '#4E5156'
                                : 'linear-gradient(#D51F40, #F37024)',
                            WebkitBackgroundClip: 'text',
                            WebkitTextFillColor: 'transparent',
                          }}
                        >
                          <a href="javascript:void(0)" onClick={onClicklink}>
                            {status == 'waiting' ? 'Batal Join' : 'Join Tim'}
                          </a>
                        </Text>
                      )}
                    </StackMui>
                  </BoxMui>
                </StackMui>
              </StackMui>
            </StackMui>
          </BoxMui>
        </>
      )}
    </BoxMui>
  );
};
