import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import Tabs from '@mui/material/Tabs';
import { styled } from '@mui/system';

export const TabStyled: any = styled(Tab)`
  min-width: 0;
  max-width: none;
  min-height: 0;
  padding: 14px 0;
  font-family: 'Lexend Deca', sans-serif;
  font-weight: 600;
  text-transform: inherit;
  font-size: 12px;
  line-height: 15px;
  color: ${({ theme }) => theme.palette.neutral400};
  -webkit-box-decoration-break: clone;
  display: inline-block;
  &.Mui-selected {
    background: linear-gradient(90deg, #dd3339 0%, #f06628 100%);
    background-clip: text;
    -webkit-background-clip: text;
    text-fill-color: transparent;
    -webkit-text-fill-color: transparent;
  }
  &:not(:last-child) {
    margin-right: 16px;
  }
` as typeof Tab;

export const TabsStyled = styled(Tabs)(({ theme }: any) => ({
  minHeight: 0,
  '.MuiTabs-indicator': {
    height: '4px',
    borderRadius: '16px 16px 0px 0px;',
    background: theme.palette.primaryGradient3,
  },
}));

export const TabPanelStyled = styled(Box)(() => ({
  padding: 0,
  margin: 0,
  fontFamily: "'Lexend Deca', sans-serif",
}));
