import styled from 'styled-components';
import { ScrimWinLoseProps } from './index';

export const Card = styled.div`
  position: relative;
  width: 98%;
  background: rgba(55, 57, 66, 0.6);
  border-radius: 16px;
`;

export const HeaderCard = styled.div<ScrimWinLoseProps>`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  /* padding: 10px 16px 12px; */

  position: relative;
  width: 63%;
  /* height: 28px; */
  background: ${props =>
    props.bg ?? 'linear-gradient(0deg, rgba(0, 120, 41, 0.3), rgba(0, 120, 41, 0.3))'};
  border-radius: 0px 0px 16px 16px;

  /* Inside Auto Layout */

  flex: none;
  order: 0;
  flex-grow: 0;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
`;

export const BorderDate = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 4px 6px;

  position: relative;
  bottom: 70px;
  width: 51%;
  height: 25px;
  background: rgba(255, 255, 255, 0.06);
  border-radius: 6px;
  margin-left: auto;
  margin-right: auto;
`;
export const HeaderTitle = styled.p`
  position: relative;

  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 140%;
  text-align: center;
  color: #ddd8d8;
`;

